<style lang="scss">
  @import "../../services/popup/popup";
  @import "../../scss/variables";
  @import "../../scss/mixins";

  .popup-container .von-popup {
    background-color: #fff;
  }

  .popup-container.custom-popup-container {
    &.popup-showing {
      transition: 0.3s;

      &.active {
        background-color: rgba(#000000, 0.65);
      }
    }
  }
</style>
<template>
  <div class="popup-container custom-popup-container" :effect="effect"
       :class="{'popup-showing active': state == 1, 'popup-showing popup-hidden': state == 2}">
    <div class="popup von-popup">
      <slot></slot>
    </div>
  </div>
</template>
<script>
  function preventDefault (e) {
    e.preventDefault()
  }

  export default {
    props: {
      effect: {
        type: String,
        default: 'scale'
      }
    },

    data () {
      return {
        state: 0 // 0: hidden, 1: showing, 2: hidding
      }
    },

    methods: {
      show () {
        this.state = 1
        document.body.classList.add('popup-open')
        document.addEventListener('touchstart', preventDefault)

        return new Promise((resolve, reject) => {
          this.$on('PopupButtonClickEvent', (data) => {
            resolve(data.buttonIndex)
          })
        })
      },

      hide (buttonIndex) {
        this.state = 2

        setTimeout(() => {
          this.state = 0
          document.body.classList.remove('popup-open')
          document.removeEventListener('touchstart', preventDefault)
          this.$emit('PopupButtonClickEvent', {buttonIndex: buttonIndex})
        }, 300)
      },

      getState () {
        return this.state
      }
    }
  }
</script>



