<template>
  <CustomPopup ref="p">
    <div class="popup-body popup-assistant popup-unopen-assistant">
      <div class="text">
        任务助手未开启
      </div>

      <button class="button button-assertive btn-open" @click="open()">打开助手</button>

      <div class="tips">
        如遇问题请 <span class="assertive" style="text-decoration: underline;" @click="download()">重新下载助手</span>
      </div>

      <button v-if="showHide" class="button button-royal button-small button-clear button-close" @click="hide(-1)">
        <i class="ion-ios-close-empty"></i>
      </button>
    </div>
  </CustomPopup>
</template>
<style lang='scss'>
  @import './assistant';
</style>
<script>
  import {mapGetters, mapActions} from 'vuex'
  import CustomPopup from '@/widget/CustomPopup.vue'
  import {channel, events} from 'store/channel'

  export default {
    components: {
      CustomPopup
    },

    props: {
      mode: {
        type: String,
        default: 'auto',
        validator: function (value) {
          return value === 'auto' || value === 'manual'
        }
      }
    },

    data () {
      return {
        currentRoute: ''
      }
    },

    computed: {
      ...mapGetters('assistant', [
        'showAssistantUnopenPopup'
      ]),

      showHide: function () {
        return _.indexOf(['task_detail', 'easy_detail'], this.currentRoute) === -1
      }
    },

    mounted () {
      setTimeout(() => {
        this._init(this.showAssistantUnopenPopup)
      }, 500)
    },

    destroyed () {
      channel.$off(events.POPUP_ASSISTANT_UNOPEN)
    },

    watch: {
      'showAssistantUnopenPopup': function (val) {
        this._init(val)
      }
    },

    methods: {
      _init (showAssistantUnopenPopup) {
        if (this.$refs.p && this.mode === 'auto') {
          if (showAssistantUnopenPopup && this.$refs.p.getState() !== 1) {
            this.$refs.p.show()
            this.currentRoute = $router.currentRoute.name
          }

          if (!showAssistantUnopenPopup && this.$refs.p.getState() === 1) {
            this.$refs.p.hide()
          }
        }

        if (this.$refs.p && this.mode === 'manual') {
          channel.$on(events.POPUP_ASSISTANT_UNOPEN, data => {
            if (data.showAssistantUnopenPopup && this.$refs.p.getState() !== 1) {
              this.$refs.p.show()
              this.currentRoute = $router.currentRoute.name
            }

            if (!data.showAssistantUnopenPopup && this.$refs.p.getState() === 1) {
              this.$refs.p.hide()
            }
          })
        }
      },

      ...mapActions('assistant', [
        'open',
        'download'
      ]),

      hide (buttonIndex) {
        this.$refs.p.hide(buttonIndex)
        // 如果当前页面为试玩或专属任务列表和详情，则关闭弹层后返回到dashboard
        let routes = ['tasks', 'zstasks', 'zstask_detail']
        if (_.indexOf(routes, this.$route.name) !== -1) {
          setTimeout(() => {
            $router.back('/dashboard')
          }, 300)
        }
      }
    }
  }
</script>



