<template>
  <CustomPopup ref="p">
    <div class="popup-body popup-assistant popup-install-certification">
      <div class="title">
        苹果官方认证
      </div>

      <div class="desc">
        为获得更多任务，需要您安装证书<br>验证设备唯一性
      </div>

      <button class="button button-assertive btn-install" @click="install()">安装证书</button>

      <button class="button button-royal button-small button-clear button-close" @click="hide(-1)">
        <i class="ion-ios-close-empty"></i>
      </button>
    </div>
  </CustomPopup>
</template>
<style lang='scss' scoped>
  @import "~scss/mixins";

  .popup-body.popup-install-certification {
    padding: 193px 10px 30px 10px !important;
    text-align: center;
    height: 362px;

    @include background-image(url('//qianka.b0.upaiyun.com/images/b64771acd22ad88ee485fad4b60ffe6c.png'), 255px 163px, 10px 10px);

    .title {
      font-size: 14px;
      line-height: 22px;
      color: #3A3A3A;
      margin-bottom: 12px;
    }

    .desc {
      font-size: 13px;
      line-height: 20px;
      color: #888888;
      margin-bottom: 25px;
    }

    .title, .desc {
      @include font-regular();
    }

    .btn-install {
      width: 160px;
      height: 40px;
      min-height: 40px;
      border-radius: 2px;
      margin-bottom: 0px;
      font-size: 13px;
    }
  }
</style>
<script>
  import {mapState, mapActions} from 'vuex'
  import CustomPopup from '@/widget/CustomPopup.vue'

  // utils
  const install_and_redirect = (return_url) => {
    location.href = '/profile4/profile_service.mobileconfig?return_url=' + encodeURIComponent(return_url)
  }

  export default {
    components: {
      CustomPopup
    },

    computed: {
      ...mapState([
        'showInstallCertificationPopup'
      ])
    },

    watch: {
      'showInstallCertificationPopup': function (newVal) {
        if (newVal) {
          // 展示弹层前确保无 $loading
          $loading.hide()

          this.show()
          return
        }

        this.hide()
      }
    },

    methods: {
      show () {
        this.$refs.p.show()
      },

      hide (buttonIndex) {
        this.$refs.p.hide(buttonIndex)
        this.hideInstallCertificationPopup()
      },

      install () {
        install_and_redirect('/v4/tasks')
      },

      ...mapActions([
        'hideInstallCertificationPopup'
      ])
    }
  }
</script>



