const GaoeList = r => require.ensure([], () => r(require('./GaoeList')), 'gaoe-list')

const GaoeDetail = r => require.ensure([], () => r(require('./GaoeDetail')), 'gaoe-detail')

const GaoeBackground = r => require.ensure([], () => r(require('./GaoeBackground')), 'gaoe-other')

const GaoeProgress = r => require.ensure([], () => r(require('./GaoeProgress')), 'gaoe-other')

export default [
  { path: '/gaoe/', name: 'gaoe', component: GaoeList },
  { path: '/gaoe/detail/:id', name: 'gaoe_detail', component: GaoeDetail },
  { path: '/gaoe/background/:id', name: 'gaoe_background', component: GaoeBackground },
  { path: '/gaoe/progress', name: 'gaoe_progress', component: GaoeProgress }
]



