import TaskList from './views/TaskList.vue'
import TaskDetail from './views/TaskDetail.vue'
import ZSTaskList from './views/ZSTaskList.vue'
import ZSTaskDetail from './views/ZSTaskDetail.vue'
import EasyDetail from './views/EasyDetail.vue'

export default [
  {
    path: '/tasks',
    name: 'tasks',
    component: TaskList
  },
  {
    path: '/task/detail/:id',
    name: 'task_detail',
    component: TaskDetail
  },
  {
    path: '/zstasks',
    name: 'zstasks',
    component: ZSTaskList
  },
  {
    path: '/zstask/detail/:id',
    name: 'zstask_detail',
    component: ZSTaskDetail
  },
  {
    path: '/easy/detail/:id',
    name: 'easy_detail',
    component: EasyDetail
  }
]



