<template>
  <CustomPopup ref="p">
    <div class="popup-body popup-install-kagou">
      <div class="icon-wrapper">
        <img src="//qianka.b0.upaiyun.com/images/978ec9d32204256ce0cd5a4287190b09.png" alt="">
      </div>

      <div class="desc">
        <div>下载咖购</div>
        <div>领新人0元包邮商品福利</div>
        <img class="remind-icon" src="//qianka.b0.upaiyun.com/images/784a08941dea023aac029fcee751686c.png" alt="">
      </div>

      <button class="button button-assertive btn-install" @click="install()">查看该商品</button>

      <button class="button button-royal button-small button-clear button-close" @click="hide(-1)">
        <i class="ion-ios-close-empty"></i>
      </button>
    </div>
  </CustomPopup>
</template>
<style lang='scss' scoped>
  @import "~scss/mixins";

  .popup-install-kagou {
    height: 251px;
    padding: 30px 57px !important;
    text-align: center;

    .icon-wrapper {
      margin-bottom: 30px;

      img {
        width: 52px;
        height: 52px;
      }
    }

    .desc {
      font-size: 14px;
      color: #3A3A3A;
      margin-bottom: 25px;
      position: relative;

      @include font-regular();

      div {
        height: 20px;
        line-height: 20px;
        margin-bottom: 4px;
      }

      .remind-icon {
        position: absolute;
        width: 34px;
        height: 16px;
        top: 11px;
        right: -33px;
      }
    }

    .btn-install {
      width: 160px;
      height: 40px;
      min-height: 40px;
      border-radius: 2px;
      font-size: 13px;
    }
  }
</style>
<script>
  import CustomPopup from '@/widget/CustomPopup.vue'
  import {mapState, mapActions} from 'vuex'

  export default {
    components: {
      CustomPopup
    },

    computed: {
      ...mapState('task', [
        'kagou_link',
        'showInstallKagouPopup'
      ])
    },

    watch: {
      'showInstallKagouPopup': function (newVal) {
        console.log(newVal)

        if (newVal) {
          this.show()
          return
        }

        this.hide()
      }
    },

    methods: {
      show () {
        this.$refs.p.show()
      },

      hide (buttonIndex) {
        this.$refs.p.hide(buttonIndex)
        this.hideKagou()
      },

      install () {
        if (this.kagou_link) {
          location.href = this.kagou_link
        }
      },

      ...mapActions('task', [
        'hideKagou'
      ])
    }
  }
</script>



