/* eslint eqeqeq: "off", camelcase: "off" */

let LOCATION_URL = window.location.href
let arrUrl = LOCATION_URL.split('/')

const UPLOAD_URL = arrUrl[0] + '//qianka.com/s4/assets.uploadImage'

const upload = (fileInput) => {
  return new Promise((resolve, reject) => {
    let data = new FormData()
    _.forEach(fileInput.files, (file, i) => {
      data.append(i, file)
    })

    let xhr = new XMLHttpRequest()

    xhr.onreadystatechange = function () {
      if (xhr.status == 200 && xhr.readyState == 4) {
        resolve(JSON.parse(xhr.responseText), xhr.status)
      } else if (xhr.status >= 300) {
        reject('上传失败, Status: ' + xhr.status, xhr.status)
      }
    }

    xhr.open('POST', UPLOAD_URL, true)
    xhr.send(data)

    fileInput.value = null
  })
}

export default upload



