require('./popup.scss')

const open_setting_popup = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general show_setting_file',
    buttons: [
      {text: '忽略'},
      {text: '允许'}
    ]
  },

  template: () => {
    return `
    <div class="desc">
      此网站正尝试打开"设置"以向您显示一个配置描述文件。您要允许吗？
    </div>
  `
  }
}

const popup_choose_verify_code_type = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '短信验证码' },
      { text: '语音验证码' }
    ],
    showClose: true
  },
  template: `
    <div class="title">没收到验证码？</div>
    <div class="desc">
      您可以选择获取语音验证码
    </div>
  `
}

// 该手机已绑定手机号码
const popup_has_bind_mobile = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '取消' },
      { text: '切换账号' }
    ]
  },
  template: `
    <div class="title">此手机已被其它账号绑定</div>
    <div class="desc">若当前账号未绑定手机号，切换账号后将永久丢失，确认切换？</div>
    `
}

const popup_show_qrcode = {
  options: {
    effect: 'scale',
    cssClass: 'popup_show_qrcode',
    showClose: true
  },
  template () {
    return `
      <div class="qrcode">
        <div class="across-space top"></div>
        <div class="vertical-space right"></div>
        <div class="across-space bottom"></div>
        <div class="vertical-space left"></div>
        <img src="//qianka.b0.upaiyun.com/images/3f649daeb3e028af33de0e26d23a96cf.png" alt="">
      </div>
      <div class="remind">长按并存储二维码图片</div>
      <div class="remind last">打开微信扫一扫识别该二维码</div>
      <div class="remind-or">（或者微信搜索“qiankakefu”）</div>`
  }
}
/* eslint eqeqeq: "off", camelcase: "off" */

// 提示微信绑定
const popup_wechat_bind = {
  options: {
    effect: 'scale',
    // cssClass: 'popup_wechat_bind',
    cssClass: 'popup-general',
    buttons: [
      { text: '取消' },
      { text: '绑定' }
    ]
  },

  template: `
    <div class="title">提示</div>
    <div class="desc">
      为了您的帐号安全，完成微信绑定后，即可安心兑换
    </div>`
}

// 冻结账号提示
const popup_user_freeze = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: () => {
    return `
      <div class="title">很抱歉</div>
      <div class="desc">
        您的账号已被冻结，解冻请
        <a class="assertive" href="/v4/help/complainUnfreeze" >前往申诉</a>
      </div>
    `
  }
}

// 未绑定手机退出登录提示
const popupLogoutUnbind = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    showClose: true,
    buttons: [
      {text: '绑定手机'},
      {text: '切换帐号'}
    ]
  },

  template: `
    <div class="title">警告</div>
    <div class="desc">
      您的账号尚未绑定手机，切换帐号或清除缓存后您将无法再次登录此账号
    </div>
  `
}
// 确认充值的手机号
const popup_confirm_phone = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '取消' },
      { text: '确认' }
    ]
  },

  template: (phone) => {
    return `
    <div class="title">${phone}</div>
    <div class="desc">
      确认为该手机号充值？
    </div>
  `
  }
}

const popup_withdraw_fail = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [{
      text: '知道了'
    }]
  },
  template (msg) {
    return `
  <div class="title">失败原因</div>
  <div class="desc">${msg}</div>`
  }
}

const popup_remind_use_correct_mobile = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '知道了' }
    ]
  },

  template: (mobile) => {
    return `
      <div class="title">${mobile}</div>
      <div class="desc">
        请使用该账号绑定的手机号进行验证
      </div>
    `
  }
}

const popup_unfreeze_unbind_mobile = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    showClose: true,
    buttons: [
      { text: '知道了' }
    ]
  },

  template: (mobile) => {
    return `
      <div class="title">该账号尚未绑定手机</div>
      <div class="desc">
        请绑定手机后再提交申诉
      </div>
    `
  }
}

// 绑定手机弹层
const popup_bind_phone = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '取消'},
      {text: '立即绑定'}
    ]
  },

  template: `
    <div class="title">绑定手机</div>
    <div class="desc">
      为保证您的账户资金安全，提现前必须绑定手机
    </div>
  `
}

module.exports = {
  open_setting_popup,
  popup_choose_verify_code_type,
  popup_has_bind_mobile,
  popup_show_qrcode,
  popupLogoutUnbind,
  popup_wechat_bind,
  popup_user_freeze,
  popup_confirm_phone,
  popup_withdraw_fail,
  popup_remind_use_correct_mobile,
  popup_unfreeze_unbind_mobile,
  popup_bind_phone
}



