<!-- 试玩列表详情页面 -->
<template>
  <div class="page page-task-detail" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#FFDD55"
      menu-bg-color="#FFD155"
      color="#3A3A3A"
      :on-back-button-click="back"
      :expanded="true"
      :animate="false"
      :show-back-button="false"
      :show-menu-button="true"
      :on-menu-button-click="back"
    >
      <div slot="content">
        <TaskDetailTitle>
          <div class="reward"><span>任务奖励 {{ exclusive_count ? '=' : '+' }} </span>
            <span class="income" v-text="reward" task-detail-reward></span>
            <span v-if="exclusive_count">
              <span>元试玩 + </span><span class="income" v-text="exclusive_reward"></span><span> 元专属</span>
            </span>
            <span v-else>元</span>
          </div>

          <div class="countdown">
            <span>剩余时间 </span><count-down v-if="expire_at > 0" :deadline="expire_at" format="mm:ss"></count-down>
          </div>
        </TaskDetailTitle>
      </div>
    </AnimateHeader>

    <div class="page-content task-detail">
      <scroller style="padding-top: 100px; width: 257px">
        <div class="icon-wrapper">
          <img :src="app_icon" alt="">
        </div>

        <div class="step step-1" v-if="app_keyword">
          <div>一、前往 App Store 搜索：<span class="light" v-if="app_keyword.length <= 6" v-text="app_keyword"></span></div>
          <div><span class="light"v-if="app_keyword.length > 6"  v-text="app_keyword"></span></div>
          <div>约第 <span class="light" v-text="app_rank"></span> 位，找到该图标应用下载安装</div>
          <div v-if="isPad">iPad设备请选择 仅限iPhone</div>
        </div>

        <div class="step step-1" v-if="!app_keyword">
          <div>一、前往 App Store <span class="light">下载该应用</span></div>
        </div>

        <div></div>

        <div class="step step-2">
          <div>二、回本页面，点击 <span class="light">开始试玩 </span><span v-text="tips"></span></div>
          <div>未联网试玩，无法领奖</div>
        </div>

        <div class="step step-3">
          <div>三、满足条件后，回本页面 <span class="light">领取奖励</span></div>
        </div>

        <div class="step-button can-click" @click="goAppstore()">
          <span>前往 App Store</span>
        </div>
        <div class="step-button disabled" :class="{'can-click': steps[0] !== 1}" @click="openApp()">
          <span>开始试玩</span>
        </div>
        <div class="step-button disabled" :class="{'reward': steps[2] !== 0}" @click="checkTaskStatus()">
          <span>领取奖励</span>
        </div>

        <div style="height: 200px;"></div>
      </scroller>

      <!--双倍券按钮-->
      <transition name="fade-coupon">
        <div class="coupon-use-remind" v-if="show_coupon">
          <div class="num-and-reward">您有双倍券<span class="light" v-text="coupon_num"></span>张，使用后奖励增加<span class="light" v-text="couponNum"></span>元</div>
          <div class="coupon-use-button" @click="useCoupon"><span>使用</span></div>
        </div>
      </transition>
    </div>

    <AssistantInvalid/>
    <AssistantUnopen/>
  </div>
</template>
<style lang='scss' scoped>
  @import "~scss/mixins";

  .fade-coupon-transition {
    transition: all 0.3s ease;
    overflow: hidden;
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }

  .fade-coupon-enter, .fade-coupon-leave-to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  .fade-coupon-enter-active, .fade-coupon-leave {
    opacity: 1;
    transition: all 0.3s ease;
  }

  .page-task-detail {
    .task-detail {
      padding: 100px 59px 0 59px;
      background-color: #FFF;

      .icon-wrapper {
        text-align: center;
        height: 65px;
        margin: 30px auto 25px;

        img {
          width: 65px;
          height: 65px;
          border-radius: 8px;
        }
      }

      .step {
        font-size: 14px;
        color: #3A3A3A;
        margin-bottom: 18px;
        margin-left: 59px;

        @include font-regular();

        &.step-3 {
          margin-bottom: 40px;
        }

        div {
          margin-bottom: 6px;
          margin-left: 28px;

          .light {
            font-size: 14px;
            color: #EF4F2A;
            line-height: 20px;
            font-weight: bold;

            @include font-medium();
          }
        }

        div:first-child {
          margin-left: 0;
        }
      }

      .step-button {
        width: 172px;
        height: 40px;
        border-radius: 2px;
        line-height: 40px;
        text-align: center;
        margin: 15px auto;

        span {
          font-size: 13px;
          color: #FFFFFF;

          @include font-medium();
        }

        &.disabled {
          background: #DDDDDD;
        }

        &.can-click {
          background: #4A90E2;
        }

        &.reward {
          background: #EF4F2A;
        }
      }

      .coupon-use-remind {
        width: 100%;
        height: 50px;
        line-height: 50px;
        position: fixed;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.8);

        .num-and-reward {
          display: inline-block;
          font-size: 14px;
          color: #FFFFFF;
          margin-left: 16px;

          @include font-regular();

          .light {
            color: #EF4F2A;
            font-weight: bold;
          }
        }

        .coupon-use-button {
          display: inline-block;
          background: #EF4F2A;
          border-radius: 2px;
          width: 68px;
          height: 30px;
          text-align: center;
          line-height: 30px;
          margin-left: 19px;

          span {
            font-size: 12px;
            color: #FFFFFF;

            @include font-medium();
          }
        }
      }
    }
  }
</style>
<script>
  import {tween} from 'shifty'
  import {mapState, mapGetters, mapActions} from 'vuex'
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import CountDown from '@/widget/CountDown.vue'
  import TaskDetailTitle from './TaskDetailTitle.vue'
  import {popup_abandon_task, popup_remind_risk} from '../popups/popups'
  // 助手相关弹层
  import AssistantUnopen from '../popups/AssistantUnopen.vue'
  import AssistantInvalid from '../popups/AssistantInvalid.vue'

  import {
    KEY_DOUBLE_IS_NEED_HIDE,
    KEY_UID,
    TASK_STATE_APPSTORE_HAS_GO,
    KEY_TASK_STEP
  } from 'constants'

  import {appendUrlParams, is_ios_11} from 'utils'

  let openAppLock = false
  let checkTaskStatusLock = false

  export default {
    components: {
      AnimateHeader,
      CountDown,
      TaskDetailTitle,
      AssistantUnopen,
      AssistantInvalid
    },

    data () {
      return {
        isPad: /iPad/.test(navigator.userAgent)
      }
    },

    computed: {
      ...mapState('taskDetail', [
        'task_id',
        'task_status',
        'reward',
        'app_keyword',
        'app_rank',
        'app_name',
        'app_icon',
        'app_url',
        'app_bid',
        'app_exe',
        'tips',
        'expire_at',
        'exclusive_count',
        'exclusive_reward',
        'show_coupon_btn',
        'double_reward',
        'steps',
        'show_coupon',
        'coupon_num',
        'is_invest'
      ]),

      ...mapGetters('taskDetail', [
        'couponNum'
      ])
    },

    mounted () {
      this.fetch(this.$route.params.id)
    },

    destroyed () {
      // 停止轮询应用是否安装
      this.cancelPoll()
    },

    methods: {
      // 前往appstore
      goAppstore () {
        this.isAssistantAlive()
          .then(() => {
            this.updateSteps(TASK_STATE_APPSTORE_HAS_GO)

            location.href = appendUrlParams(this.app_url, {
              'return_url': location.href
            })
          })
      },

      // 打开应用
      openApp () {
        this.isAssistantAlive()
          .then(() => {
            // 风险提示弹层
            let popup = $popup.fromTemplate(
              popup_remind_risk.template,
              popup_remind_risk.options
            )
            if (this.is_invest && this.steps[0] !== 1) { // 为有风险任务并且试玩按钮亮了之后，出弹层，否则照旧
              popup.show().then(() => {
                this.openWithVersion()
              })
            } else {
              this.openWithVersion()
            }
          })
      },

      openWithVersion () {
        if (openAppLock) return
        openAppLock = true
        setTimeout(() => {
          openAppLock = false
        }, 1000)

        if (!is_ios_11()) {
          this._openApp()
        } else {
          // TODO: ios11逻辑
          this._openAppWith11()
        }
      },

      checkTaskStatus () {
        if (checkTaskStatusLock) return
        checkTaskStatusLock = true
        setTimeout(() => {
          checkTaskStatusLock = false
        }, 1000)

        // 前一步未完成：打开应用按钮未点击过
        if (this.steps[2] === 0) return

        this.isAssistantAlive()
          .then(() => {
            this._checkTaskStatus()
          })
      },

      useCoupon () {
        this._useCoupon()
        .then(() => {
          this.doubleReward()
        })
      },

      removeNeedHideCouponId () {
        // 放弃任务时去掉双倍券缓存的ID
        let coupon_need_hide = $storage.get(KEY_DOUBLE_IS_NEED_HIDE) || []
        let need_hide_id = $storage.get(KEY_UID) + '-' + this.task_id
        let hide_id_index = coupon_need_hide.indexOf(need_hide_id)
        if (hide_id_index > -1) {
          coupon_need_hide.splice(hide_id_index, 1)
          $storage.set(KEY_DOUBLE_IS_NEED_HIDE, coupon_need_hide)
        }
      },

      back () {
        $popup.fromTemplate(
          popup_abandon_task.template,
          popup_abandon_task.options
        )
          .show()
          .then((buttonIndex) => {
            if (!buttonIndex) {
              let goBack = () => {
                setTimeout(() => {
                  $router.back({
                    path: '/tasks'
                  })
                }, 200)
              }
              // fix 异步请求没返回时store里task_id默认为0，直接返回
              if (this.task_id === 0) {
                goBack()
                return
              }
              $api
                .subtaskGiveup(this.task_id)
                .then(() => {
                  if ($storage.get(KEY_TASK_STEP)) $storage.remove(KEY_TASK_STEP)
                  this.removeNeedHideCouponId()
                  goBack()
                })
            }
          })
      },

      ...mapActions('taskDetail', [
        'fetch',
        '_openApp',
        '_openAppWith11',
        '_checkTaskStatus',
        '_useCoupon',
        'updateSteps',
        'cancelPoll'
      ]),

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      }),

      doubleReward () {
        let e = document.querySelector('[task-detail-reward]')
        let start = parseFloat(this.reward)
        let end = this.double_reward
        tween({
          from: { x: start },
          to: { x: end },
          duration: 750,
          easing: 'easeOutQuad',
          step: state => {
            e.innerHTML = state.x.toFixed(1)
          }
        }).then(
          () => {
            e.innerHTML = end.toFixed(1)
          }
        )
      }
    }
  }
</script>




