<template>
  <div>
    <ActionList v-if="showGameBanner || game_cards.length > 0" title="游戏特权">
      <Banner v-if="showGameBanner" v-for="(task, index) in game_banner" :key="index" @click.native="getGame(task.game_url)"><img slot="banner" :src="task.banner_url" alt=""></Banner>

      <Action v-for="(task, index) in game_cards" :key="index" @click.native="getGame(task.game_url)">
        <div slot="icon" class="icon">
          <img :src="task.icon">
        </div>

        <span slot="app-name" v-text="task.title"></span>
        <span slot="desc" v-for="(tag, index) in task.tags" :key="index" class="tag-box">
          <span v-if="typeof tag == 'string'" v-text="tag" class="tag"></span>

          <span v-if="typeof tag == 'object' && tag.text" v-text="' ‧ ' + tag.text" class="tag"
             :class="{'assertive': tag.theme == 'assertive'}"></span>
          </span>

          <div slot="reward-wrap" class="reward-content">
            <span class="plus">+</span>
            <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
            <span class="yuan">元</span>
          </div>
      </Action>
     </ActionList>
   </div>
</template>
<script>
  import {mapState, mapGetters} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  import Banner from '../components/Banner.vue'

  export default {
    components: {
      Action,
      ActionList,
      Banner
    },

    data () {
      return {
        banner_url: '',
        game_url: ''
      }
    },

    computed: {
      ...mapState('task', [
        'game_banner',
        'game_cards'
      ]),

      ...mapGetters('task', [
        'showGameBanner'
      ])
    },

    methods: {
      getGame (url) {
        location.href = url
      }
    }
  }
</script>



