import './popups.scss'

// 下载线上任务助手弹层
export const popup_download_assistant = {
  options: {
    effect: 'scale',
    cssClass: 'popup-download-assistant',
    buttons: [
      {text: '下载助手'}
    ],
    showClose: true
  },

  template: ({app_icon, app_name, app_developer}) => {
    return `
      <div class="remind">
        <span>这是您的任务助手</span>
        <span>做任务期间助手必须开启</span>
      </div>
      <img class="app-icon" src="${app_icon}" alt="">
      <p class="app-name">${app_name}</p>
      <div class="app-developer">${app_developer}<img class="arrow" src="//qianka.b0.upaiyun.com/images/fd2f442bd993479e34e64c25fbd7184b.png" alt=""></div>
      <img class="bg" src="//qianka.b0.upaiyun.com/images/f071994ae1f4a519e81022eb6d553e86.png" alt="">
      <div class="bottom-remind"><span>每个人的任务助手可能不同</span></div>
    `
  }
}

// 绑定手机弹层
export const popup_bind_phone = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '暂不绑定'},
      {text: '立即绑定'}
    ]
  },

  template: `
    <div class="title">当前账号有风险</div>
    <div class="desc">
      切换账号或清除缓存后您的账号及资产将会丢失，为了您的账户资金安全，请绑定手机后领取任务
    </div>
  `
}

// 越狱用户
export const popup_jail_breaker = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: (link) => {
    return `
      <div class="title">很抱歉</div>
      <div class="desc">
        您的设备已越狱或安装了第三方应用商店软件、越狱软件，请恢复正版或删除该类应用。如需帮助请
        <a class="assertive" href="${link}" >联系客服</a>
      </div>
    `
  }
}

// 芝麻信用弹层
export const popup_bind_zhima = {
  options: {
    effect: 'scale',
    cssClass: 'popup-bind-zhima',
    buttons: [
      {text: '立即认证'}
    ],
    showClose: true
  },

  template: `
    <div class="icon text-center"><img src="//qianka.b0.upaiyun.com/images/e132b16d4dda3a0acfbdde188f2be9e2.png" alt=""></div>
    <div class="title text-center">
      <img class="line" src="//qianka.b0.upaiyun.com/images/b90c5bb15267413f7ec95ca658bb3a8d.png" >
      <span>芝麻信用认证</span>
      <img class="line" src="//qianka.b0.upaiyun.com/images/b90c5bb15267413f7ec95ca658bb3a8d.png" >
    </div>
    <div class="desc text-center">应广告商要求，领取更多试玩任务需要先认证身份唯一性，其他类型任务无需认证</div>
  `
}

// 冻结账号提示
export const popup_user_freeze = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: () => {
    return `
      <div class="title">很抱歉</div>
      <div class="desc">
        您的账号已被冻结，解冻请
        <a class="assertive" href="/v4/help/complainUnfreeze" >前往申诉</a>
      </div>
    `
  }
}

// 放弃进行中的任务, 抢新的任务
export const popup_has_task_ongoing = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '取消'},
      {text: '确认'}
    ]
  },

  template: taskType => `
    <div class="desc">不能同时抢多个${taskType || '试玩'}任务哦!<br>要放弃上个任务并领取该任务吗?</div>
  `
}

// 任务进行中弹层
export const popup_task_ongoing = {
  options: {
    effect: 'scale',
    cssClass: 'popup-task-ongoing',
    buttons: [
      {text: '继续试玩'}
    ],
    showClose: true
  },

  template: (title, desc) => `
    <div class="img-wrap"><img class="top" src="//assets.qkcdn.com/images/39da55991b31bb1762b58cf1397a7548.png"></div>
    <div class="title">${title}</div>
    <div class="desc">${desc}</div>
  `
}

// 放弃任务
export const popup_abandon_task = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '放弃'},
      {text: '取消'}
    ]
  },

  template: `
    <div class="desc">是否放弃任务</div>
  `
}

 // 专属任务提示安装应用
export const popupInstallApp = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '忽略' },
      { text: '下载' }
    ]
  },

  template: `
    <div class="desc no-title">
      请先安装任务应用
    </div>
  `
}

// 专属任务提示放弃
export const popupGiveUp = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '放弃任务' },
      { text: '取消' }
    ]
  },
  template: '<div class="desc no-title">确定要放弃当前任务？</div>'
}

// 专属列表不可领取任务
export const popupMutedTask = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '知道了' }
    ]
  },

  template (args) {
    return `
      <div class="desc no-title">
        该任务${args.date}才能领取
      </div>
    `
  }
}

// 专属任务有未完成的任务
export const popupHasOngoing = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '知道了' }
    ]
  },
  template: '<div class="desc no-title">您还有任务尚未完成，<br>无法开始该任务</div>'
}

export const popup_shiwan_fail = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '知道了' }
    ]
  },
  template: `
    <div class="title text-center">试玩失败</div>
    <div class="desc text-center">您下载的应用错误或尚未完成安装</div>
  `
}

// 放弃进行中的任务, 抢新的任务
export const popup_snatch_new_task = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '取消'},
      {text: '确认'}
    ]
  },

  template: `
    <div class="desc">不能同时抢多个任务哦!<br>要放弃上个任务并接取该任务吗?</div>
  `
}

// 开启推送弹层
export const popup_enable_push = {
  options: {
    effect: 'scale',
    cssClass: 'popup-enable-push',
    buttons: [
      {text: '马上开启', theme: 'assertive'}
    ],
    showClose: true
  },

  template: `
    <p class="title font-regular">开启通知</p>
    <p class="sub-title font-regular">增加<span class="assertive font-bold">40%</span>任务抢夺成功率</p>
    <div class="img"></div>
  `
}

// 风险提示弹层
export const popup_remind_risk = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: `
    <div class="title">风险提示</div>
    <div class="desc">任务不要求投资，钱咖也不建议您未充分了解平台、项目前进行投资</div>
  `
}



