<template>
  <div class="assistant-banner">
    <scalable class="app-icon" width="44" height="44"><img :src="info.app_icon"></scalable>
    <div class="title">需启动助手才能开始任务</div>
    <div class="desc">您的任务助手是：<span v-text="info.app_name"></span></div>
    <button class="button button-assertive button-small btn-download" @click="showDownloadAssistantPopup()">下载</button>
  </div>
</template>
<style lang='scss' scoped>
  @import "~scss/mixins";

  .assistant-banner {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 60px;
    background: #FFF2DD;
    color: #FFF;
    width: 100%;
    padding: 10px 16px 10px 68px;
    z-index: 8;

    /* box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.15); */

    .app-icon {
      width: 44px;
      height: 44px;
      position: absolute;
      top: 8px;
      left: 14px;
      background: #FFFFFF;
      border: 0.5px solid #DDDDDD;
      border-radius: 6px;
      box-sizing: content-box;

      img {
        width: 80px;
        height: 80px;
        border-radius: 8px;
        margin-top: 4px;
        margin-left: 4px;
      }
    }

    .title {
      margin-top: 2px;
      font-size: 14px;
      line-height: 20px;
      color: #845B57;

      @include font-regular();
    }

    .desc {
      font-size: 12px;
      line-height: 17px;
      color: #C19F8C;

      @include font-regular();
    }

    .btn-download {
      position: absolute;
      width: 56px;
      height: 28px;
      top: 15px;
      right: 16px;
      border-radius: 2px;
    }
  }
</style>
<script>
  import {mapState} from 'vuex'
  import downloadAssistantPopupMixin from './downloadAssistantPopupMixin'

  export default {
    computed: {
      ...mapState('assistant', [
        'info'
      ])
    },

    mixins: [downloadAssistantPopupMixin]
  }
</script>



