<template>
  <div class="zs-entrance" @click="$router.forward('/zstasks')" >
    <div class="icon-wrapper">
      <div v-for="(icon, index) in zstask_info.icons" :key="index" class="icon" :class="{'offset1': index == 1, 'offset2': index == 2}">
        <img :src="icon" alt="">
      </div>
    </div>
    <div class="total-tip">您有{{zstask_info.total}}个专属任务</div>
    <div>
    </div>
    <div class="reward-content">
      <span class="plus">+</span>
      <span class="reward" v-text="parseFloat(zstask_info.total_reward).toFixed(1)"></span>
      <span class="yuan">元</span>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import '~scss/mixins';

  .zs-entrance {
    height: 60px;
    background: #FFF8EE;
    line-height: 60px;
    font-size: 0;

    @include font-regular();

    .icon-wrapper {
      margin-left: 15px;
      margin-right: 14px;
      display: inline-block;
      vertical-align: middle;
      height: 34px;

      .icon {
        height: 34px;
        width: 34px;
        border-radius: 6px;
        display: inline-block;
        position: relative;

        &.offset1 {
          margin-left: -16px;
        }

        &.offset2 {
          margin-left: -16px;
        }

        img {
          border: 2px solid #FFFFFF;
          border-radius: 6px;
          width: 34px;
          height: 34px;
        }
      }
    }

    .total-tip {
      display: inline-block;
      font-size: 14px;
      height: 24px;
      line-height: 24px;
      color: #3A3A3A;
      vertical-align: middle;
    }

    .reward-content {
      position: absolute;
      top: 0px;
      right: 14px;
      height: 60px;
      line-height: 60px;
      color: #EF4F2A;
      display: flex;

      @include font-medium ();

      .plus {
        order: 1;
        padding-right: 2px;
      }

      .reward {
        order: 2;
      }

      .yuan {
        order: 3;
        padding-left: 2px;
      }

      .plus, .yuan {
        font-size: 11px;
      }

      .reward {
        font-size: 22px;
      }

      .plus {
        transform: translate3d(0, 3%, 0);
      }

      .yuan {
        transform: translate3d(0, 2.5%, 0);
      }
    }
  }
</style>
<script>
  import {mapState} from 'vuex'

  export default {
    computed: {
      ...mapState('task', [
        'zstask_info'
      ])
    }
  }
</script>



