import {mapState} from 'vuex'
import {
  popup_download_assistant
} from '../popups/popups.js'
import {
  KEY_ASSISTANT_GUIDE,
  IOS_PROFILE_TRUST
} from 'constants'
import AssistantGuide from '@/AssistantGuide'
import Swipe from 'swipe-js-iso'
import {appendUrlParams} from 'utils'

const timeout = (duration) => {
  return new Promise((resolve) => {
    setTimeout(resolve, duration || 0)
  })
}

export default {
  computed: {
    ...mapState('assistant', [
      'info'
    ])
  },

  data () {
    return {
      assistantGuideModal: undefined
    }
  },

  destroyed () {
    if (this.assistantGuideModal) {
      $modal.destroy(this.assistantGuideModal)
    }
  },

  methods: {
    showDownloadAssistantPopup () {
      let s // swiper instance

      if (this.info.app_type === 3) { // 企业版助手
        const template = `
          <div id="swipe-assistant-guide-wrap">
            <div id="swipe-assistant-guide">
              <div class='swipe-wrap'>
                <div class="swipe-item">
                  <div class="step step-1"></div>
                </div>
                <div class="swipe-item">
                  <div class="step step-2"></div>
                </div>
                <div class="swipe-item">
                  <div class="step step-3"></div>
                </div>
                <div class="swipe-item">
                  <div class="step step-4"></div>
                </div>
              </div>
            </div>

            <div class="circles">
              <span class="circle active"></span>
              <span class="circle"></span>
              <span class="circle"></span>
              <span class="circle"></span>
            </div>
          </div>
        `
        let popup = $popup.fromTemplate(template, {
          effect: 'scale',
          cssClass: 'popup-assistant-guide',
          showClose: true,
          keep: true,
          buttons: [
            {
              text: '看教程',
              onClick: () => {
                popup.hide()
                console.log(s)
                timeout(300).then(() => {
                  if (s) {
                    s.kill()
                    s = undefined
                  }

                  $modal.fromComponent(AssistantGuide, {
                    title: '详细教程',
                    theme: 'dark'
                  }).then((modal) => {
                    this.assistantGuideModal = modal
                    this.assistantGuideModal.show()
                  })
                })
              }
            },
            {
              text: '去信任',
              onClick: () => {
                let guide = $storage.get(KEY_ASSISTANT_GUIDE)
                if (guide) {
                  guide.showed = 1
                  $storage.set(KEY_ASSISTANT_GUIDE, guide)
                }

                location.href = IOS_PROFILE_TRUST
              }
            }
          ]
        })

        timeout(300).then(() => {
          let mySwipe = document.getElementById('swipe-assistant-guide')
          let wrap = document.getElementById('swipe-assistant-guide-wrap')

          s = new Swipe(mySwipe, { // eslint-disable-line
            startSlide: 0,
            speed: 250,
            auto: 3000,
            continuous: true,
            disableScroll: false,
            stopPropagation: false,
            callback: function (index, slide) { console.log('slide changes') },
            transitionEnd: function (index, slide) {
              let circles = wrap.querySelectorAll('.circle')
              _.each(circles, (c) => {
                c.className = 'circle'
              })
              circles[index].className = 'circle active'
            }
          })

          popup.show()
        })

        return
      }

      let popup = $popup.fromTemplate(
        popup_download_assistant.template(this.info),
        popup_download_assistant.options
      )

      popup.show().then(buttonIndex => {
        if (buttonIndex === 0) {
          location.href = appendUrlParams(this.info.download_url, {
            'return_url': location.href
          })
        }
      })
    }
  }
}



