/* eslint eqeqeq: "off", camelcase: "off" */
require('./popups.scss')

const popup_important_001 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '确认'},
      {text: '更换绑定'}
    ]
  },

  template: (mobile) => `
    <div class="title">重要通知</div>
    <div class="desc">
      新版钱咖使用手机号进行登录, 为保证您的正常使用, 请确认您的绑定号码:
    </div>
    <h4 style="position: relative; top: 3px; color: #484746; text-align: center; font-size: 20px; font-weight: normal; margin-bottom:0; line-height: 38px;">${_.reduce(mobile, (s, v, i) => { return s + v + ((i + 2) % 4 == 0 ? ' ' : '') })}</h4>
  `
}

const popup_important_002 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '忽略'},
      {text: '绑定'}
    ]
  },

  template: `
    <div class="title">重要通知</div>
    <div class="desc">
      新版钱咖使用手机号进行登录, 为保障您可以正常使用, 请尽快绑定手机
    </div>
  `
}

const popup_important_003 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: `
    <div class="title">重要通知</div>
    <div class="desc">
      苹果证书登录为内部体验功能, 后续可能取消, 建议使用手机号登录
    </div>
  `
}

const popup_important_004 = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '立即绑定'}
    ]
  },

  template: `
    <div class="title">重要通知</div>
    <div class="desc">
      苹果证书登录为内部体验功能, 为保障您能正常登录钱咖, 请绑定手机
    </div>
  `
}

// 下注中奖弹层
const popup_lottery_win = {
  template: (win_info) => {
    let {lottery_id, reward, bets} = win_info

    return `
      <img src="//qianka.b0.upaiyun.com/images/6dc6a001c2292da7bc8ea72bb65e3eeb.png" alt="">
      <div class="win-info">
        <div class="win-money">
          +  <span class="font-big">${reward}</span> 元
        </div>
        <div class="win-desc">
          ${lottery_id}期，中奖 ${bets} 注
        </div>
      </div>
    `
  }
}

// 国庆活动弹层
const popup_national_event = {
  options: {
    effect: 'scale',
    cssClass: 'popup-national-event',
    buttons: [
      {text: '马上收徒'}
    ],
    showClose: true
  },

  template: `
    <div class="title">活动介绍</div>
    <div class="time">时间：<span>2017.9.30-10.8</span>（仅限9天)</div>
    <div class="desc">
      规则：1. 在现有收徒奖励的基础上，每日新增的第一个完成了标准任务的iOS徒弟，每完成一个标准任务，额外奖励1元，最高奖励10元；
    </div>
    <div class="desc2">2. 其他徒弟参考既有收徒规则。</div>
  `
}

module.exports = {
  popup_important_001,
  popup_important_002,
  popup_important_003,
  popup_important_004,
  popup_lottery_win,
  popup_national_event
}



