<template>
  <div class="row-list-wrap">
    <div @click="jumpTo(item.path)" v-if="!locked" v-for="(item, index) in list" :key="index" class="item-vertical" :style="{background: 'url(' + item.bg + ') no-repeat'}">
      <div class="title">{{ item.title }}</div>
      <div class="subtitle">{{ item.subTitle }}</div>
    </div>
    <div v-if="locked" class="">
      <img class="shade" src="//assets.qkcdn.com/images/b32e8431329e5bfd3d69c099a2003378.png"/>
      <img class="shade" src="//assets.qkcdn.com/images/9f9a506100a5bdbde51152fe7bcc847f.png"/>
      <img class="shade" src="//assets.qkcdn.com/images/d8f4dbc2ecf26c69b48816dbbeda392f.png"/>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "~scss/mixins";

  @mixin mrg-b ($num: 8) {
    margin-bottom: $num + px;
  }

  .row-list-wrap {
    .item-vertical {
      height: 68px;
      padding: 16px 20px;
      color: #FFFFFF;
      letter-spacing: 0.2px;
      background-size: 100% 100% !important;

      @include font-semibold();
      @include mrg-b();

      .title {
        font-size: 15px;
        font-weight: 600;
        line-height: 21px;
      }

      .subtitle {
        font-size: 10px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    img.shade {
      width: 100%;
      height: auto;
      margin-bottom: 2px;

      &:last-child {
        margin-bottom: -4px;
      }
    }
  }
</style>
<script>
  export default {
    props: {
      list: {
        type: Array,
        default: [
          {
            title: '',
            subTitle: '',
            bg: ''
          }
        ]
      },
      locked: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      jumpTo (path) {
        $router.forward(path)
      }
    }
  }
</script>



