<template>
  <div class="page">
   <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="老用户登录"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content change-nickname">
      <label for="phone">
        <div class="title">手机号</div>
        <input type="tel" id="phone" class="input" placeholder="输入手机号码" v-model="form.phone" maxlength="13">
      </label>
      <div class="division"></div>
      <button class="button button-clear btn-get-code"
          :class="{'button-royal disabled': getCodeLock, 'button-dark': !getCodeLock}"
          @click="sendCode()" v-text="getCodeText"></button>
      <div class="bottom-line"></div>
      <label for="code">
        <div class="title">验证码</div>
        <input type="tel" id="code" class="input" placeholder="输入验证码" v-model="form.code">
        <div class="bottom-line last"></div>
      </label>

      <button class="button button-submit" :class="{'can-submit': canSubmit, 'button-disabled': !canSubmit}" @click="login()">登录</button>
      <p class="assertive link-unbind-phone" @click="unbindPhone()">
        老用户未绑定手机？<span>点此登录</span>
      </p>
    </div>
  </div>
</template>
<style lang='scss' scoped>
  @import "../../scss/mixins";

  .change-nickname {
    background: #FFFFFF;
    padding-top: 75px;
    padding-left: 30px;
    color: #b5b5b5;

    @include font-regular();

    .title {
      font-size: 12px;
      height: 17px;
      line-height: 17px;
    }

    .input {
      width: 200px;
      height: 33px;
      font-size: 24px;
      line-height: 33px;
      margin-bottom: 3px;
      display: inline-block;
      padding: 0;
      color: #3A3A3A;

      @include font-regular();

      &::-webkit-input-placeholder {
        line-height: 33px;
        height: 33px;
        color: #DDDDDD;
      }
    }

    .division {
      position: absolute;
      top: 100px;
      right: 114px;
      width: 1px;
      height: 17px;
      background: #DDDDDD;
    }

    .btn-get-code {
      position: absolute;
      padding: 0;
      height: 20px;
      font-size: 14px;
      line-height: 20px;
      min-height: 20px;
      top: 98px;
      right: 30px;
    }

    .bottom-line {
      width: 200px;
      height: 1px;
      background: #DDDDDD;
      margin-bottom: 30px;

      &.last {
        margin-bottom: 60px;
      }
    }

    .button-submit {
      width: 172px;
      height: 40px;
      min-height: 40px;
      line-height: 40px;
      font-size: 13px;
      font-weight: 500;
      color: #FFFFFF;
      margin-bottom: 206px;
      border-radius: 2px;

      @include font-semibold();

      &.button-disabled {
        background: #DDDDDD;
      }

      &.can-submit {
        background-color: #ef502b;
      }
    }

    .link-unbind-phone {
      height: 20px;
      font-size: 14px;
      color: #b5b5b5;

      span {
        color: #4a90e2;
      }
    }
  }
</style>
<script>
  /* eslint eqeqeq: "off", camelcase: "off" */
  import AnimateHeader from '@/widget/animate-header/Index.vue'

  import {
    login_with_certification,
    invalid_certification,
    get_code_fail,
    popup_choose_verify_code_type
  } from './popups.js'

  import {
    FLASH_LOGIN_WITH_CERT,
    KEY_LAST_USER_CONNECT_KEY,
    COOKIE_KEY_LAST_UID
  } from 'constants.js'

  const count_down_limit = 60
  const code_unregistered_phone = 10001
  const code_not_ios_user = 10002
  var formatLock = false

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        form: {
          phone: '',
          code: ''
        },

        canSubmit: false,

        getCodeLock: false,
        getCodeText: '获取验证码',
        sec: 0,

        loginLock: false,
        voiceVerify: false
      }
    },

    watch: {
      'form.phone': function (val) {
        if (formatLock) return
        formatLock = true
        // 格式化
        let v = val.replace(/\s/g, '')
        this.form.phone = _.filter([v.substr(0, 3), v.substr(3, 4), v.substr(7, 4)], function (value) { return value !== '' }).join(' ')
        formatLock = false
        this.canSubmit = this.canFormSubmit(val, this.form.code)
      },
      'form.code': function (val) {
        this.canSubmit = this.form.phone && val && val.length == 4

        if (val.length > 4) this.form.code = val.substr(0, 4)
        this.canSubmit = this.canFormSubmit(this.form.phone, val)
      }
    },

    mounted () {
      // 老用户通过证书登录后, 着陆页在这里,
      // 检查参数中是否有 match_user
      // 若存在, 则调用接口 match_user, 判断是否成功匹配到老用户
      // 之后再各种弹层
      let search_str = location.search
      if (search_str.length > 0 && search_str.indexOf('match_user=1') >= 0) {
        $api.matchUser()
          .then(res => res.data)
          .then((data) => {
            // 若返回的 err_code == 10001
            // 则表示未关联到用户
            if (data.err_code == 10001) {
              let popup = $popup
                .fromTemplate(invalid_certification.template, invalid_certification.options)
              return popup.show()
            }
            // 登录成功
            $storage.set(FLASH_LOGIN_WITH_CERT, 1)
            $storage.set(KEY_LAST_USER_CONNECT_KEY, $cookie.get(COOKIE_KEY_LAST_UID))
            location.href = '/v4/dashboard'
          })
      }
    },

    destroyed () {
      $modal.destroy(this.agreement)
    },

    methods: {
      back () {
        if (/iPad/.test(navigator.userAgent)) {
          location.href = '/v4/'
        } else {
          let path = _.get(this.$route.query, 'backroute', '/')
          $router.back({ path })
        }
      },

      sendCode () {
        if (this.voiceVerify) {
          this.popCodeType()
        } else {
          this.getCode(1)
        }
      },

      getCode (type) {
        // 防止按钮被快速点击两次
        if (this.getCodeLock) return
        this.getCodeLock = true

        // 处于倒计时中
        if (this.sec > 0) return

        // 校验
        let phone = this.form.phone.replace(/\s/g, '')
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        }

        if (tips) {
          setTimeout(() => { this.getCodeLock = false }, 1000)
          $toast.show(tips)
          return
        }
        this.voiceVerify = true
        $api.loginSendCode({phone: phone, type: type})
          .then(res => res.data && res.data.err_code)
          .then((err_code) => {
            if (err_code == code_unregistered_phone) {
              this.unregisteredPhone()
              this.voiceVerify = false
              return
            }

            this.sec = count_down_limit
            this.countDown()
          }, () => {
            this.getCodeLock = false
          })
      },

      popCodeType () {
        let popup = $popup.fromTemplate(
            popup_choose_verify_code_type.template,
            popup_choose_verify_code_type.options
          )
        popup.show().then((index) => {
          if (index === 0) {
            this.getCode(1)
          }
          if (index === 1) {
            this.getCode(2)
          }
        })
      },

      login () {
        if (!this.canSubmit) return

        if (this.loginLock) return
        this.loginLock = true
        setTimeout(() => { this.loginLock = false }, 1000)

        let phone = this.form.phone.replace(/\s/g, '')
        let code = this.form.code
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        } else if (!code) {
          tips = '验证码不能为空'
        } else if (!/^[\d]{4}$/.test(code)) {
          tips = '验证码格式不正确'
        }

        if (tips) {
          $toast.show(tips)
          return
        }

        $api.loginByPhone({phone: phone, code: code})
          .then(res => res.data)
          .then(data => {
            if (data.err_code === code_not_ios_user) {
              $toast.show(data.err_msg)
              return
            }
            $storage.set(KEY_LAST_USER_CONNECT_KEY, $cookie.get(COOKIE_KEY_LAST_UID))
            let route = { path: '/dashboard' }
            $router.forward(route)
          })
      },

      countDown () {
        if (this.sec) {
          this.sec--
          this.getCodeText = this.sec ? this.sec + 's' : '发送验证码'
          setTimeout(() => {
            this.countDown()
          }, 1000)
        } else {
          this.getCodeLock = false
        }
      },

      // 未绑定手机情况下，用证书登录
      unbindPhone () {
        let popup = $popup
          .fromTemplate(login_with_certification.template, login_with_certification.options)

        popup.show().then((buttonIndex) => {
          if (buttonIndex == 1) { // 通过证书登录
            let return_url = location.protocol + '//' + location.host + '/v4/login?match_user=1'
            location.href = '/profile4/profile_service.mobileconfig?return_url=' + encodeURIComponent(return_url)
          }
        })
      },

      unregisteredPhone () {
        let popup = $popup.fromTemplate(get_code_fail.template, get_code_fail.options)
        popup.show().then((buttonIndex) => {
          if (buttonIndex == 1) { // 创建新账号
            $api.createUser()
              .then(() => {
                $api.dashboard().then(() => {
                  $router.forward({
                    path: '/dashboard'
                  })
                })
              })
          } else {
            this.getCodeLock = false
          }
        })
      },

      canFormSubmit (phone, code) {
        return phone && phone.length == 13 && code && code.length == 4
      }
    }
  }
</script>



