/* eslint no-unused-vars: "off" */
import {
  KEY_TASK_STEP,
  TASK_STATE_APP_UNINSTALLED,
  TASK_STATE_APPSTORE_HAS_GO,
  TASK_STATE_APP_INSTALLED,
  TASK_STATE_APP_OPENED,
  TASK_STATE_LOCAL,
  KEY_DOUBLE_IS_NEED_HIDE,
  KEY_UID
} from 'constants'

import {is_ios_11} from 'utils'

import { popup_task_ongoing } from '../components/task/popups/popups'

const POLLING_DELAY = 1000

const initState = () => ({
  task_id: 0, // 任务ID
  task_status: 0, // 任务状态（1: 其他任务进行中 2: 正常 3: 已完成或未开始）
  reward: '0', // 任务奖励
  app_keyword: '', // 应用关键字
  is_manually_appstore: 0, // 关键字搜索是否需要手工打开appstore 1需要手工打开，0不需要
  app_rank: 0, // 应用排名
  app_name: '', // 应用名称，有关键字的任务返回空字符串
  app_icon: '', // 应用图标
  app_url: '', // 跳转链接
  app_bid: '', // 应用bundle id
  app_exe: '', // 应用exe
  tips: '', // 试玩提示
  expire_at: 0, // 任务过期时间戳（含毫秒）
  exclusive_count: 0, // 专属任务个数，无专属任务则返回0
  exclusive_reward: '', // 专属任务奖励，无专属任务则返回空字符串
  show_coupon_btn: 0, // 是否显示双倍券按钮
  double_reward: '0', // 任务双倍奖励
  coupon_num: 0,

  // 非payload
  steps: [1, 0, 0],
  listening: false,
  show_coupon: 0, // 双倍券是否展示
  cancel_poll: false,

  // iOS11给钥匙的回调
  callback_url: '',
  is_invest: 0 // 是否是投资类型任务，是则弹弹层
})

const state = initState()

const saveCouponStatus = () => {
  // 已点击试玩不需要展示双倍券提示的任务
  let coupon_need_hide = $storage.get(KEY_DOUBLE_IS_NEED_HIDE) || []
  let need_hide_id = $storage.get(KEY_UID) + '-' + state.task_id
  if (coupon_need_hide.indexOf(need_hide_id) === -1) {
    coupon_need_hide.push(need_hide_id)
    $storage.set(KEY_DOUBLE_IS_NEED_HIDE, coupon_need_hide)
  }
}

const types = {
  INIT: 'init',
  SYNC: 'sync',
  COUPON_SHOW_STATUS: 'coupon_show_status',
  UPDATE_STEPS: 'update_steps',
  CANCEL_POLL: 'cancel_poll'
}

const getters = {
  couponNum (state) {
    return state.double_reward - state.reward
  }
}

const mutations = {
  [types.INIT] (state) {
    _.extend(state, initState())
  },

  [types.SYNC] (state, payload) {
    _.extend(state, payload)
  },

  [types.COUPON_SHOW_STATUS] (state, payload) {
    state.show_coupon = payload
  },

  [types.UPDATE_STEPS] (state, status) {
    switch (status) {
      case TASK_STATE_APP_UNINSTALLED:
        state.steps = [1, 0, 0]
        break
      case TASK_STATE_APPSTORE_HAS_GO:
        state.steps = [2, 0, 0]
        break
      case TASK_STATE_APP_INSTALLED:
        state.steps = [2, 1, 0]
        break
      case TASK_STATE_APP_OPENED:
        state.steps = [2, 2, 1]
        break
      case TASK_STATE_LOCAL: // 按本地记录
        let task_step = $storage.get(KEY_TASK_STEP)
        if (task_step && task_step.task_id === state.task_id) {
          _.each(task_step.steps.split('|'), (s, i) => {
            state.steps[i] = parseInt(s)
          })
        }
    }

    $storage.set(KEY_TASK_STEP, {
      task_id: state.task_id,
      steps: state.steps.join('|')
    })
  },

  [types.CANCEL_POLL] (state) {
    state.cancel_poll = true
  }
}

const actions = {
  fetch ({ commit }, task_id) {
    commit(types.INIT)

    return $api
      .subtaskDetail(task_id)
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          // task_status: 2:进行中 3:已完成或未开始
          if (payload.task_status !== 2) {
            return $router.back('/tasks')
          }
          // 双倍券展示逻辑
          payload.expire_at = payload.expire_at * 1000
          if (payload.app_rank === -1) {
            payload.app_rank = 'X'
          }
          commit(types.SYNC, payload)

          if (payload.show_coupon_btn) {
            let coupon_need_hide = $storage.get(KEY_DOUBLE_IS_NEED_HIDE) || []
            let need_hide_id = $storage.get(KEY_UID) + '-' + state.task_id
            if (coupon_need_hide.indexOf(need_hide_id) === -1) {
              let show_coupon = 1
              commit(types.COUPON_SHOW_STATUS, show_coupon)
            }
          }

          /* 每次刷新任务详情时，call钥匙心跳 */
          $assistant.updateState()

          commit(types.UPDATE_STEPS, TASK_STATE_LOCAL)

          function listen_installation (resolve) {
            const poll = (resolve) => {
              setTimeout(() => {
                $assistant
                  .getAppInfo(state.app_bid)
                  .then((res) => {
                    let payload = res && res.data && res.data.payload

                    if (payload && payload.bundle_id) {
                      resolve()
                      return
                    } else {
                      if (state.steps[0] === 2) {
                        commit(types.UPDATE_STEPS, TASK_STATE_APPSTORE_HAS_GO)
                      } else {
                        commit(types.UPDATE_STEPS, TASK_STATE_APP_UNINSTALLED)
                      }
                    }

                    if (!state.cancel_poll) poll(resolve)
                  })
              }, 1000)
            }

            return new Promise(resolve => {
              poll(resolve)
            })
          }

          if (!is_ios_11()) {
            listen_installation()
              .then(() => {
                if (state.steps[2] === 1) return
                commit(types.UPDATE_STEPS, TASK_STATE_APP_INSTALLED)
              })
          }
        })
  },

  _openApp ({commit}) {
    $assistant
      .getAppInfo(state.app_bid)
        .then(res => res && res.data && res.data.payload)
        .then((payload) => {
          if (payload && payload.bundle_id) {
            if (payload.bundle_version) {
              $assistant
                .openApp({
                  bid: state.app_bid,
                  exe: state.app_exe
                })
                .then(() => {
                  $api.subtaskOpenApp({
                    task_id: state.task_id,
                    task_type: 1
                  })
                  commit(types.UPDATE_STEPS, TASK_STATE_APP_OPENED)
                  commit(types.COUPON_SHOW_STATUS, 0)
                  saveCouponStatus()
                })
            } else { // 应用下载中
              let template = `
                <div class="text-center">
                  应用下载中，请稍后打开
                </div>
              `

              $popup
                .fromTemplate(template, {
                  buttons: [{ text: '知道了' }]
                })
                .show()
            }
          } else { // 应用未安装
            $toast.show('您下载了错误应用或尚未完成安装')
          }
        })
      .catch(e => console.log(e))
  },

  _openAppWith11 ({commit}) {
    // 打开应用
    $assistant.openAppIOS11({
      bid: state.app_bid,
      url: state.callback_url
    }).then(res => res.data && res.data.status)
      .then((status) => {
        if (status === 'ok') {
          commit(types.UPDATE_STEPS, TASK_STATE_APP_OPENED)
          /* 打开应用成功以后，call钥匙心跳 */
          $assistant.updateState()
          saveCouponStatus()
        } else {
          $toast.show('您下载了错误应用或尚未完成安装')
        }
      })
  },

  _checkTaskStatus ({dispatch}) {
    $api.subtaskState(state.task_id)
      .then(res => res.data && res.data.payload)
      .then(payload => {
        if (payload.status === 3) { // 任务已完成或未开始
          return $router.back('/tasks')
        }

        if (payload.status === 2) { // 任务进行中
          let remain_seconds = payload.remain_seconds
          // 向上取整
          let time_str = Math.ceil(remain_seconds / 60) + '分钟'
          $popup.fromTemplate(
            popup_task_ongoing.template(
              '时间还不够哟',
              `再试玩${time_str}就能领奖啦`
            ),
            popup_task_ongoing.options
          ).show().then((buttonIndex) => {
            // close按钮
            if (buttonIndex === -1) {
              return
            } else if (buttonIndex === 0) {
              // 继续试玩
              if (!is_ios_11()) {
                dispatch('_openApp')
              } else {
                // TODO: ios11逻辑
                dispatch('_openAppWith11')
              }
            }
          })
        }

        /* 提交审核，向服务器发起请求的同时，call钥匙心跳 */
        $assistant.updateState()
      })
  },

  _useCoupon ({commit}) {
    return new Promise((resolve) => {
      $api.useCoupon(state.task_id)
        .then(res => res.data)
        .then((data) => {
          commit(types.COUPON_SHOW_STATUS, 0)

          if (data.err_code === 40001) {
            $toast.show('没有可用的双倍券')
            return
          } else if (data.err_code === 40002) {
            $toast.show('当前任务已经使用过双倍券')
            return
          }
          resolve()
          // TOOD: 这里不用持久化双倍券的事情情况吗？
        })
    })
  },

  updateSteps ({ commit }, status) {
    commit(types.UPDATE_STEPS, status)
  },

  cancelPoll ({ commit }) {
    commit(types.CANCEL_POLL)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



