import Vue from 'vue'
import Vonic from 'vonic'
import 'services'

// Global scss
import './scss/diablo.scss'

// Mount Incognito Browsing page
import Incognito from './components/Incognito'
let IncognitoComponent = Vue.extend(Incognito)
let incognito = new IncognitoComponent().$mount('[qk-incognito]') // vm
let incognitoError = 'incognito mode'
if (incognito.getState() === 1) throw incognitoError

// Page Components

// 欢迎页
import Index from '@/welcome/Index'
// 老用户登录
import Login from '@/login/Login'
// 首页
import Dashboard from '@/dashboard/Index'

// 试玩任务、专属任务、简易任务
import taskRoutes from '@/task/routes'
// 彩蛋任务
import eggsRoutes from '@/eggs/routes'
// 个人中心
import userRoutes from '@/user_center/routes'
// 高额任务路由
import gaoeRoutes from '@/gaoe/routes'
// 收徒任务
import apprenticeRoutes from '@/apprentice/routes'
// 应用中心
import hotappsRoutes from '@/hotapps/routes'
// 信用卡任务
import creditRoutes from '@/credit/routes'
// 芝麻信用
import zhimaRoutes from '@/zhima/routes'
// 抽奖
import lotteryRoutes from '@/lottery/routes'
// 返利
import rebateRoutes from '@/rebate/routes'
// 借钱
import BorrowRoutes from '@/borrow/routes'
// 闪电借钱
import LoanRoutes from '@/loan/routes'

// store
import store from './store'

// Routes
let routes = [
  { path: '/', name: 'welcome', component: Index },
  { path: '/login', name: 'login', component: Login },
  { path: '/dashboard', name: 'dashboard', component: Dashboard }
]

routes = routes
  .concat(taskRoutes)
  .concat(eggsRoutes)
  .concat(userRoutes)
  .concat(gaoeRoutes)
  .concat(apprenticeRoutes)
  .concat(hotappsRoutes)
  .concat(zhimaRoutes)
  .concat(lotteryRoutes)
  .concat(creditRoutes)
  .concat(rebateRoutes)
  .concat(BorrowRoutes)
  .concat(LoanRoutes)

// failback to index when 404
routes = routes.concat([{ path: '*', component: Dashboard }])

// Mount Customer Service
import CustomerService from './components/CustomerService'
let CustomerServiceComponent = Vue.extend(CustomerService)
new CustomerServiceComponent({store}).$mount('[qk-customer-service]')

// HTML5 History Mode
Vonic.app.setConfig('routerOptions', {
  mode: 'history',
  base: __ENV__.base
})

// Vonic.app.setConfig('pushMethod', 'replace')

Vonic.app.setConfig('beforeEach', (to, from, next) => {
  // 从这几个页面需要关闭钥匙轮询
  if (from && from.name && (_.indexOf([
    'tasks',
    'task_detail',
    'zstasks',
    'zstask_detail',
    'gaoe',
    'apprentice'
  ], from.name) > -1)) {
    $assistant.cancelPoll()
  }

  // 判断即将跳转至的路由是否需要显示或隐藏咖妹
  const customerServiceRoutes = ['task_detail', 'easy_detail']
  $bus.$emit(
    (to && to.name && _.indexOf(customerServiceRoutes, to.name) > -1)
      ? 'showCustomerService' : 'hideCustomerService'
  )

  next()
})

import { crius, uuid } from 'utils'
import { KEY_UUID } from 'constants'

Vonic.app.setConfig('afterEach', (to, from) => {
  // 生成统计用 UUID 并保存到本地
  if (!$storage.get(KEY_UUID)) {
    $storage.set(KEY_UUID, uuid())
  }

  // 全局兼容iPad滑动
  let isPad = /iPad/.test(navigator.userAgent)
  if (isPad) {
    setTimeout(() => {
      let pageDom = document.querySelector('.page-content')
      pageDom.classList.add('no-scrolling')
    }, 1000)
  }

  // 统计路由切换
  if (__ENV__ && __ENV__.stat_switch === 1) {
    let msg = {
      url: encodeURIComponent(location.href),
      u: $storage.get('qk:biz:uid'),
      uu: $storage.get('qk:biz:uuid'),
      a: 'qianka',
      t: 'PAGE',
      c: 'page_' + (to.name || 'anonymous_route')
    }

    if (from && from.name) {
      msg.ref = 'page_' + from.name
    } else {
      msg.ref = encodeURIComponent(document.referrer)
    }

    crius.send(msg)
  }
})

Vue.use(Vonic.app, {
  routes,
  store
})

// Mount Webclip
import Webclip from './components/Webclip'
let WebclipComponent = Vue.extend(Webclip)
new WebclipComponent().$mount('[qk-webclip]')

// 全局芝麻信用认证流程
import ZhimaGlobal from './components/ZhimaGlobal'
let ZhimaGlobalComponent = Vue.extend(ZhimaGlobal)
new ZhimaGlobalComponent({
  store
}).$mount('[qk-zhima]')

Vue.prototype.forward = $router.forward
Vue.prototype.back = $router.back

// import RouterExt from 'services/router-ext'
// $router.forwardAfterApi = RouterExt.forwardAfterApi
// $router.backAfterApi = RouterExt.backAfterApi

import FastClick from 'fastclick'
FastClick.attach(document.body)



