<!-- 倒计时组件 -->
<template>
  <span>
    <span v-text="value"></span><span v-if="value===0 || isInt(value)">.00</span>
  </span>
  </span>
</template>
<script>
  /* eslint eqeqeq: "off", camelcase: "off" */

  import {
    KEY_TASK_LAST_REWARD_VALUE
  } from 'constants'

  export default {
    props: {
      end: {
        type: Number,
        validator: function (v) {
          return parseFloat(v) == v
        },
        required: true
      },
      cssClass: {
        type: String,
        default: ''
      }

    },

    data () {
      return {
        value: null,
        timer: null,
        runTime: 1000, // 1sec
        delay: 10,
        incrimentsArray: []
      }
    },
    created () {
      this.initAnimateNumber()
    },

    mounted () {
    },

    destroyed () {
      if (this.timer) clearInterval(this.timer)
    },

    methods: {
      updateValue () {
        if (this.value < this.end) {
          this.value = (parseFloat(this.value) + this.incriment).toFixed(2)
        } else {
          if (this.value != this.end) {
            this.value = parseFloat(this.end).toFixed(2)
          }
          clearInterval(this.timer)
        }
      },
      isInt (num) {
        return Number.isInteger(num)
      },

      run (endVal) {
        this.start = this.getAnimateNumberStartVal()
        this.end = parseFloat(endVal).toFixed(2)
        $storage.set(KEY_TASK_LAST_REWARD_VALUE, {num: this.end})
        if (this.cssClass) this.$el.classList.add(this.cssClass)
        if (parseFloat(this.end) === parseFloat(this.value)) return  // parseFloat ensures number comparison, since toFixed converts to string
        if (parseFloat(this.start) > parseFloat(this.end)) {
          this.value = this.end
          return
        }
        this.incrimentsArray = this.counterUp(this.start, this.end)
        this.timer = setInterval(this.setVal, 10)
      },
      setVal () {
        if (this.incrimentsArray && this.incrimentsArray.length) {
          this.value = this.incrimentsArray.shift()
        } else {
          clearInterval(this.timer)
        }
      },
      initAnimateNumber () {
        this.start = this.end = this.value = this.getAnimateNumberStartVal()
      },
      getAnimateNumberStartVal () {
        let cached = $storage.get(KEY_TASK_LAST_REWARD_VALUE)
        return (cached && cached.num) ? parseFloat(cached.num).toFixed(2) : parseFloat(0).toFixed(2)
      },
      counterUp (start, end) {
        let nums = []
        let num = parseFloat(end - start).toFixed(2)
        let divisions = this.runTime / this.delay
        let isFloat = /^[0-9]+\.[0-9]+$/.test(num)
        let decimalPlaces = isFloat ? (num.split('.')[1] || []).length : 0
        // Generate list of incremental numbers to display
        for (let i = divisions; i >= 1; i--) {  // handles floats
          let newNum = parseFloat(((num / divisions) * i) + parseFloat(start)).toFixed(decimalPlaces)
          nums.unshift(newNum)
        }
        return nums
      }
    }
  }
</script>



