const types = {
  SYNC_DASHBOARD: 'sync_dashboard',
  SYNC_GIFT: 'sync_gift'
}

const state = {
  uid: 0, // 用户id
  nickname: null, // 昵称
  avatar: '//assets.qkcdn.com/images/3a451d5f90dfda4494a00c94e97513d2.png!200x200',
  balance: '0.00', // 总金额
  total_reward: '0.00', // 累计收入
  today_reward: '0.00', // 今日收入
  lottery: { // 每日开奖
    status: 1,
    win: 0
  },
  create_day: '', // 新人创建日期
  bind_mobile: 0, // 是否绑定手机
  gift: 0, // 新人红包
  show_customer: 0, // 显示咖妹客服
  has_finished_task_once: 1 // 接口请求没完成，设置为1，默认显示.是否完成过限时任务 1：是 0：否
}

const getters = {
}

const mutations = {
  [types.SYNC_DASHBOARD] (state, payload) {
    let jfb_account = payload.jfb_account
    if (jfb_account && jfb_account.is_open) {
      payload.total_reward = (parseFloat(payload.total_reward) + parseFloat(jfb_account.total_to_price)).toFixed(2)
      payload.today_reward = (parseFloat(payload.today_reward) + parseFloat(jfb_account.today_to_price)).toFixed(2)
      payload.balance = (parseFloat(payload.balance) + parseFloat(jfb_account.surplus_to_price)).toFixed(2)
    }
    Object.assign(state, payload)
    console.log('state: ', state)
  },
  [types.SYNC_GIFT] (state, gift) {
    state.gift = gift
  }
}

const actions = {
  dashboard ({commit}) {
    return $api.dashboard()
      .then((res) => res.data && res.data.payload)
      .then((payload) => {
        console.log('payload: ', payload)
        commit(types.SYNC_DASHBOARD, payload)
      })
  },
  lotteryGetReward ({commit}, lottery_id) {
    return $api.lotteryGetReward(lottery_id)
      .then((res) => res.data && res.data)
  },
  fetchGift ({commit}) {
    return $api.createUser()
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC_GIFT, payload.gift)
      })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



