// 开奖首页
const Lottery = r => require.ensure([], () => r(require('./Lottery')), 'lottery')

// 规则
const Rule = r => require.ensure([], () => r(require('./Rule')), 'lottery-rule')

// 获取筹码
const GetChips = r => require.ensure([], () => r(require('./GetChips')), 'lottery-getchips')

// 筹码明细
const ChipsDetail = r => require.ensure([], () => r(require('./chipsDetail/Index')), 'lottery-chipsdetail')

// 下注
const Betting = r => require.ensure([], () => r(require('./Betting')), 'lottery-betting')

export default [
  { path: '/lottery', name: 'lottery', component: Lottery },
  { path: '/lottery/rule', name: 'lottery_rule', component: Rule },
  { path: '/lottery/betting', name: 'lottery_betting', component: Betting },
  { path: '/lottery/getChips', name: 'lottery_getChips', component: GetChips },
  { path: '/lottery/chipsDetail/:type', name: 'lottery_chipsDetail', component: ChipsDetail }
]



