/* eslint no-unused-vars: "off" */
import {
  popupInstallApp
} from '../components/task/popups/popups'
import {appendUrlParams} from 'utils'

const initState = () => ({
  tasks: [],
  zstask_info: {
    id: 0,
    status: 0,
    reward: '0.0',
    icon: '',
    title: '',
    hint: '',
    bundle_id: '',
    appstore_url: '',
    zs_tasks: [],
    subtask_id: '',
    callback_url: ''
  },
  steps: [1, 0]
})

const state = initState()

const types = {
  INIT: 'init',
  SYNC: 'sync',
  FETCH_ZSTASK_INFO: 'fetch_zstask_info',
  UPDATE_STEPS: 'update_steps'
}

const getters = {

}

const mutations = {
  [types.INIT] (state) {
    _.extend(state, initState())
  },

  [types.SYNC] (state, payload) {
    state.tasks = payload.tasks
  },

  [types.FETCH_ZSTASK_INFO] (state, payload) {
    state.zstask_info = payload
  },

  [types.UPDATE_STEPS] (state, steps) {
    state.steps = steps
  }
}

const actions = {
  fetch ({ commit }) {
    commit(types.INIT)

    $api.zstasks()
      .then((res) => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC, payload)
      })
  },

  fetchDetail ({ commit }, task_id) {
    return new Promise((resolve) => {
      $api.zstaskDetail(task_id)
        .then((res) => res.data && res.data.payload)
        .then((payload) => {
          commit(types.FETCH_ZSTASK_INFO, payload)
          resolve()
        })
    })
  },

  _openApp ({commit}) {
    let zstask_info = state.zstask_info
    let bundleId = zstask_info.bundle_id
    let appstoreUrl = zstask_info.appstore_url
    if (zstask_info.status === 0) {
      // 任务还没有开始
      // 检查应用是不是已经安装
      $assistant.getAppInfo(bundleId)
        .then((response) => {
          let resp = response.data.payload
          if (resp && resp.bundle_id && resp.bundle_version) {
            // 先通知后端开始任务
            $api.zstaskStart(zstask_info.id)
              .then((response) => {
                // 打开应用
                $assistant.openApp({ bid: bundleId })
                  .then((response) => {
                    // 打开应用后，需要通知服务器端开始任务计时
                    $api.subtaskOpenApp({ task_id: zstask_info.subtask_id, task_type: 2 })
                      .then((response) => {
                        commit(types.UPDATE_STEPS, [1, 1])
                        // 发送keyupstatus通知
                        $assistant.updateState()
                          .then((response) => {
                            $api.zstaskCheckState(zstask_info.id)
                          })
                      })
                  })
              })
          } else {
            // 应用没有安装
            let p = popupInstallApp
            let popup = $popup.fromTemplate(p.template, p.options)
            popup.show().then((btn) => {
              if (btn === 1) { // 下载
                location.href = appendUrlParams(appstoreUrl, {
                  'return_url': location.href
                })
              }
            })
          }
        })
    } else if (zstask_info.status === 2) {
      // 任务进行中， 直接打下应用
      $assistant.openApp({ bid: bundleId })
        .then((response) => {
          // 打开应用后，需要通知服务器端开始任务计时
          $api.subtaskOpenApp({ task_id: zstask_info.subtask_id, task_type: 2 })
          .then((response) => {
            commit(types.UPDATE_STEPS, [1, 1])
            // 发送keyupstatus通知
            $assistant.updateState()
          })
        })
    } else {
      // 其它， 已完成或无效， 直接回列表
      setTimeout(() => {
        $router.back({
          path: '/zstasks'
        })
      }, 600)
    }
  },

  _openAppWith11 ({commit}) {
    let zstask_info = state.zstask_info
    let bundleId = zstask_info.bundle_id
    let callback_url = zstask_info.callback_url
    if (zstask_info.status === 0) {
      // 任务还没有开始
      // 先通知后端开始任务
      $api.zstaskStart(zstask_info.id)
        .then((response) => {
          // 打开应用
          $assistant.openAppIOS11({ bid: bundleId, url: callback_url })
            .then(res => res.data && res.data.status)
            .then((status) => {
              if (status === 'ok') {
                commit(types.UPDATE_STEPS, [1, 1])
                $api.zstaskCheckState(zstask_info.id)
              } else {
                let appstoreUrl = zstask_info.appstore_url
                window.location = appstoreUrl
              }
            })
        })
    } else if (zstask_info.status === 2) {
      // 任务进行中， 直接打下应用
      $assistant.openAppIOS11({ bid: bundleId, url: callback_url })
        // 打开应用后，需要通知服务器端开始任务计时
        .then(res => res.data && res.data.status)
        .then((status) => {
          if (status === 'ok') {
            commit(types.UPDATE_STEPS, [1, 1])
            $api.zstaskCheckState(zstask_info.id)
          } else {
            let appstoreUrl = zstask_info.appstore_url
            window.location = appstoreUrl
          }
        })
    } else {
      // 其它， 已完成或无效， 直接回列表
      setTimeout(() => {
        $router.back({
          path: '/zstasks'
        })
      }, 600)
    }
  },

  _checkReward ({commit}) {
    $api
      .zstaskCheckState(state.zstask_info.id)
      .then((response) => {
        let payload = response.data.payload
        let status = payload.status

        if (status === 1) {
          $router.back('/zstasks')
          return
        }
        if (status === 2) { // 任务进行中
          let template = `
            <div class="title text-center">试玩进行中</div>
            <div class="desc text-center">满30秒后即可领取奖励</div>
          `
          $popup
            .fromTemplate(template, {
              effect: 'scale',
              cssClass: 'popup-general',
              buttons: [{ text: '知道了' }]
            })
            .show()
        }
        if (status === 0 || status === 3) {
          let message = status === 0 ? '任务未开始' : '任务已作废'

          let template = (message) => {
            return `
              <div class="desc text-center">${message}</div>
            `
          }

          $popup
            .fromTemplate(template(message), {
              effect: 'scale',
              cssClass: 'popup-general',
              buttons: [{ text: '返回任务列表' }]
            })
            .show()
            .then(btn => {
              $router.forward('/zstasks')
            })
          return
        }

        // 发送keyupstatus通知
        $assistant.updateState()
      })
  },

  updateSteps ({ commit }, steps) {
    commit(types.UPDATE_STEPS, steps)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



