<template>
  <div class="list-wrap">
    <div @click="jumpTo(item.path)" v-for="(item, index) in list" :key="index" :item="item" class="column"
       :style="{background: 'url(' + item.bg + ') no-repeat', color: item.color}">
      <div class="title">{{ item.title }}</div>
      <div class="sub-title">{{ item.subTitle }}</div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "~scss/mixins";

  @mixin mrg-t ($num: 8) {
    margin-top: $num + px;
  }

  @mixin mrg-r ($num: 9) {
    margin-right: $num + px;
  }

  .list-wrap {
    div.column:nth-child(1) {
      @include mrg-r();
    }
  }

  .column {
    width: 163px;
    height: 138px;
    display: inline-block;
    background-size: 100% 100% !important;
    padding-top: 12px;
    padding-left: 16px;

    @include mrg-t();

    .title {
      font-size: 16px;
      font-weight: 600;
    }

    .sub-title {
      font-size: 11px;
      font-weight: 600;
    }

    @include font-semibold();
  }
</style>
<script>
 export default {
   props: {
     list: {
       type: Array,
       default: [{
         bg: '',
         color: '#3a3a3a',
         title: '',
         subTitle: ''
       }]
     }
   },
   data () {
     return {
     }
   },
   methods: {
     jumpTo (path) {
       $router.forward(path)
     }
   }
 }
</script>



