<template>
  <div class="page-content qk-bind-zhima-form">
    <div class="bnind-progress">
      <div class="progress progress-left"><img src="//qianka.b0.upaiyun.com/images/4d9a91998d5f76215d9906bd244f684a.png" alt=""><span>立即认证</span></div>
      <div class="progress-arrow-right"><img src="//qianka.b0.upaiyun.com/images/55f80168960728176a548daf0f8eaad3.png" alt=""></div>
      <div class="progress progress-middle"><img src="//qianka.b0.upaiyun.com/images/d1bcfe4f2d596dd55b84bf1bee933de3.png" alt=""><span>同意协议</span></div>
      <div class="progress-arrow-left"><img src="//qianka.b0.upaiyun.com/images/55f80168960728176a548daf0f8eaad3.png" alt=""></div>
      <div class="progress progress-right"><img src="//qianka.b0.upaiyun.com/images/705b3ea7c079440cba9b79cc1101ebec.png" alt=""><span>身份验证</span></div>
    </div>
    <form action="/s4/3rd/zhima.register" method="post" id="bind-phone">
      <div class="list list-ios login-form">
        <div class="item item-borderless">
            <input class="" ref="phone" type="tel" name="phone" placeholder="芝麻信用实名认证时绑定的手机号" v-model="form.phone" @focus="focus()" @blur="blur()">

            <div class="btn-clear-mobile" v-if="showClearMobileBtn" @click="clearMobile()">
              <img src="//qianka.b0.upaiyun.com/images/d9736469d2fe0512ee89485fbf1a7689.png">
            </div>
        </div>
      </div>
    </form>
    <div class="item-submit">
      <button class="button button-block button-bind" @click="submit()">{{submitText}}</button>
    </div>
    <div class="bind-remind">
      <span>1. 以上信息仅用于认证并确认用户唯一性</span>
      <span>2. 认证后钱咖将更加精准的为您匹配限时任务</span>
    </div>
  </div>
</template>

<style lang='scss' scoped>
  @import '../../scss/variables';
  @import '../../scss/mixins';

  .qk-bind-zhima-form {
    background-color: #EEEEEE;
    .hairline-top {
      @include hairline(top);
    }

    .bnind-progress {
      position: relative;
      background-color: #FFFFFF;
      height: 100px;
      .progress {
        position: absolute;
        display: inline-block;
        img {
          width: 52px;
          height: 42px;
        }
        &.progress-left {
          top: 14px;
          left: 52px;
          span {
            display: block;
            @include font-regular;
            font-size: 11px;
            color: #888888;
            letter-spacing: 0;
            line-height: 18px;
          }
        }

        &.progress-middle {
          top: 14px;
          left: 162px;
          span {
            display: block;
            @include font-regular;
            font-size: 11px;
            color: #C5C4C3;
            letter-spacing: 0;
            line-height: 18px;
          }
        }

        &.progress-right {
          top: 14px;
          left: 272px;
          span {
            display: block;
            @include font-regular;
            font-size: 11px;
            color: #C5C4C3;
            letter-spacing: 0;
            line-height: 18px;
          }
        }
      }
      .progress-arrow-left {
        position: absolute;
        top: 25px;
        left: 126px;
        img {
          width: 12px;
          height: 8px;
        }
      }
      .progress-arrow-right {
        position: absolute;
        top: 25px;
        right: 125px;
        img {
          width: 12px;
          height: 8px;
        }
      }
    }

    .btn-clear-mobile {
      position: absolute;
      top: 19px;
      right: 8px;
      width: 24px;
      height: 50px;
      line-height: 12px;
      img {
        width: 12px;
        height: 12px;
      }
    }

    .item-submit {
      padding: 0 16px;
      .button {
        border-radius: 2px;
        background-color: #04C9B3;
        @include font-regular;
        font-size: 16px;
        color: #FFFFFF;
      }

    }

    .login-form {
      margin-bottom: 30px;
      margin-top: 10px;
      .item {
        padding: 8px 0;
        input {
          width: 100%;
        }
      }
    }

    .bind-remind {
      position: absolute;
      left: 16px;
      font-size: 13px;
      color: #B5B5B5;
      letter-spacing: 0;
      line-height: 22px;
      span {
        display: block;
      }
    }
  }
</style>

<script>
  export default {
    data () {
      return {
        form: {
          phone: ''
        },
        submitText: '提交',
        showClearMobileBtn: false,
        // 数据是否符合校验
        phoneOK: false
      }
    },

    mounted () {
      this.submitText = '提交'
    },

    watch: {
      'form.phone': function (val) {
        if (val && val.length > 0) {
          this.showClearMobileBtn = true
        } else {
          this.showClearMobileBtn = false
        }

        if (!val) {
          this.phoneOK = false
        } else if (!/^1[23456789][\d]{9}$/.test(val)) {
          this.phoneOK = false
        } else {
          this.phoneOK = true
        }

        if (val.length > 11) this.form.phone = val.substr(0, 11)
      }
    },

    methods: {
      clearMobile () {
        this.form.phone = ''
        this.$refs.phone.focus()
      },

      focus () {
        if (this.form.phone && this.form.phone > 0) {
          this.showClearMobileBtn = true
        }
      },

      blur () {
        this.showClearMobileBtn = false
      },

      check_input () {
        let phone = this.form.phone
        let tips = ''

        if (!phone) {
          tips = '手机号不能为空'
        } else if (!/^1[23456789][\d]{9}$/.test(phone)) {
          tips = '手机号格式不正确'
        }
        return tips
      },

      submit () {
        let tips = this.check_input()
        if (tips) {
          $toast.show(tips)
          return
        }
        this.submitText = '正在跳转芝麻信用官网...'
        setTimeout(() => {
          document.getElementById('bind-phone').submit()
        }, 1000)
      }
    }
  }
</script>



