/* eslint no-unused-vars: off */
const types = {
  SYNC_LOANS: 'sync_loans',
  SYNC_LOAN_DETAIL: 'sync_loan_detail'
}

// utilities
const makeLoans = (payload) => {
  return _.map(payload, raw => {
    return _.assign(_.pick(raw, [
      'id',
      'icon',
      'rate',
      'tags'
    ]), {
      name: raw.title,
      rateType: raw.rate_type,
      releaseIn: raw.loan_time,
      releaseInUnit: raw.loan_time_unit,
      amount: raw.loan_money_min === raw.loan_money_max
        ? raw.loan_money_min
        : raw.loan_money_min + '～' + raw.loan_money_max
    })
  })
}

const state = {
  loans: [],
  loanDetail: {}
}

const getters = {

}

const mutations = {
  [types.SYNC_LOANS] (state, loans) {
    _.each(loans, (l) => {
      if (l.tags.length === 0) {
        l.tags.push('仅需身份证即可放款')
      } else {
        l.tags.splice(0, 0, '身份证')
      }
    })
    state.loans = loans
  },

  [types.SYNC_LOAN_DETAIL] (state, payload) {
    let {loan_money_min, loan_money_max, loan_time_min, loan_time_max} = payload
    payload.loan_range = loan_money_min +
      (loan_money_min === loan_money_max ? '' : '～' + loan_money_max)

    payload.loan_time_range = loan_time_min +
      (loan_time_min === loan_time_max ? '' : '～' + loan_time_max)

    payload.tags.unshift('身份证')
    state.loanDetail = payload
  }
}

const actions = {
  fetchLoans ({commit}, payload) {
    let {amount, releaseIn, qulifications} = payload
    $loading.show()
    return $api.loanList(amount, releaseIn, qulifications)
      .then(res => res.data && res.data.payload)
      .then(payload => {
        commit(types.SYNC_LOANS, makeLoans(payload))
        $loading.hide()
        return payload
      })
  },

  fetchDetail ({ commit }, id) {
    return $api.getLoanDetail(id)
      .then(res => res.data && res.data.payload)
      .then(payload => {
        commit(types.SYNC_LOAN_DETAIL, payload)
        return payload
      })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



