<template>
  <CustomPopup ref="p">
    <div class="popup-body popup-assistant popup-invalid-assistant">
      <div class="text">
        您的助手已失效
      </div>

      <button class="button button-assertive btn-download" @click="download()">下载新助手</button>

      <button class="button button-royal button-small button-clear button-close" @click="hide(-1)">
        <i class="ion-ios-close-empty"></i>
      </button>
    </div>
  </CustomPopup>
</template>
<style lang='scss'>
  @import './assistant';
</style>
<script>
  import {mapGetters, mapActions} from 'vuex'
  import CustomPopup from '@/widget/CustomPopup.vue'
  import {channel, events} from 'store/channel'

  export default {
    components: {
      CustomPopup
    },

    props: {
      mode: {
        type: String,
        default: 'auto',
        validator: function (value) {
          return value === 'auto' || value === 'manual'
        }
      }
    },

    computed: {
      ...mapGetters('assistant', [
        'showAssistantInvalidPopup'
      ])
    },

    mounted () {
      setTimeout(() => {
        this._init(this.showAssistantInvalidPopup)
      }, 500)
    },

    destroyed () {
      channel.$off(events.POPUP_ASSISTANT_INVALID)
    },

    watch: {
      'showAssistantInvalidPopup': function (val) {
        this._init(val)
      }
    },

    methods: {
      _init (showAssistantInvalidPopup) {
        if (this.$refs.p && this.mode === 'auto') {
          if (showAssistantInvalidPopup && this.$refs.p.getState() !== 1) {
            this.$refs.p.show()
          }

          if (!showAssistantInvalidPopup && this.$refs.p.getState() === 1) {
            this.$refs.p.hide()
          }
        }

        if (this.$refs.p && this.mode === 'manual') {
          channel.$on(events.POPUP_ASSISTANT_INVALID, data => {
            if (data.showAssistantInvalidPopup && this.$refs.p.getState() !== 1) {
              this.$refs.p.show()
            }

            if (!data.showAssistantInvalidPopup && this.$refs.p.getState() === 1) {
              this.$refs.p.hide()
            }
          })
        }
      },

      hide (buttonIndex) {
        this.$refs.p.hide(buttonIndex)
        // 如果当前页面为试玩或专属任务列表和详情，则关闭弹层后返回到dashboard
        let routes = ['tasks', 'zstasks', 'zstask_detail']
        if (_.indexOf(routes, this.$route.name) !== -1) {
          setTimeout(() => {
            $router.back('/dashboard')
          }, 300)
        }
      },

      ...mapActions('assistant', [
        'download'
      ])
    }
  }
</script>



