/* eslint eqeqeq: "off", camelcase: "off" */

/* 任务助手相关弹层 */

// 任务助手未开启
const popup_open_assistant = {
  options: function (scheme) {
    return {
      effect: 'scale',
      cssClass: 'popup-open-assistant',
      showClose: true,
      closeCallback: function () {
        let router = window.$router
        if (router.currentRoute.name === 'tasks') {
          router.back('/dashboard')
        }
      },
      keep: true,
      buttons: [
        {
          text: '开启助手',
          theme: 'assertive',
          onClick: (buttonIndex) => {
            // console.log(buttonIndex)
            // 打开助手
            // location.href = scheme
            location.href = scheme + 'openURL?type=outside&url=' + encodeURIComponent(location.href)
          }
        }
      ]
    }
  },

  template: function (download_url) {
    return `
      <div class="icon icon-off"></div>
      <p class="title">任务助手未开启</p>
      <p class="tips">
        如遇到问题请 <a class="link" href="${download_url}">重新下载</a>
      </p>
    `
  }
}

// 任务助手有更新
const popup_upgrade_assistant = {
  options: {
    effect: 'scale',
    cssClass: 'popup-upgrade-assistant',
    showClose: true,
    buttons: [
      {text: '立即更新', theme: 'assertive'},
      {text: '打开助手'}
    ]
  },

  template: `
    <div class="icon-new icon-download"></div>
    <p class="title" style="font-size: 16px">您的助手有更新</p>
    <div class="sub-title">暂不更新</div>
  `
}

// 任务助手已失效
const popup_invalid_assistant = {
  options: {
    effect: 'scale',
    cssClass: 'popup-invalid-assistant',
    showClose: true,
    buttons: [
      {text: '下载新助手', theme: 'assertive'}
    ]
  },

  makeOptions: function (download_url) {
    return {
      effect: 'scale',
      cssClass: 'popup-invalid-assistant',
      // showClose: true,
      keep: true,
      buttons: [
        {
          text: '下载新助手',
          theme: 'assertive',
          onClick: (buttonIndex) => {
            // console.log(buttonIndex)
            // 下载助手
            location.href = download_url
          }
        }
      ]
    }
  },

  template: `
    <div class="icon-new icon-invalid"></div>
    <p class="title">您的助手已失效</p>
  `
}

// 任务助手未开启 lite版
const popup_open_assistant_lite = {

  template: function (download_url, type) {
    let desc = ''
    if (type == 'tasks') { desc = '开始试玩前需要激活助手' }
    if (type == 'apprentice') { desc = '需要激活助手才能进行分享' }
    return `
      <div class="icon icon-off"></div>
      <p class="title">${desc}</p>
      <p class="tips">
        如遇到问题请 <a class="link" href="${download_url}">重新下载并回本页面激活</a>
      </p>
    `
  }
}

// 任务助手已失效 lite版
const popup_invalid_assistant_lite = {
  // options 写在弹层应用的地方
  template: () => {
    return `
      <div class="icon icon-invalid"></div>
      <p class="title">您的助手已失效</p>
      <p class="title">下载新助手后请<span class="assertive">回本页面激活</span></p>
    `
  }
}

// 安装/打开 助手弹层
const popup_install_or_launch_assistant = {
  template: (app_icon, app_name) => {
    return `
      <img class="app-icon" src="${app_icon}" alt="">
      <p class="app-name">${app_name}</p>
      <p class="tips">
        这是您的任务助手，做任务期间必须<br>开启哦，<span class="assertive">下载后需通过本页面激活</span>
      </p>
    `
  }
}

module.exports = {
  popup_open_assistant,
  popup_upgrade_assistant,
  popup_invalid_assistant,
  popup_open_assistant_lite,
  popup_invalid_assistant_lite,
  popup_install_or_launch_assistant
}



