<template>
  <div class="page has-navbar" v-nav="{title: '芝麻信用认证', showBackButton: true, onBackButtonClick: back }">
    <div class="page-content">
      <div class="remind-icon text-center"><img src="//qianka.b0.upaiyun.com/images/664f4b1c03150cbd2c664ad39bea62f1.png" alt=""><span>认证失败</span></div>
      <div class="zhima-button text-center" @click="bindAgain()"><span class="text-center">重新认证</span></div>
      <div class="back text-center" @click="back()"><u>返回试玩列表</u></div>
    </div>
  </div>
</template>

<style lang='scss' scoped>
@import "../../scss/mixins";

  .page-content {
    .remind-icon {
      padding-top: 72px;

      img {
        width: 32px;
        height: 36px;
      }

      span {
        margin-top: 15px;
        display: block;
        @include font-regular;
        font-size: 16px;
        color: #484746;
      }
    }

    .zhima-button {
      margin-top: 40px;
      margin-left: 16px;
      width: 343px;
      height: 44px;
      background: #04C9B3;
      border-radius: 2px;

      span {
        @include font-regular;
        height: 44px;
        line-height: 44px;
        font-size: 16px;
        color: #FFFFFF;
      }
    }

    .back {
      margin-top: 16px;
      font-size: 12px;
      color: #04C9B3;
      letter-spacing: 0;
    }
  }
</style>

<script>
  import BindZhimaForm from './BindZhimaForm.vue'

  export default {
    data () {
      return {
        bindZhimaModal: undefined
      }
    },

    mounted () {
      $modal.fromComponent(BindZhimaForm, {
        title: '芝麻信用认证',
        theme: 'dark'
      }).then((modal) => {
        this.bindZhimaModal = modal
      })
    },

    destroyed () {
      if (this.bindZhimaModal) {
        $modal.destroy(this.bindZhimaModal)
      }
    },

    methods: {
      bindAgain () {
        this.bindZhimaModal.show()
      },
  
      back () {
        $router.back({ path: '/tasks' })
      }
    }
  }
</script>



