// 我
import User from './user/User'
// 账户设置
const UserProfile = r => require.ensure([], () => r(require('./user/UserProfile')), 'user-profile')
// 修改用户名
const UserNickname = r => require.ensure([], () => r(require('./user/UserNickname')), 'user-nickname')
// 我的二维码
const UserQrcode = r => require.ensure([], () => r(require('./user/UserQrcode')), 'user-qrcode')
// 绑定、修改手机
const UserBindMobile = r => require.ensure([], () => r(require('./user/UserBindMobile')), 'user-bindmobile')
// 绑定微信
const UserBindWeixin = r => require.ensure([], () => r(require('./user/UserBindWeixin')), 'user-bindweixin')
// 解绑微信
const UserUnbindWeixin = r => require.ensure([], () => r(require('./user/UserUnbindWeixin')), 'user-unbindweixin')
// 账户明细
const UserAccountDetail = r => require.ensure([], () => r(require('./user/UserAccountDetail')), 'user-accountdetail')
// 提现列表
const WithdrawList = r => require.ensure([], () => r(require('./withdraw/WithdrawList')), 'withdraw-list')
// 手机提现
const WithdrawPhone = r => require.ensure([], () => r(require('./withdraw/WithdrawPhone')), 'withdraw-phone')
// 微信提现
const WithdrawWeixin = r => require.ensure([], () => r(require('./withdraw/WithdrawWeixin')), 'withdraw-weixin')
// 支付宝提现
const WithdrawAlipay = r => require.ensure([], () => r(require('./withdraw/WithdrawAlipay')), 'withdraw-alipay')
// 集分宝提现
const WithdrawRebateScore = r => require.ensure([], () => r(require('./withdraw/WithdrawRebateScore')), 'withdraw-rebatescore')
// 提现结果页
const WithdrawResult = r => require.ensure([], () => r(require('./withdraw/WithdrawResult')), 'withdraw-result')
// 提现进度页
const WithdrawProcess = r => require.ensure([], () => r(require('./withdraw/WithdrawProcess')), 'withdraw-process')
// 关于钱咖
const AboutHome = r => require.ensure([], () => r(require('./about/Home')), 'about-home')
// 帮助中心
const Help = r => require.ensure([], () => r(require('./help/Help')), 'help')
// 自助服务
const SelfHelp = r => require.ensure([], () => r(require('./help/SelfHelp')), 'selfhelp')
// 试玩任务、高额任务申诉
const ComplainSubtask = r => require.ensure([], () => r(require('./help/ComplainSubtask')), 'complain-subtask')
const ComplainHitask = r => require.ensure([], () => r(require('./help/ComplainHitask')), 'complain-hitask')

const WithDrawArrivedExample = r => require.ensure([], () => r(require('./withdraw/Example')), 'withdraw-example')
const Entrance = r => require.ensure([], () => r(require('./entrance/Index')), 'entrance')
// 申诉解冻
const ComplainUnfreeze = r => require.ensure([], () => r(require('./help/ComplainUnfreeze')), 'complain-unfreeeze')
const UnfreezeForm = r => require.ensure([], () => r(require('./help/UnfreezeForm')), 'unfreeze-form')

export default [
  { path: '/user/', name: 'user', component: User },
  { path: '/user/profile', name: 'user_profile', component: UserProfile },
  { path: '/user/nickname', name: 'user_nickname', component: UserNickname },
  { path: '/user/qrcode', name: 'user_qrcode', component: UserQrcode },
  { path: '/user/bindMobile/:status', name: 'user_bindMobile', component: UserBindMobile },
  { path: '/user/bindWeixin', name: 'user_bindWeixin', component: UserBindWeixin },
  { path: '/user/unbindWeixin', name: 'user_unbindWeixin', component: UserUnbindWeixin },
  { path: '/user/accountDetail', name: 'user_accountDetail', component: UserAccountDetail },
  { path: '/withdraw/', name: 'withdraw', component: WithdrawList },
  { path: '/withdraw/rebatescore', name: 'withdraw_rebate_score', component: WithdrawRebateScore },
  { path: '/withdraw/phone', name: 'withdraw_phone', component: WithdrawPhone },
  { path: '/withdraw/weixin', name: 'withdraw_weixin', component: WithdrawWeixin },
  { path: '/withdraw/alipay', name: 'withdraw_alipay', component: WithdrawAlipay },
  { path: '/withdraw/result', name: 'withdraw_result', component: WithdrawResult },
  { path: '/withdraw/result/:backroute', name: 'withdraw_result_backroute', component: WithdrawResult },
  { path: '/withdraw/process/:type', name: 'withdraw_process', component: WithdrawProcess },
  { path: '/withdraw/arrived-example', name: 'withdraw_arrived_example', component: WithDrawArrivedExample },
  { path: '/entrance', name: 'qianka_entrance', component: Entrance },
  { path: '/about/', name: 'about', component: AboutHome },
  { path: '/help/', name: 'help', component: Help },
  { path: '/help/self', name: 'help_self', component: SelfHelp },
  { path: '/help/complainSubtask', name: 'help_complainSubtask', component: ComplainSubtask },
  { path: '/help/complainHitask', name: 'help_complainHitask', component: ComplainHitask },
  { path: '/help/ComplainUnfreeze', name: 'help_complainUnfreeze', component: ComplainUnfreeze },
  { path: '/help/UnfreezeForm', name: 'unfreeze_form', component: UnfreezeForm }
]



