import {popup_bind_zhima} from '../popups/popups'
import BindZhimaForm from '../../zhima/BindZhimaForm.vue'

export default {
  components: {
    BindZhimaForm
  },

  data () {
    return {
      bindZhimaModal: undefined
    }
  },

  destroyed () {
    if (this.bindZhimaModal) {
      $modal.destroy(this.bindZhimaModal)
    }
  },

  methods: {
    showBindZhimaPopup () {
      let popup = $popup.fromTemplate(
        popup_bind_zhima.template,
        popup_bind_zhima.options
      )
      popup.show().then((buttonIndex) => {
        if (buttonIndex === 0) {
          if (this.bindZhimaModal) {
            this.bindZhimaModal.show()
            return
          }

          $modal.fromComponent(BindZhimaForm, {
            title: '芝麻信用认证',
            theme: 'dark'
          }).then((modal) => {
            this.bindZhimaModal = modal
            this.bindZhimaModal.show()
          })
        }
      })
    }
  }
}



