const TASK_MATCHED_TERM = 6 * 3600 * 1000

const timeout = (duration) => {
  return new Promise((resolve) => {
    setTimeout(resolve, duration || 0)
  })
}

const initState = () => ({
  show_push: 0,
  is_jail_broken: 0,

  ongoing: [], // 进行中的任务

  newbie: [], // 新人专享

  tasks: [], // 标准任务
  tasks_coming: [], // 标准任务预告
  task_limited: 0, // 标准任务限量标记
  has_tasks_coming: true,
  has_tasks: true,

  recommends: [], // 推荐任务

  easy: [], // 简易任务
  easy_coming: [], // 简易任务预告
  easy_limited: 0, // 简易任务限量标记

  in_compare: { // 比对中任务数
    tasks: 0,
    newbie: 0,
    coming: 0
  },

  // game_banner: {
  //   banner_url: '',
  //   game_url: ''
  // },
  game_banner: [],
  game_cards: [],

  zstask_info: { // 专属任务信息
    total: 0, // 任务数
    total_reward: '0.00',
    icons: []
  },

  promotions: [], // 咖购商品推荐
  is_first_click: true, // 第一次点击推荐商品标识
  kagou_link: '', // 咖购app下载地址
  weishang_url: '', // 有练跳转地址

  showInstallKagouPopup: false
})

const state = initState()

const LIST_TYPE_NORMAL = 'normal' // 正常任务列表
const LIST_TYPE_NEWBIE = 'newbie' // 新人列表

// 争抢任务状态
const TASK_START_QUEUE = 1 // 排队中
const TASK_START_ONGOING = 2 // 进行中
const TASK_START_FAIL = 3 // 错误

const types = {
  INIT: 'init',
  SYNC: 'sync',
  CHANGE_KAGOU_FIRST_CLICK_STATUS: 'change_kagou_first_click_status',
  SHOW_INSTALL_KAGOU_POPUP: 'show_install_kagou_popup',
  HIDE_INSTALL_KAGOU_POPUP: 'hide_install_kagou_popup'
}

// utils
const getOngoingTaskByType = (tasks, type) => {
  let ongoingTask = _.find(tasks, t => t.status === 2)
  if (ongoingTask) return [_.extend(ongoingTask, {type})]
  return []
}

const mutations = {
  [types.INIT] (state) {
    _.extend(state, initState())
  },

  [types.SYNC] (state, payload) {
    switch (payload.type) {
      case LIST_TYPE_NORMAL:
        state.show_push = payload.show_push
        state.is_jail_broken = payload.is_jail_broken

        if (state.is_jail_broken) return

        // 新人专享列表
        state.newbie = _.filter(payload.newbie, (t) => {
          return t.status !== 2
        })

        // 标准任务列表
        let tasks = _.filter(payload.tasks, (t) => {
          return t.status !== 2
        })
        // 有练入口
        state.weishang_url = payload.weishang_url

        state.tasks = tasks
        if (state.tasks.length === 0) {
          state.has_tasks = false
        }

        // 简易任务列表
        let easy = _.filter(payload.easy, (t) => {
          return t.status !== 2
        })

        _.each(easy, t => {
          t.tags.push(`剩${t.qty}份`)
        })

        state.easy = easy

        // 进行中的任务
        state.ongoing = []
          .concat(getOngoingTaskByType(payload.newbie, 1))
          .concat(getOngoingTaskByType(payload.easy, 2))
          .concat(getOngoingTaskByType(payload.tasks, 1))

        // 标准任务预告
        state.tasks_coming = payload.incoming
        if (state.tasks_coming.length === 0) {
          state.has_tasks_coming = false
        }

        // 简易任务预告
        let easy_coming = payload.easy_incoming

        _.each(easy_coming, t => {
          t.tags.push(`剩${t.qty}份`)
        })

        state.easy_coming = easy_coming

        // 匹配中的任务数
        state.in_compare = payload.in_compare

        // 专属任务信息
        _.extend(state.zstask_info, _.pick(payload.zs_task_info, _.keys(state.zstask_info)))

        // 游戏板块
        // let banner = _.find(payload.games, function (t) { return t.banner_url !== '' })
        // _.extend(state.game_banner, _.pick(banner, _.keys(state.game_banner)))
        state.game_banner = _.filter(payload.games, t => t.banner_url !== '')
        state.game_cards = _.filter(payload.games, t => t.banner_url === '')
        if (state.game_banner.length > 5) {
          state.game_banner = state.game_banner.slice(0, 4)
        }

        // 咖购商品推挤
        let promotions = payload.promotions
        _.each(promotions, t => {
          t.title = t.title.replace(/【/g, '').replace(/】/g, ' · ')
        })
        state.promotions = promotions

        state.kagou_link = payload.kagou_link

        // 推荐模块任务
        if (payload.show_recommends) {
          state.recommends.push(shoutuPortal, youlianPortal)
        }
        break
      case LIST_TYPE_NEWBIE:
        // 新人列表标准任务
        state.tasks = payload.tasks.splice(0, 8)
        // 新人列表标准任务预告
        if (payload.incoming.length > 8) {
          state.tasks_coming = payload.incoming.splice(0, 8)
        } else {
          state.tasks_coming = payload.incoming
        }
        break
    }
  },

  [types.CHANGE_KAGOU_FIRST_CLICK_STATUS] (state, payload) {
    state.is_first_click = payload
  },

  [types.SHOW_INSTALL_KAGOU_POPUP] (state) {
    state.showInstallKagouPopup = true
  },

  [types.HIDE_INSTALL_KAGOU_POPUP] (state) {
    state.showInstallKagouPopup = false
  }
}

const _getTips = (count) => {
  return count > 0
    ? `还有${count}个任务正在匹配，请稍后刷新...`
    : ''
}

const getters = {
  newbieTips (state) {
    return _getTips(state.in_compare.newbie)
  },

  tasksTips (state) {
    return _getTips(state.in_compare.tasks)
  },

  showGameBanner (state) {
    return state.game_banner.length > 0
  }
}

const actions = {
  fetch ({ commit }) {
    commit(types.INIT)

    // 先判断是否需要重新匹配任务
    let now = (new Date()).getTime()
    let task_matched = $storage.get(KEY_TASK_MATCHED)

    // 匹配任务
    function subtaskAfterTaskMatched () {
      $loading.show('正在匹配任务')

      const handleError = () => {
        timeout(3000).then(() => {
          $toast.show('网络错误，请刷新页面')
        })
      }

      // 需要调用 $api.subtaskInit() 和 $assistant.updateState() 并且两个接口调用都成功后才可以绑定用户信息
      return $api.subtaskInit()
        .then(() => {
          // 2秒后再次调用列表页接口，查看是否有可做的任务
          return $assistant.updateState().then(() => { return timeout(2000) })
        }, handleError)
        .then(() => {
          return $api.subtaskList(true)
            .then(res => res.data && res.data.payload)
            .then(payload => { // 以2秒后的第二次请求返回数据为依据来更新UI、操作localStorage
              let now = (new Date()).getTime()
              $storage.set(KEY_TASK_MATCHED, now + TASK_MATCHED_TERM)
              $loading.hide()
              return payload
            }, handleError)
        }, handleError)
    }

    // 匹配任务未成功 或 匹配任务成功过但已过期，则需要重新匹配
    let getPayload = !task_matched || (task_matched && task_matched < now)
      ? subtaskAfterTaskMatched()
      : $api.subtaskList().then(res => res.data && res.data.payload) // 匹配任务成功且未过期

    getPayload
      .then(payload => {
        function do_commit (payload) {
          commit(types.SYNC, _.extend(payload, {type: LIST_TYPE_NORMAL}))
        }

        if (payload.show_push) {
          $assistant.isPushEnabled()
            .then(res => {
              let enabled = res && res.data && res.data.payload === '1'
              payload.show_push = !enabled
              do_commit(payload)
            })
            .catch(e => console.log(e))
        } else {
          do_commit(payload)
        }
      })
  },

  fetchNewbie ({ commit }) {
    return $api.subtaskListNewbie()
      .then(res => res.data && res.data.payload)
      .then(payload => {
        commit(types.SYNC, _.extend(payload, {type: LIST_TYPE_NEWBIE}))
      })
  },

  // 抢任务
  start ({ commit }, t) {
    let task_id = t.id

    // 任务是否争抢中
    let starting = false
    // quality 加速标记
    let speed = t.is_quality ? 1 : 0

    function _startTask (task_id, speed) {
      if (!starting) {
        $loading.show('拼命争抢任务中...')
        starting = true
      }

      $api.subtaskStart(task_id, speed)
        .then(res => res.data)
        .then(data => new Promise((resolve) => {
          setTimeout(() => { resolve(data) }, 1000)
        }))
        .then((data) => {
          if (data.err_code === ERR_CERTIFICATION) { // 需要安装证书
            $loading.hide()
            commit('show_install_certification_popup', null, {root: true})
          } else if (data.err_code === ERR_JAILBREAKER) { // 越狱用户
            $loading.hide()
            channel.$emit(events.POPUP_JAIL_BREAKER)
          } else if (data.err_code === ERR_UNBINDPHONE) { // 绑定手机
            $loading.hide()
            channel.$emit(events.POPUP_BIND_PHONE)
          } else if (data.err_code === ERR_ZHIMA) { // 需要绑定芝麻信用
            $loading.hide()
            channel.$emit(events.POPUP_BIND_ZHIMA)
          } else if (data.err_code === ERR_ACCOUNTFROZEN) { // 用户已被冻结
            $loading.hide()
            channel.$emit(events.POPUP_USER_FREEZE)
          } else {
            let { type, delay, message } = data.payload // eslint-disable-line no-unused-vars

            if (type === TASK_START_QUEUE) { // 排队中
              _startTask(task_id, speed)
            } else if (type === TASK_START_ONGOING) { // 抢到任务
              /* 统计抢到任务 */
              crius.sendAction('EVENT', 'subtask_started', {
                task_id: task_id
              })
              /* 统计抢到任务 - END */

              setTimeout(() => {
                $toast.show('抢到任务，正在跳转...', 1000)

                setTimeout(() => {
                  $router.forward({
                    name: 'task_detail',
                    params: {
                      id: task_id
                    }
                  })
                }, 1000)
              }, 500)
            } else { // type == 3, 错误提示
              $toast.show(message)
            }
          }
        })
    }

    _startTask(task_id, speed)
  },

  // 咖购安装弹层
  forwardPromotions ({commit}, task) {
    // 如果是返利入口，则直接去返利页面
    if (task.isPortal) {
      task.onPortalClick()
      return
    }

    let url = task.url

    $assistant
      .getAppInfo(KAGOU_BUNDLE_ID)
      .then(res => res && res.data && res.data.payload)
      .then(payload => {
        if (payload && payload.bundle_id && payload.bundle_version) {
          if (state.is_first_click === true) {
            commit(types.CHANGE_KAGOU_FIRST_CLICK_STATUS, false)
            location.href = url
          } else {
            let params = getUrlParams(url)
            let exparams = JSON.parse(decodeURIComponent(params.exparams))
            location.href = 'http://sns.kagou.me/kagou-guid/views/jump.html?plan_id=' + exparams.plan_id
          }
        } else {
          commit(types.SHOW_INSTALL_KAGOU_POPUP)
        }
      }, () => {
        commit(types.SHOW_INSTALL_KAGOU_POPUP)
      })
  },

  hideKagou ({commit}) {
    commit(types.HIDE_INSTALL_KAGOU_POPUP)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



