/* eslint eqeqeq: "off", camelcase: "off" */

// 使用证书登录弹层
const login_with_certification = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '取消'},
      {text: '证书登录'}
    ]
  },
  template: `
    <div class="desc no-title">如安装过苹果证书，可通过证书登录</div>
  `
}

// 使用证书登录失败弹层
const invalid_certification = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: `
    <div class="title">登录失败</div>
    <div class="desc">
      <div>证书未关联到您的账号<br>请使用手机号进行登录</div>
    </div>
  `
}

const get_code_fail = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    title: '',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: `
    <div class="title">获取验证码失败</div>
    <div class="desc">该手机号尚未绑定过钱咖账号</div>
  `
}

// 验证码类型提示
const popup_choose_verify_code_type = {
  options: {
    effect: 'scale',
    cssClass: 'popup-general',
    buttons: [
      { text: '短信验证码' },
      { text: '语音验证码' }
    ],
    showClose: true
  },
  template: `
    <div class="title">没收到验证码？</div>
    <div class="desc">
      您可以选择获取语音验证码
    </div>
  `
}

module.exports = {
  login_with_certification,
  invalid_certification,
  get_code_fail,
  popup_choose_verify_code_type
}



