<template>
  <transition name="fade-webclip">
    <div class="webclip" v-if="show">
      <div class="qk-icon"></div>
      <div class="title">钱咖永久访问入口</div>
      <div class="desc">再也不担心找不到钱咖</div>
      <button class="button button-assertive button-small btn-common btn-install" @click="onClick()">立即安装</button>
      <button class="button button-assertive button-small btn-common btn-maybe_later" @click="maybeLater()">以后再说</button>
    </div>
  </transition>
</template>
<style lang="scss" scoped>
  @import "../scss/mixins";

  .fade-webclip-enter, .fade-webclip-leave-to, .fade-webclip-leave {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
    -webkit-transform: translate3d(0, 100%, 0);
  }

  .fade-webclip-enter-active, .fade-webclip-leave-active {
    transition: all 0.3s ease;
  }

  .webclip {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 60px;
    background: rgba(0, 0, 0, 0.8);
    color: #FFF;
    width: 100%;
    padding: 10px 16px 10px 68px;
    z-index: 20;

    .qk-icon {
      width: 40px;
      height: 40px;
      border-radius: 4px;

      @include background-image(url(//qianka.b0.upaiyun.com/images/4ee0c9242575135ba0c1613e492ce0a6.png));

      position: absolute;
      top: 10px;
      left: 16px;
    }

    .title {
      font-size: 14px;
      line-height: 20px;
      margin-left: -3px;
    }

    .desc {
      font-size: 11px;
      line-height: 16px;
      opacity: 0.5;
      margin-left: -3px;

      @include font-regular;
    }

    .btn-common {
      position: absolute;
      padding: 0;
      width: 64px;
      height: 28px;
      min-height: 28px;
      line-height: 28px;
      border-radius: 2px;
    }

    .btn-install {
      top: 16px;
      right: 88px;
      background: #ea5a49;
    }

    .btn-maybe_later {
      top: 16px;
      right: 16px;
      border: solid 1px #ea5a49;
      color: #ea5a49;
      background-color: rgba(0, 0, 0, 0);
    }
  }
</style>
<script>
  import {WEBCLIP_MAYBE_LATER_CLICK_NUM} from 'constants'
  export default {
    created () {
      $bus.$on('showWebclip', (data) => {
        this.url = data.url
        setTimeout(() => {
          this.show = true
        }, 500)
      })

      $bus.$on('hideWebclip', () => {
        this.show = false
      })
    },

    data () {
      return {
        url: '',
        show: false
      }
    },

    methods: {
      onClick () {
        console.log('install webclip!')
        location.href = this.url
        this.show = false
      },
      maybeLater () {
        // 存储点击次数
        let key = `${WEBCLIP_MAYBE_LATER_CLICK_NUM}`
        let num = $storage.get(key) || 0
        ++num
        console.log(key, num)
        $storage.set(key, num)
        // 用来控制当日不再显示
        let day = new Date().getDate()
        $cookie.set(key, day)
        this.show = false
      }
    }
  }
</script>



