<template>
  <div class="unsupported-browser" v-if="state === 1">
    <div style="background-color: #fff;" v-if="isIOS11()">
      <img class="auto" src="//assets.qkcdn.com/images/a70e88bc5d36bad29b379458dad37622.jpeg" />
      <img class="auto" src="//assets.qkcdn.com/images/3470c6376268d9183482054a89612602.jpeg" />
      <img style="margin-top: -6px;" class="auto"
           src="//assets.qkcdn.com/images/45ff87ce19d9bdd344a78163791ae2d5.jpeg"/>
    </div>
    <img v-else class="guide-image"
         src="//qianka.b0.upaiyun.com/images/418e0fbdf138ce667435c3bdc083cd93.png"/>
  </div>
</template>
<style lang="scss" scoped>
  @import '../scss/variables';

  .unsupported-browser {
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 100%;
    position: absolute;
    color: white;
    z-index: $incognito-mode-z-index;
    top: 0;
    left: 0;

    .guide-image {
      position: absolute;
      bottom: 0px;
      right: 0px;
      width: 100%;
      height: auto;
    }

    .auto {
      width: 100%;
      height: auto;
    }

    .guide-text {
      font-size: 14px;
      position: absolute;
      bottom: 20px;
      right: 15px;
      text-align: left;
      padding: 20px 45px;
      background-image: url(//qianka.b0.upaiyun.com/images/346b8f6eaab92648bfc351ea7deea5b0.png);
      background-size: 24px 60px;
      background-position: right 20px;
      background-repeat: no-repeat;

      li + li {
        margin-top: 10px;
      }
    }

    .message-text {
      font-size: 18px;
      position: absolute;
      top: 20%;
      width: 100%;
      text-align: center;
    }
  }
</style>
<script>
  import {is_dnt, is_ios_11} from 'utils'

  export default {
    created () {
      if (is_ios_11()) {
        this.state = is_dnt()
      } else {
        try {
          // try to use localStorage
          localStorage.test = 1
        } catch (e) {
          this.state = 1
        }
      }
    },

    data () {
      return {
        state: 0 // 0: hide, 1: show
      }
    },

    methods: {
      getState () {
        return this.state
      },

      isIOS11 () {
        return is_ios_11()
      }
    }
  }
</script>



