import BindZhima from './BindZhima'
import BindSucess from './BindSuccess'
import BindFail from './BindFail'

export default [
  { path: '/zhima/bindZhima', name: 'bind_zhima', component: BindZhima },
  { path: '/zhima/bindSuccess', name: 'bind_zhima', component: BindSucess },
  { path: '/zhima/bindFail', name: 'bind_zhima', component: BindFail }
]



