// 问卷调查
// parameters
/**
 * parameters {obj}
 * job  从事职业
 * expenditure  消费占支出的百分比
 * borrow_type   借钱方式
 * iphone_num   拥有的苹果设备数
 * is_shell     是否有旧的苹果设备需要出售
 * recycled_idea  对手机回收平台的看法
 * **/
_api.survery = (parameters) => {
  return api.post('/s4/question.addQuestion', parameters)
}
// 应用中心接口 详情页 /s4/install.appDetail
_api.getAppDetail = (app_id) => {
  return api.get('/s4/install.appDetail', {
    params: { app_id },
    keep_cache: 0
  })
}
// 应用中心接口  首页  /s4/install.index
_api.getAppIndex = () => {
  return api.get('/s4/install.index')
}
// 应用中心接口   /s4/install.categoryList
_api.getAppCategoryList = () => {
  return api.get('/s4/install.categoryList')
}

_api.borrow = (form) => {
  return api.post('/s4/apply', form)
}

// 是否借过钱
_api.hasBorrow = () => {
  return api.get('/s4/everBorrowed', {
    keep_cache: 0,
    params: {
      t: new Date().getTime()
    }
  })
}

// 借钱验证码
_api.borrowSendCode = (form) => {
  return api.post('/s4/borrow.sendCode', form)
}

/* /Lite 版助手 相关接口 */

// 简易任务 - 开始任务
_api.simpletaskStart = (task_id) => {
  return api.get('/s4k/simpletask.start', {
    keep_cache: 0,
    params: { task_id }
  })
}

// 简易任务 - 详情
_api.simpletaskDetail = (task_id) => {
  return api.get('/s4/simpletask.detail', {
    keep_cache: 0,
    params: { task_id }
  })
}

// 简易任务 - 领取奖励
_api.simpletaskState = (task_id) => {
  return api.get('/s4k/simpletask.state', {
    keep_cache: 0,
    params: { task_id }
  })
}

// 简易任务 - 放弃任务
_api.simpletaskGiveup = (task_id) => {
  return api.get('/s4/simpletask.giveup', {
    keep_cache: 0,
    params: { task_id }
  })
}

// 闪电借钱 - 借贷平台列表
_api.loanList = (amount, time, tag) => {
  return api.get(`/s4/loan.list?filter_amount=${amount}&filter_time=${time}&filter_tag=${tag}`)
}

// 闪电借钱 - 借钱详情页面
_api.getLoanDetail = (id) => {
  return api.get(`/s4/loan.detail?id=${id}`, {
    keep_cache: 500
  })
}

_.extend(api, _api)

export default api



