const types = {
  SYNC: 'sync',
  SYNC_TASKS: 'syncTasks',
  SYNC_ACCOUNT_STATUS: 'sync_account_status'
}
const initState = () => {
  return {
    info: {},
    tasks: [],
    account_status: 1
  }
}

const state = initState()

const getters = {
}

const mutations = {
  [types.SYNC] (state, payload) {
    Object.assign(state, payload)
  },

  [types.SYNC_TASKS] (state, payload) {
    Object.assign(state, payload)
  },

  [types.SYNC_ACCOUNT_STATUS] (state, payload) {
    state.account_status = payload.account_status
  }
}

const actions = {
  getTaskByDate ({commit}, payload) {
    return $api.highearnDateFilterTask({task_date: payload})
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        console.log('actions:getTaskByDate:response:payload ', payload)
        commit(types.SYNC_TASKS, payload)
        return payload
      })
  },

  getSubTaskByDate ({commit}, taskDate) {
    return $api.complainSubtaskDateFilter({
      task_date: taskDate
    }).then(res => res.data && res.data.payload)
      .then((payload) => {
        return payload
      })
  },

  submitRegisterTaskInfo ({commit}, payload) {
    return $api.highearnComplain(payload).then(function (resp) {
      $toast.show('提交成功！')
      $router.back({
        path: '/help/self'
      })
    })
  },

  submitSubtaskInfo ({commit}, payload) {
    return $api.complainSubtask(payload)
  },

  upload ({commit}, file) {
    return $upload(file).then(data => data && data.payload)
  },

  getAccountStatus ({commit}) {
    $api.accountStatus()
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC_ACCOUNT_STATUS, payload)
      })
  },

  unfreezeSendCode ({commit}, form) {
    return $api.unfreezeSendCode(form)
  },

  unfreezeComplain ({commit}, form) {
    return $api.unfreezeComplain(form)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



