<template>
  <ActionList v-if="easy.length > 0" title="简易任务">
    <Action v-for="task in easy" :key="task.task_id" @click.native="startTask(task)">
      <div class="icon" slot="icon">
        <img :src="task.icon">
      </div>

      <span slot="app-name" v-text="task.title"></span>
      <span slot="desc" v-for="(tag, index) in task.tags" :key="index" class="tag-box">
        <span v-if="typeof tag == 'string'" v-text="tag" class="tag"></span>
        <span v-if="typeof tag == 'object' && tag.text" v-text="' ‧ ' + tag.text" class="tag"
          :class="{'assertive': tag.theme == 'assertive'}"></span>
        <span v-if="typeof tag == 'object' && tag.countdown * 1000 > getNowTime()" class="tag time-tag"
          :class="{'assertive': tag.theme == 'assertive'}">
          <count-down :deadline="tag.countdown * 1000"></count-down>
        </span>
      </span>
      <div slot="reward-wrap" class="reward-content">
        <span class="plus">+</span>
        <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
        <span class="yuan">元</span>
      </div>
    </Action>
  </ActionList>
</template>
<style lang="scss" scoped>
</style>
<script>
  import {mapState, mapActions} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  // import {
  //   popup_has_task_ongoing
  // } from '../popups/popups'

  export default {
    components: {
      Action,
      ActionList
    },

    computed: {
      ...mapState('task', [
        'ongoing',
        'easy'
      ])
    },

    methods: {
      startTask (t) {
        // let ongoingEasyTask = _.find(this.ongoing, t => {
        //   return t.type === 2
        // })

        // if (ongoingEasyTask && ongoingEasyTask.id !== t.id) {
        //   $popup.fromTemplate(
        //     popup_has_task_ongoing.template('简易'),
        //     popup_has_task_ongoing.options
        //   )
        //     .show()
        //     .then((buttonIndex) => {
        //       if (buttonIndex === 1) {
        //         this.getTaskDetail(t.id)
        //       }
        //     })
        // } else {
        //   this.getTaskDetail(t.id)
        // }
        this.getTaskDetail(t.id)
      },

      getTaskDetail (task_id) {
        $loading.show('排队中, 请勿刷新页面')
        let self = this

        function _start (task_id) {
          self.start(task_id).then((payload) => {
            switch (payload.type) {
              case 1:
                // 排队中
                setTimeout(() => {
                  _start(task_id)
                }, payload.delay || 1000)
                break
              case 2:
                $loading.hide()
                $router.forward(`/easy/detail/${task_id}`)
                break
              case 3:
                $toast.show(payload.message || '出错了')
            }
          })
        }

        _start(task_id)
      },

      ...mapActions('easy', [
        'start'
      ]),

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      })
    }
  }
</script>



