<template>
  <div class="page has-navbar">
    <div class="page-content">
      <div class="item item-borderless instruction">
        由于iOS9以上系统限制，安装“任务助手”后，首次打开会提示“未受信任的企业级开发者”。为正常使用，请按如下流程操作(设置-通用-描述文件与设备管理-信任)，即可解决哦！
      </div>

      <div class="item item-borderless step">
        <p>1.在[设置]－[通用]－[描述文件与设备管理]<br>找到相对应的证书</p>
        <div class="img img-1"></div>
      </div>
      <div class="item item-borderless step">
        <p>2.点击信任</p>
        <div class="img img-2"></div>
      </div>
      <div class="item item-borderless step">
        <p>3.回到桌面打开助手</p>
        <div class="img img-3"></div>
      </div>
      <div class="item item-borderless step">
        <p>4.打开后请务必允许使用数据</p>
        <div class="img img-4"></div>
      </div>
      <div class="button button-trust" @click="trust()">去信任</div>
      <div style="height: 50px;"></div>

    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import '../scss/mixins.scss';

  .page-content {
    background: #FFFFFF;

    .item.item-borderless {
      text-overflow: auto;
      white-space: normal;

      &.instruction {
        padding: 19px;
        font-size: 13px;
        line-height: 22px;
        text-align: justify;
        color: #8c8c8c;
      }

      &.step {
        padding: 20px 21px;

        p {
          font-size: 14px;
          line-height: 20px;
          color: #EA5A49;
          margin: 0 0 10px 0;
        }

        div.img {
          width: 100%;

          &.img-1 {
            height: 156px;

            @include background-image(url(//qianka.b0.upaiyun.com/images/f933be91dd897d5699ccafd15c6df0b1.png), 213px 106px, center);
          }

          &.img-2 {
            height: 139px;

            @include background-image(url(//qianka.b0.upaiyun.com/images/35bfecb808c5da5614699ac2fabb1766.png), 225px 139px, center);
          }

          &.img-3 {
            height: 108px;

            @include background-image(url(//qianka.b0.upaiyun.com/images/508e298702d25b2fcc26d64655f1c018.png), 170px 108px, center);
          }

          &.img-4 {
            height: 108px;

            @include background-image(url(//qianka.b0.upaiyun.com/images/cdeae50b01ed25d8f0f94bd3f223eb31.png), 170px 108px, center);
          }
        }
      }
    }

    .button-trust {
      margin: 30px auto;
      display: block;
      width: 160px;
      height: 40px;
      min-height: 40px;
      background: #EF4F2A;
      border-radius: 2px;
      font-size: 13px;
      color: #FFFFFF;

      @include font-medium();
    }
  }

  .bar-dark {
    background-color: rgba(#2D2C33, 0.98) !important;
    color: #FFF;

    .title {
      font-weight: 100;
    }
  }
</style>
<script>
  import {
    KEY_ASSISTANT_GUIDE,
    IOS_PROFILE_TRUST
  } from 'constants'

  export default {
    methods: {
      trust () {
        let guide = $storage.get(KEY_ASSISTANT_GUIDE)
        if (guide) {
          guide.showed = 1
          $storage.set(KEY_ASSISTANT_GUIDE, guide)
        }

        location.href = IOS_PROFILE_TRUST
      }
    }
  }
</script>



