<script>
  import bindZhimaMixin from '@/task/views/bindZhimaMixin'
  import {mapState} from 'vuex'
  import {channel, events} from 'store/channel'

  export default {
    computed: {
      ...mapState([
        'zhima_status' // 0 不触发验证流程 1 触发验证流程
      ])
    },

    mixins: [bindZhimaMixin],

    mounted () {
      channel.$on(events.POPUP_BIND_ZHIMA, () => {
        this.showBindZhimaPopup()
      })
    }
  }
</script>



