<template>
  <div class="jail-broken" v-if="is_jail_broken">
    <div class="tips">
      您的设备已越狱或安装了第三方应用商店软件、越狱软件，请恢复正版或删除该类应用。如需帮助请 <span class="assertive" @click="customService()">联系客服</span>
    </div>
  </div>
</template>

<style lang='scss' scoped>
  @import "~scss/mixins";

  .jail-broken {
    padding: 315px 56px 0 56px;

    @include background-image(url('//qianka.b0.upaiyun.com/images/8c2da759f453ea71091af703295e06b7.png'), 255px 255px, 60px 50px);

    .tips {
      width: 263px;
      font-size: 14px;
      line-height: 24px;
      color: #3A3A3A;
      text-align: center;

      @include font-regular();

      .assertive {
        text-decoration: underline;
      }
    }
  }
</style>

<script>
  import {mapState} from 'vuex'
  import {getCustomerServiceLink} from '../../../utils'

  export default {
    computed: {
      ...mapState('task', [
        'is_jail_broken'
      ])
    },

    methods: {
      customService () {
        location.href = getCustomerServiceLink()
      }
    }
  }
</script>



