<template>
  <ActionList v-if="showActionList" :title="title">
    <Action v-for="(task, index) in coming" :key="index" @click.native="onComingTaskClick">
      <div slot="icon" class="icon" :class="{'border-blue': taskType === 'easy', 'border-green': taskType !== 'easy'}">
        <div class="icon-no-img"
          :class="{'easy': taskType === 'easy'}">
          <div v-text="task.start_date"></div>
        </div>
      </div>

      <span slot="app-name" v-text="task.title"></span>

      <Tags slot="desc" :tags="task.tags" />

      <div slot="reward-wrap" class="reward-content">
        <span class="plus">+</span>
        <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
        <span class="yuan">元</span>
      </div>
    </Action>

    <!-- 标准任务 暂无样式 -->
    <Action v-if="taskType === 'standard' && !has_tasks_coming">
      <div class="icon" slot="icon">
        <img src="//qianka.b0.upaiyun.com/images/809c958dfd2f7f6f78da97f40322423a.png">
      </div>
      <span slot="app-name">暂无任务预告</span>
      <span slot="desc">过会儿再来看看吧</span>
    </Action>
  </ActionList>
</template>
<script>
  import {mapState, mapGetters, mapActions} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  import Tags from '../components/actions/Tags.vue'
  import downloadAssistantPopupMixin from './downloadAssistantPopupMixin'

  export default {
    props: {
      taskType: {
        type: String,
        default: 'standard'
      }
    },

    components: {
      Action,
      ActionList,
      Tags
    },

    computed: {
      'title': function () {
        return this.taskType === 'easy'
          ? '简易任务预告'
          : '标准任务预告'
      },

      ...mapState('task', [
        'tasks_coming',
        'easy_coming',
        'has_tasks_coming'
      ]),

      'coming': function () {
        return this.taskType === 'easy'
          ? this.easy_coming
          : this.tasks_coming
      },

      ...mapGetters('assistant', [
        'isNewbie'
      ]),

      'showActionList': function () {
        return this.taskType === 'easy'
          ? this.coming.length > 0
          : this.coming.length > 0 || !this.has_tasks_coming
      }
    },

    mixins: [downloadAssistantPopupMixin],

    methods: {
      onComingTaskClick () {
        const showPopup = () => {
          // $popup.fromTemplate(
          //   `<div class="text-center">大咖别急，该任务还没开始哦~</div>`,
          //   {
          //     buttons: [
          //       {text: '知道了'}
          //     ]
          //   }
          // ).show()

          $toast.show('任务还没开始哦~')
        }

        this.isAssistantAlive().then(() => {
          showPopup()
        }, () => {
          if (this.isNewbie) {
            this.showDownloadAssistantPopup()
            return
          }
          showPopup()
        })
      },

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      })
    }
  }
</script>



