<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <div class="dashboard-container page-content">
      <header>
        <img class="logo" src="//assets.qkcdn.com/images/6dc5a7a8f2c77fa08df2a8f666537e76.png"/>
      </header>
      <section class="unbind-mobile-tips" :class="{'transition': showUnbindMobileTips}">
        <div class="unbind-mobile-tips_inner" :class="{'animated bounceIn': showUnbindMobileTips_animate}">当前账号无法保存收益，请尽快
          <a class="bind-mobile" href="javascript:;" @click="jumpTo('bindMobile')">绑定手机
            <img class="arrow_red_r" src="//assets.qkcdn.com/images/736d62afedd2131485ad1c660fa0e4d2.png">
          </a></div>
      </section>
      <section class>
        <div class="balance">
          <div class="fs12-fw600 cf7 ls09 lh17">我的资产</div>
          <div class="layout-relative">
            <animate-num ref="animatenum" class="money"></animate-num>
            <div @click="jumpTo('user')" class="withdraw">
              <img class="photo" :src="avatar"/>
              <span class="">提现</span></div>
          </div>
          <div class="fs12-fw600 cf7">累计收入<span class="cff mrg-r16 mrg-l4">{{ total_reward }}元</span>今日收入
            <span class="cff mrg-l4">{{ today_reward }}元</span></div>
        </div>
      </section>
      <section class="entrance-middle">
        <column-items :list="entrancesMiddle"></column-items>
      </section>
      <!-- dashboard 接口请求完成，has_finished_task_once 初始化为0或1，才相应显示 -->
      <section class="entrance-bottom">
        <row-items v-if="has_finished_task_once >= 0" :list="entrancesBottom" :locked="bottomEntranceLocked"></row-items>
      </section>
      <footer>
        <img class="reminder" src="//assets.qkcdn.com/images/5c442a8122c482bbc8718c41ea43e72c.png"/>
      </footer>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "~scss/mixins";
  @import "./animate.scss";

  @mixin padding-lr ($num: 20) {
    padding-left: $num + px;
    padding-right: $num+ px;
  }

  .dashboard-container {
    header {
      height: 62px;
      padding-top: 24px;
      padding-bottom: 16px;

      @include padding-lr();

      background: #FFFFFF;
    }

    .unbind-mobile-tips {
      height: 0;
      padding-bottom: 0;
      overflow: hidden;

      &.transition {
        height: 44px;
        padding-bottom: 8px;
      }

      .unbind-mobile-tips_inner {
        opacity: 0;

        &.animate {
          opacity: 1;
        }

        height: 36px;
        line-height: 36px;
        padding: 0 15px;
        border-radius: 2px;
        background-color: #fff5e3;
        font-size: 12px;
        font-weight: 600;
        color: #3a3a3a;

        @include font-semibold();

        .bind-mobile {
          color: #ea5a49;
          text-decoration: none;

          .arrow_red_r {
            width: 4px;
            height: 6px;
            vertical-align: middle;
            margin-bottom: 3px;
          }
        }
      }
    }

    .logo {
      width: 135px;
      height: 22px;
    }

    section {
      @include padding-lr();

      background: #FFFFFF;
    }

    section.entrance-middle {
      padding-bottom: 15px;
    }

    section.entrance-bottom {
      margin-top: 10px;
      padding-top: 15px;
      padding-bottom: 15px;
    }

    .balance {
      padding: 16px;
      width: 335px;
      height: 124px;
      background: url("//assets.qkcdn.com/images/97e3b01f9f32bb4883df6071de579209.png") no-repeat;
      background-size: 100% 100%;

      .ls09 {
        letter-spacing: 0.9px;
      }

      .fs12-fw600 {
        font-size: 12px;
        font-weight: 600;

        @include font-semibold();
      }

      .cf7 {
        color: #ff9177;
      }

      .cff {
        color: #FFFFFF;
      }

      .mrg-l4 {
        margin-left: 4px;
      }

      .mrg-r16 {
        margin-right: 16px;
      }

      .lh17 {
        line-height: 17px;
      }

      .layout-relative {
        position: relative;
        margin-top: 10px;

        .money {
          display: inline-block;
          height: 48px;
          line-height: 48px;
          font-size: 48px;
          color: #ffffff;
          font-family: DINMittelschriftStd;
        }

        .withdraw {
          position: absolute;
          right: 0;
          display: inline-block;
          width: 74px;
          height: 32px;
          line-height: 32px;
          border-radius: 16px;
          background-color: #ffe371;
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
          font-size: 13px;
          font-weight: 600;
          color: #3a3a3a;

          @include font-semibold();

          img.photo {
            width: 28px;
            height: 28px;
            border-radius: 50%;
            vertical-align: top;
            margin-top: 2px;
            margin-left: 2px;
            margin-right: 2px;
          }
        }
      }
    }

    footer {
      img.reminder {
        display: block;
        width: 174px;
        height: 100px;
        margin: 29px auto 34px;
      }
    }
  }
</style>
<script>
  import Vue from 'vue'
  import ColumnItems from './components/ColumnItems.vue'
  import RowItems from './components/RowItems.vue'
  import AnimateNum from '@/widget/AnimateNum.vue'
  import {
    popup_important_001,
    popup_important_002,
    popup_important_003,
    popup_important_004,
    popup_lottery_win,
    popup_national_event
  } from './popups.js'
  import {
    KEY_NEW_QIANKA_NOTICE,
    FLASH_LOGIN_WITH_CERT,
    KEY_UID,
    KEY_NICKNAME,
    KEY_TASK_LIST_TOP_TIPS,
    WEBCLIP_MAYBE_LATER_CLICK_NUM
  } from 'constants'
  import {mapState, mapActions} from 'vuex'

  let entrances_middle = [
    {color: '#3a3a3a',
      bg: '//assets.qkcdn.com/images/7ddef4981fcf542e9f212bc3e0359eb8.png',
      title: '试玩赚钱',
      subTitle: '海量应用 下载奖现金',
      path: '/tasks'
    },
    {color: '#ffffff',
      bg: '//assets.qkcdn.com/images/d2b472f2ae4351f8227bea3f7b99c611.png',
      title: '收徒赚钱',
      subTitle: '上不封顶 躺着赚现金',
      path: '/apprentice'
    }
  ]
  let entrances_bottom = [
    {bg: '//assets.qkcdn.com/images/0717aa26844e8e4635c0597da39cde35.png',
      title: '办卡赚钱',
      subTitle: '无需开卡 高额现金奖励',
      path: '/credit/list'
    },
    {bg: '//assets.qkcdn.com/images/ee091e446ee9956ae31a3529a3bb4f5f.png',
      title: '注册赚钱',
      subTitle: '步骤简单 高额现金奖励',
      path: '/gaoe'
    },
    {bg: '//assets.qkcdn.com/images/0b346ba449d484d54537ae39aa51bb48.png',
      title: '每日开奖',
      subTitle: '天天福利 千元大奖一起分',
      path: '/lottery'
    }
  ]
  export default {
    components: {
      ColumnItems,
      RowItems,
      AnimateNum
    },
    data () {
      return {
        entrancesMiddle: entrances_middle,
        entrancesBottom: entrances_bottom,
        bottomEntranceLocked: false,
        showUnbindMobileTips: false,
        showUnbindMobileTips_animate: false
      }
    },
    computed: {
      ...mapState('dashboard', [
        'uid',
        'nickname',
        'avatar',
        'balance',
        'today_reward',
        'total_reward',
        'lottery',
        'create_day',
        'bind_mobile',
        'gift',
        'show_customer',
        'webclip',
        'has_finished_task_once'
      ])
    },
    mounted () {
      this.fetchDashboard()
    },
    destroyed () {
      $bus.$emit('hideWebclip')
      $bus.$emit('hideCustomerService')
      if (this.hongbao) {
        this.hongbao.$destroy()
      }

      if (this.showAnimateNumDelay) {
        clearTimeout(this.showAnimateNumDelay)
      }
    },
    methods: {
      ...mapActions('dashboard', [
        'dashboard',
        'lotteryGetReward'
      ]),
      fetchDashboard () {
        this.dashboard().then(() => {
          this.startAnimateNumber(this.balance, 300)
          this.showUnbindMobileTips = !this.bind_mobile && this.total_reward >= 2
          this.bottomEntranceLocked = !this.has_finished_task_once
          if (this.showUnbindMobileTips) {
            setTimeout(() => {
              this.showUnbindMobileTips_animate = true
            }, 750)
          }
          // 保存到 local storage
          $storage.set(KEY_UID, this.uid)
          $storage.set(KEY_NICKNAME, this.nickname)

          // 如果用户 total_reward 超过 2 元, 则需要在列表页面中展示类似客官常来啊之类的文字提示 (TaskListTopTips)
          let topTipsKey = `${KEY_TASK_LIST_TOP_TIPS}:${this.uid}`
          if (parseFloat(this.total_reward) >= 2 && !$storage.get(topTipsKey)) {
            $storage.set(topTipsKey, 1) // 1: 需要展示，未展示 2: 需要展示，已展示
          }
          // 新人红包
          if (this.gift) {
            // gift=4 特殊渠道新用户，不弹红包弹层，直接跳转列表页（后台设置红包金额为-1
            if (this.gift === 4) {
              $router.forward({ path: '/tasks' })
            } else {
              this.prepareOpenHongbao().then((popup) => {
                popup.show()
              })
            }
          }
          // 咖妹的向下定位px
          let pos_bottom = 26
          let emitWebclip = () => {
            pos_bottom = 60 + pos_bottom
            $bus.$emit('showWebclip', { url: this.webclip })
          }
          // (前端控制)点击"以后再说"，关闭提示，且当日不再提示，点击过≥2次"以后再说"，该用户再也不提示安装钱咖入口
          let webclip_key = WEBCLIP_MAYBE_LATER_CLICK_NUM
          let webclip_click_num = $storage.get(webclip_key)
          if (this.webclip) {
            if (!webclip_click_num) {
              emitWebclip()
            } else if (new Date().getDate().toString() !== $cookie.get(webclip_key) && webclip_click_num < 2) {
              emitWebclip()
            }
          }
          // 咖妹
          if (this.show_customer) {
            $bus.$emit('showCustomerService', { pos_bottom })
          }
          // 开奖弹层
          this.lotteryPop()
          // 钱咖4新版上线，4种样式
          this.importantPop()
        })
      },
      prepareOpenHongbao () {
        let prepareHongbao = this.prepareHongbao

        return new Promise((resolve) => {
          require.ensure([], () => {
            let el = document.createElement('div')
            el.id = 'open-hongbao'
            document.body.appendChild(el)
            const OpenHongbao = require('./OpenHongbao.vue')

            let popup = new Vue({
              components: {
                OpenHongbao
              },
              el: '#open-hongbao',
              template: `
                <open-hongbao ref="openHongbao" :onOpenHongbao="onOpenHongbao"></open-hongbao>
              `,
              methods: {
                show () {
                  this.$refs.openHongbao.show()
                },
                hide () {
                  this.$refs.openHongbao.hide()
                },
                onOpenHongbao () {
                  const coin_animate_duration = 750
                  const popup_leave_duration = 300

                  const timeout = (duration, result) => {
                    return new Promise(resolve => setTimeout(() => resolve(result), duration))
                  }

                  timeout(coin_animate_duration - 250)
                    .then(() => {
                      this.hide()
                      return timeout(popup_leave_duration)
                    })
                    .then(() => {
                      prepareHongbao().then(popup => popup.show())
                    })
                }
              }
            })

            resolve(popup)
          })
        })
      },

      prepareHongbao () {
        return new Promise((resolve) => {
          require.ensure([], () => {
            let el = document.createElement('div')
            el.id = 'hongbao'
            document.body.appendChild(el)
            const Hongbao = require('./HongbaoNew.vue')

            let gift = this.gift
            let hongbao = new Vue({
              components: {
                Hongbao
              },
              el: '#hongbao',
              template: `
                <hongbao :gift="gift" ref="hongbao" :make-money-callback="onClick"></hongbao>
              `,
              data: {
                gift: gift
              },
              methods: {
                show () {
                  this.$refs.hongbao.show()
                },
                hide () {
                  this.$refs.hongbao.hide()
                },
                onClick: () => {
                  $router.forward('/tasks')
                }
              }
            })

            resolve(hongbao)
          })
        })
      },
      lotteryPop () {
        // 开奖弹层
        if (this.lottery.win === 1 && this.lottery.win_info) {
          let popup = $popup.fromTemplate(popup_lottery_win.template(this.lottery.win_info),
            {
              effect: 'scale',
              cssClass: 'popup-lotterty-win',
              keep: true,
              buttons: [
                {
                  text: '点击领取',
                  onClick: () => {
                    popup.updateButtonStatusByIndex(0, false)
                    let lottery_id = this.lottery.win_info.lottery_id
                    this.lotteryGetReward(lottery_id)
                      .then(data => {
                        let payload = data.payload
                        let error_code = data.err_code
                        let err_msg = data.err_msg
                        // 40002: 已领取  40003: 未中奖
                        if (error_code && error_code === 40002 || error_code === 40003) {
                          $toast.show(err_msg)
                          popup.hide()
                          return
                        } else {
                          popup.updateButtonStatusByIndex(0, true)
                          popup.hide()
                          $toast.show(`成功领取 ${payload.reward} 元奖励`)

                          let balance = parseFloat(this.balance) + parseFloat(payload.reward)
                          let total_income = parseFloat(this.total_reward) + parseFloat(payload.reward)
                          let today_income = parseFloat(this.today_reward) + parseFloat(payload.reward)

                          this.updateIncome({
                            balance: balance.toFixed(2),
                            total_income: total_income.toFixed(2),
                            today_income: today_income.toFixed(2),
                            delay: 300
                          })
                        }
                      }, (err) => {
                        console.log(err)
                        popup.updateButtonStatusByIndex(0, true)
                      })
                  }
                }
              ]
            }
          )
          popup.show()
        }
      },
      importantPop () {
        // 新版钱咖上线重要通知，4种样式
        let is_old_driver = parseInt(this.create_day) < 20170301

        const makeImportantPopup = (templateAndOptions) => {
          return $popup.fromTemplate(
            templateAndOptions.template,
            templateAndOptions.options
          )
        }
        let isOld = !$storage.get(KEY_NEW_QIANKA_NOTICE + ':1')
        let isBindMobile = this.bind_mobile
        if (isBindMobile && is_old_driver && isOld) {
          // 弹层一: 如果该用户为4.0上线前的老用户且绑定了手机，则第一次进入4.0首页会显示此弹层
          let popup = $popup.fromTemplate(
            popup_important_001.template(isBindMobile),
            popup_important_001.options
          )
          popup.show().then((buttonIndex) => {
            if (buttonIndex === 1) {
              $router.forward({
                path: '/user/bindMobile/1',
                query: { backroute: '/dashboard' }
              })
            }
            $storage.set(KEY_NEW_QIANKA_NOTICE + ':1', 1)
          })
        } else if (is_old_driver && !isBindMobile && isOld) {
          // 弹层二: 如果该用户为4.0上线前的老用户但未绑定手机，则第一次进入4.0首页会显示此弹层
          let popup = makeImportantPopup(popup_important_002)
          popup.show().then((buttonIndex) => {
            if (buttonIndex === 1) {
              $router.forward({
                path: '/user/bindMobile/0',
                query: { backroute: '/dashboard' }
              })
            }
            $storage.set(KEY_NEW_QIANKA_NOTICE + ':1', 1)
          })
        } else if (is_old_driver && isBindMobile && this.bind_udid && $storage.get(FLASH_LOGIN_WITH_CERT)) {
          // 弹层三: 用户使用证书登录成功，并且该用户已绑定手机，每次成功登录后都会在首页显示此弹层
          let popup = makeImportantPopup(popup_important_003)
          popup.show()
          $storage.remove(FLASH_LOGIN_WITH_CERT)
        } else if (is_old_driver && !isBindMobile && this.bind_udid && $storage.get(FLASH_LOGIN_WITH_CERT)) {
          // 弹层四: 用户使用证书登录成功，并且该用户未绑定手机，每次成功登录后都会在首页显示此弹层，弹层上没有关闭按钮
          let popup = makeImportantPopup(popup_important_004)
          popup.show().then((buttonIndex) => {
            if (buttonIndex === 0) {
              $router.forward({
                path: '/user/bindMobile/0',
                query: { backroute: '/dashboard' }
              })
            }
          })
          $storage.remove(FLASH_LOGIN_WITH_CERT)
        }
      },
      updateIncome (data) {
        this.balance = data.balance
        this.total_reward = data.total_income
        this.today_reward = data.today_income
        this.startAnimateNumber(data.balance, data.delay)
      },
      startAnimateNumber (amount, delay = 0) {
        if ((parseInt(amount) !== 0) && !amount) return
        this.animate_num_end = parseFloat(amount) || 0

        this.showAnimateNumDelay = setTimeout(() => {
          this.$refs.animatenum.run(this.animate_num_end)
        }, delay)
      },
      showNationalPopup () {
        let popup = $popup.fromTemplate(
          popup_national_event.template,
          popup_national_event.options
        )

        popup.show().then((buttonIndex) => {
          if (buttonIndex === 0) {
            $router.forward({
              path: '/apprentice'
            })
          }
        })
      },
      jumpTo (routeName) {
        switch (routeName) {
          case 'bindMobile':
            $router.forward({
              path: '/user/bindMobile/0',
              query: { backroute: '/dashboard' }
            })
            break
          case 'user':
            $router.forward({
              path: '/user/'
            })
            break
        }
      }
    }
  }
</script>



