<template>
  <div class="page page-easy-detail" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#FFDD55"
      menu-bg-color="#FFD155"
      color="#3A3A3A"
      :on-back-button-click="back"
      :expanded="true"
      :animate="false"
      :show-back-button="false"
      :show-menu-button="true"
      :on-menu-button-click="back"
    >
      <div slot="content">
       <TaskDetailTitle>
        <!-- 简易任务任务 - 奖励 -->
        <div class="reward"><span>任务奖励 + </span>
          <span class="income" v-text="detail.reward"></span> 元
        </div>

        <div class="countdown">
          <span>剩余时间 </span><count-down v-if="showCountDown" :deadline="detail.deadline * 1000" format="mm:ss"></count-down>
        </div>
       </TaskDetailTitle>
      </div>
    </AnimateHeader>
    <div class="page-content easy-detail">
      <div class="icon-wrapper">
        <!-- 让设计给一个默认的 icon -->
        <img :src="detail.icon" alt="应用图标">
      </div>
      <div class="step">
        <div>下载应用，安装后无需打开</div>
        <div>即可返回本页领取奖励</div>
      </div>

      <div class="btn-wrapper">
        <div class="button button-positive btn-download" @click="download(detail.download_url)">
          <span>下载应用</span>
        </div>
      </div>

      <div class="btn-wrapper" v-if="is_ios_11">
        <div class="button button-positive btn-download" @click="openApp(detail)">
          <span>打开应用</span>
        </div>
      </div>

      <div class="btn-wrapper">
        <div class="button button-assertive btn-reward" :class="{'disabled': appStatus !== 2}" @click="checkState(detail.task_id)">
          <span v-text="btnRewardText"></span>
        </div>
      </div>
    </div>

    <AssistantInvalid/>
    <AssistantUnopen/>
  </div>
</template>
<style lang='scss' scoped>
  @import "~scss/mixins";

  .page-easy-detail {
    .easy-detail {
      padding-top: 100px;
      background: #FFF;

      .icon-wrapper {
        width: 70px;
        height: 70px;
        line-height: 70px;
        margin: 30px auto 16px;
        padding: 2px;

        img {
          width: 65px;
          height: 65px;
          border-radius: 8px;
        }
      }

      .step {
        text-align: center;
        font-size: 14px;
        color: #3A3A3A;
        margin-bottom: 40px;

        @include font-regular();

        div {
          height: 20px;
          line-height: 20px;
          margin-bottom: 6px;
        }
      }

      .btn-wrapper {
        text-align: center;

        .btn-download, .btn-reward {
          width: 172px;
          height: 40px;
          min-height: 40px;
          border-radius: 2px;
          line-height: 40px;
          margin: 0 auto 15px auto;
          font-size: 13px;

          @include font-medium();

          &.disabled {
            background: #DDDDDD;
            opacity: 1;
          }
        }
      }
    }
  }
</style>
<script>
  import {mapState, mapActions} from 'vuex'
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import CountDown from '@/widget/CountDown.vue'
  import TaskDetailTitle from './TaskDetailTitle.vue'
  import AssistantInvalid from '../popups/AssistantInvalid.vue'
  import AssistantUnopen from '../popups/AssistantUnopen.vue'

  import {
    // popup_task_ongoing,
    popup_abandon_task
  } from '../popups/popups'

  import {KEY_EASYDETAIL_BTN_REWARD} from 'constants'

  import {appendUrlParams} from 'utils'
  const is_ios_11 = () => {
    let re = /OS ((\d{1,2})_\d{1,2}||(\d{1,2})_\d{1,2}_\d{1,2}) like Mac OS X/i

    let result = re.exec(window.navigator.userAgent)
    let ios_ver = result && result[1] && result[1].substr(0, 2)
    return parseInt(ios_ver) === 11
  }

  export default {
    components: {
      AnimateHeader,
      CountDown,
      TaskDetailTitle,
      AssistantInvalid,
      AssistantUnopen
    },

    data () {
      return {
        appStatus: 0, // 0 未下载 1 下载中 2 已经下载
        syncTimer: 0,
        // 现在不用这个值来点亮按钮了
        btnRewardText: '领取奖励',
        btnRewardEnabled: $storage.get(`${KEY_EASYDETAIL_BTN_REWARD}:${this.$route.params.id}`) || 0,
        showCountDown: false,
        is_ios_11: is_ios_11()
      }
    },

    computed: {
      ...mapState('easy', [
        'detail'
      ])
    },

    mounted () {
      this.refresh()
    },

    destroyed () {
      clearTimeout(this.syncTimer)
    },

    methods: {
      syncAppStatus (bundle_id) {
        let self = this

        function sync (bundle_id) {
          $assistant.getAppInfo(bundle_id)
            .then(res => res && res.data && res.data.payload)
            .then(payload => {
              if (payload && payload.bundle_id) {
                if (payload.bundle_version) { // 应用已经安装
                  /* 如果应用下载完了，强制 call 一下钥匙心跳 */
                  $assistant.updateState()

                  let cnt = 10
                  const countDown = () => {
                    if (cnt > 0) {
                      self.btnRewardText = `正在检测应用(${cnt}s)`
                      cnt--
                      setTimeout(() => {
                        countDown()
                      }, 1000)
                      return
                    }

                    self.btnRewardText = '领取奖励'
                    self.appStatus = 2
                  }

                  countDown()

                  return
                }

                // 应用下载中
                self.appStatus = 1
              } else {
                // 应用未安装
                self.appStatus = 0
              }

              if (self.appStatus < 2) {
                self.syncTimer = setTimeout(() => {
                  sync(bundle_id)
                }, 1000)
              }
            })
        }
        if (!is_ios_11()) {
          sync(bundle_id)
        }
      },

      openApp (detail) {
        this.isAssistantAlive()
          .then(() => {
            $assistant.openAppIOS11({
              bid: detail.bundle_id,
              url: detail.callback_url
            }).then(res => res.data && res.data.status)
              .then((status) => {
                if (status === 'ok') {
                  this.appStatus = 2
                  this.btnRewardEnabled = 1
                  $storage.set(`${KEY_EASYDETAIL_BTN_REWARD}:${this.$route.params.id}`, 1)
                  /* 如果应用下载完了，强制 call 一下钥匙心跳 */
                  $assistant.updateState()
                } else {
                  $toast.show('应用没有安装或正在安装')
                }
              })
          })
      },

      refresh () {
        this
          .isAssistantAlive()
          .then(() => {
            return this.fetch(this.$route.params.id)
          })
          .then(payload => {
            this.syncAppStatus(payload.bundle_id)
            this.showCountDown = true
          })
      },

      back () {
        $popup.fromTemplate(
          popup_abandon_task.template,
          popup_abandon_task.options
        )
          .show()
          .then(buttonIndex => {
            if (buttonIndex === 0) {
              let goBack = () => {
                $router.back('/tasks')
              }
              if (this.detail.task_id === 0) {
                goBack()
                return
              }
              this.giveup(this.detail.task_id).then(() => {
                $storage.set(`${KEY_EASYDETAIL_BTN_REWARD}:${this.$route.params.id}`, 0)
                goBack()
              })
            }
          })
      },

      download (download_url) {
        if (!this.is_ios_11) {
          this.btnRewardEnabled = 1
          $storage.set(`${KEY_EASYDETAIL_BTN_REWARD}:${this.$route.params.id}`, 1)
        }
        location.href = appendUrlParams(download_url, {
          'return_url': location.href
        })
      },

      checkState (task_id) {
        if (this.appStatus === 0) {
          $popup
            .fromTemplate(`
              <div class="title">
                下载失败
              </div>
              <div class="desc">
                您下载的应用错误或尚未完成安装
              </div>
            `, {
              cssClass: 'popup-general',
              buttons: [
                { text: '知道了' }
              ]
            })
            .show()

          return
        }

        if (this.appStatus === 1) {
          $popup
            .fromTemplate(`
              <div class="text-center">
                应用下载中，安装后可领取奖励
              </div>
            `, {
              buttons: [{ text: '知道了' }]
            })
            .show()

          return
        }

        this.check(task_id).then(payload => {
          if (payload.status === 2) {
            $popup
              .fromTemplate(`
                <div class="title">
                  拼命提交任务中
                </div>

                <div class="desc">
                  繁忙期间可能存在几分钟延迟
                </div>
              `, {
                cssClass: 'popup-general',
                buttons: [{ text: '知道了' }]
              })
              .show()
          }

          /* 提交审核，向服务器发起请求的同时，call钥匙心跳 */
          $assistant.updateState()
        })
      },

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      }),

      ...mapActions('easy', [
        'fetch',
        'check',
        'giveup'
      ])
    }
  }
</script>



