<template>
  <div class="page page-zstask-detail" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#FFDD55"
      menu-bg-color="#FFD155"
      color="#3A3A3A"
      :on-back-button-click="back"
      :expanded="true"
      :animate="false"
      :show-back-button="false"
      :show-menu-button="true"
      :on-menu-button-click="back"
    >
      <div slot="content">
        <TaskDetailTitle>
          <div class="reward"><span>任务奖励 + </span>
            <span class="income" v-text="zstask_info.reward"></span> 元
          </div>
        </TaskDetailTitle>
      </div>
    </AnimateHeader>
    <div class="page-content zstask-detail">
      <div class="icon-wrapper">
        <img :src="zstask_info.icon" alt="">
      </div>
      <div class="app_name" v-text="zstask_info.title"></div>

      <div class="step-button" @click="openApp(zstask_info)">
        <span>开始任务</span>
      </div>
      <div class="step-button disabled" @click="checkReward()" :class="{reward: steps[1] == 1}">
        <span>领取奖励</span>
      </div>
    </div>

    <AssistantInvalid />
    <AssistantUnopen />
  </div>
</template>
<style lang='scss' scoped>
  @import "~scss/mixins";

  .page-zstask-detail {
    .zstask-detail {
      padding-top: 100px;
      background: #FFF;

      .icon-wrapper {
        border-radius: 8px;
        width: 70px;
        height: 70px;
        line-height: 70px;
        margin: 30px auto 16px;
        padding: 2px;

        img {
          width: 65px;
          height: 65px;
          border-radius: 8px;
        }
      }

      .app_name {
        text-align: center;
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #3A3A3A;
        height: 22px;
        line-height: 22px;
        margin-bottom: 30px;
      }

      .step-button {
        width: 172px;
        height: 40px;
        background: #4A90E2;
        border-radius: 2px;
        line-height: 40px;
        text-align: center;
        margin: 0 auto 15px;

        span {
          font-size: 13px;
          color: #FFFFFF;

          @include font-medium();
        }

        &.disabled {
          background: #DDDDDD;
        }

        &.reward {
          background: #EF4F2A;
        }
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import TaskDetailTitle from './TaskDetailTitle.vue'
  import AssistantUnopen from '../popups/AssistantUnopen.vue'
  import AssistantInvalid from '../popups/AssistantInvalid.vue'
  import {mapState, mapActions} from 'vuex'
  import {
    ZSTASK_STATE_ONGOING,
    ZSTASK_STATE_FINISHED
  } from 'constants'

  import {
    popupGiveUp
  } from '../popups/popups.js'

  const is_ios_11 = () => {
    let re = /OS ((\d{1,2})_\d{1,2}||(\d{1,2})_\d{1,2}_\d{1,2}) like Mac OS X/i

    let result = re.exec(window.navigator.userAgent)
    let ios_ver = result && result[1] && result[1].substr(0, 2)
    return parseInt(ios_ver) === 11
  }

  export default {
    components: {
      AnimateHeader,
      TaskDetailTitle,
      AssistantUnopen,
      AssistantInvalid
    },

    data () {
      return {
        checkRewardLock: false
      }
    },

    computed: {
      ...mapState('zstask', [
        'zstask_info',
        'steps'
      ])
    },

    mounted () {
      this.isAssistantAlive().then(() => {
        this.fetchDetail(this.$route.params.id)
          .then(() => {
            if (this.zstask_info.status === ZSTASK_STATE_ONGOING) {
              $api.zstaskCheckState(this.zstask_info.id)
            }
            if (this.zstask_info.status === ZSTASK_STATE_FINISHED) {
              $router.back('/zstasks')
            }
          })
      })
    },

    methods: {
      openApp (task) {
        this.isAssistantAlive().then(() => {
          if (!is_ios_11()) {
            this._openApp(task)
          } else {
            this._openAppWith11(task)
          }
        })
      },

      checkReward () {
        this.isAssistantAlive().then(() => {
          if (this.steps[1] === 0) return false
          if (this.checkRewardLock) return false
          this.checkRewardLock = true
          this._checkReward()
          setTimeout(() => {
            this.checkRewardLock = false
          }, 600)
        })
      },

      back () {
        let goBack = () => {
          $router.back('/zstasks')
        }
        if (this.zstask_info.id === 0) {
          goBack()
          return
        }
        $api.zstaskCheckState(this.zstask_info.id).then((response) => {
          let payload = response.data.payload
          if (payload.status === ZSTASK_STATE_ONGOING) {
            let p = popupGiveUp
            let popup = $popup.fromTemplate(p.template, p.options)
            popup.show().then((btn) => {
              if (btn === 0) { // 放弃任务
                $api.zstaskGiveup(this.zstask_info.id).then(() => {
                  goBack()
                })
              } else if (btn === 1) { // 取消
              }
            })
          } else {
            goBack()
          }
        })
      },

      ...mapActions('zstask', [
        'fetchDetail',
        'updateSteps',
        '_openApp',
        '_openAppWith11',
        '_checkReward'
      ]),

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      })
    }
  }
</script>



