<!-- 试玩赚钱列表 -->
<template>
  <div class="page page-task-list" v-nav="{hideNavbar: true}">
    <TaskListHeader
      bg-color="#FFDD55"
      menu-bg-color="#FFD155"
      color="#3A3A3A"
      title="试玩赚钱"
      sub-title="① 下载应用 &nbsp;② 打开试玩 &nbsp;③ 领取现金"
      :on-back-button-click="back"
      :expanded="true"
      :animate="true"
      :get-scroll-top="getScrollTop"
      :has-sub-header="showAssistantBanner"
    >
      <AssistantBanner v-if="showAssistantBanner" slot="subheader"></AssistantBanner>
    </TaskListHeader>

    <div class="page-content task-list" :class="{'has-subheader': showAssistantBanner}">
      <scroller ref="scroller" :style="{paddingTop: (showAssistantBanner ? 160 : 100) + 'px'}">
        <!--专属任务入口-->
        <ZsTaskBanner v-if="zstask_info.total > 0"/>
        <!-- 进行中的任务 -->
        <BlockTasksOngoing />

        <!-- 标准任务 -->
        <BlockTasks />

        <!-- 推荐任务 -->
        <BlockRecommends />

        <!-- 提示 -->
        <div class="task-tips" v-if="showAssistantBanner">
          开启助手显示全部任务
        </div>

        <!-- 简易任务 -->
        <BlockEasy />

        <!-- 游戏 -->
        <BlockGame />

        <!-- 标准任务预告 -->
        <BlockComing task-type='standard'/>

        <!-- 提示 -->
        <div class="task-tips" v-if="showAssistantBanner && tasks_coming.length == 8">
          开启助手显示全部任务
        </div>

        <!-- 简易任务预告 -->
        <BlockComing task-type='easy'/>

        <JailBroken />

        <div :style="{
          height: (showAssistantBanner ? 160 : 100) + 'px'
        }"></div>

        <div style="height: 20px;"></div>
      </scroller>
    </div>

    <AssistantInvalid />
    <AssistantUnopen />

    <!-- 业务弹层 -->
    <InstallCertification />
    <InstallKagou />
  </div>
</template>

<style lang='scss' scoped>
  @import '~scss/mixins';

  .task-list {
    padding-top: 100px;
    background: #FFF;

    &.has-subheader {
      padding-top: 160px;
    }

    .task-tips {
      text-align: center;
      font-size: 13px;
      line-height: 40px;
      height: 40px;
      color: #b5b5b5;

      @include font-regular();
    }
  }
</style>

<script>
  import {mapState, mapGetters, mapActions} from 'vuex'
  import TaskListHeader from '../components/header/Index.vue'
  import AssistantBanner from './AssistantBanner'
  import ZsTaskBanner from './ZsTaskBanner.vue'

  import BlockTasksOngoing from './BlockTasksOngoing.vue'
  import BlockTasks from './BlockTasks.vue'
  import BlockEasy from './BlockEasy.vue'
  import BlockComing from './BlockComing.vue'

  import BlockRecommends from './BlockRecommends.vue'
  import BlockGame from './BlockGame.vue'

  import JailBroken from './JailBroken.vue'

  // 助手相关弹层
  import AssistantUnopen from '../popups/AssistantUnopen.vue'
  import AssistantInvalid from '../popups/AssistantInvalid.vue'
  import InstallCertification from '../popups/InstallCertification.vue'
  import InstallKagou from '../popups/InstallKagou.vue'

  import {getCustomerServiceLink} from 'utils'

  import {
    popup_bind_phone,
    popup_jail_breaker,
    popup_user_freeze
  } from '../popups/popups'

  import {channel, events} from 'store/channel'

  export default {
    components: {
      TaskListHeader,
      AssistantBanner,
      ZsTaskBanner,

      BlockTasksOngoing,

      BlockTasks,
      BlockRecommends,

      BlockEasy,

      BlockGame,
      BlockComing,

      JailBroken,

      AssistantUnopen,
      AssistantInvalid,

      InstallCertification,
      InstallKagou
    },

    data () {
      return {
        showAssistantBanner: false
      }
    },

    computed: {
      ...mapState('task', [
        'zstask_info',
        'tasks_coming'
      ]),
      ...mapGetters('assistant', [
        'isNewbie'
      ])
    },

    mounted () {
      this.refresh()

      channel.$on(events.POPUP_JAIL_BREAKER, () => {
        this.showJailBreakerPopup()
      })

      channel.$on(events.POPUP_BIND_PHONE, () => {
        this.showBindPhonePopup()
      })

      channel.$on(events.POPUP_USER_FREEZE, () => {
        this.showUserFreezePopup()
      })
    },

    methods: {
      back () {
        $router.back('/dashboard')
      },

      refresh () {
        this.isAssistantAlive().then(() => {
          this.$store.dispatch('task/fetch')
        }, () => {
          if (this.isNewbie) {
            this.showAssistantBanner = true
            this.$store.dispatch('task/fetchNewbie')
          }
        })
      },

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      }),

      // 按照新的页面逻辑，这个弹层基本不会出现
      showJailBreakerPopup () {
        let popup = $popup.fromTemplate(
          popup_jail_breaker.template(getCustomerServiceLink()),
          popup_jail_breaker.options
        )
        popup.show()
      },

      showBindPhonePopup () {
        let popup = popup = $popup.fromTemplate(
          popup_bind_phone.template,
          popup_bind_phone.options
        )

        popup.show().then(buttonIndex => {
          if (buttonIndex === 1) {
            $router.forward({
              path: '/user/bindMobile/0',
              query: { backroute: '/tasks' }
            })
          }
        })
      },

      showUserFreezePopup () {
        let popup = $popup.fromTemplate(
          popup_user_freeze.template(),
          popup_user_freeze.options
        )
        popup.show()
      },

      getScrollTop () {
        let {left, top} = this.$refs.scroller.getPosition() // eslint-disable-line no-unused-vars
        return top
      }
    }
  }
</script>



