<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <div class="dashboard-container page-content">
      <header>
        <img class="logo" src="//assets.qkcdn.com/images/6dc5a7a8f2c77fa08df2a8f666537e76.png"/>
      </header>
      <section class="unbind-mobile-tips" :class="{'transition': showUnbindMobileTips}">
        <div class="unbind-mobile-tips_inner" :class="{'animated bounceIn': showUnbindMobileTips_animate}">当前账号无法保存收益，请尽快
          <a class="bind-mobile" href="javascript:;" @click="jumpTo('bindMobile')">绑定手机
            <img class="arrow_red_r" src="//assets.qkcdn.com/images/736d62afedd2131485ad1c660fa0e4d2.png">
          </a></div>
      </section>
      <section class>
        <div class="balance">
          <div class="fs12-fw600 cf7 ls09 lh17">我的资产</div>
          <div class="layout-relative">
            <animate-num ref="animatenum" class="money"></animate-num>
            <div @click="jumpTo('user')" class="withdraw">
              <img class="photo" :src="avatar"/>
              <span class="">提现</span></div>
          </div>
          <div class="fs12-fw600 cf7">累计收入<span class="cff mrg-r16 mrg-l4">{{ total_reward }}元</span>今日收入
            <span class="cff mrg-l4">{{ today_reward }}元</span></div>
        </div>
      </section>
      <section class="entrance-middle">
        <column-items :list="entrancesMiddle"></column-items>
      </section>
      <section class="entrance-bottom">
        <row-items :list="entrancesBottom" :locked="bottomEntranceLocked"></row-items>
      </section>
      <footer>
        <img class="reminder" src="//assets.qkcdn.com/images/5c442a8122c482bbc8718c41ea43e72c.png"/>
      </footer>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "~scss/mixins";
  @import "~@/dashboard/animate.scss";

  @mixin padding-lr ($num: 20) {
    padding-left: $num + px;
    padding-right: $num+ px;
  }

  .dashboard-container {
    header {
      height: 62px;
      padding-top: 24px;
      padding-bottom: 16px;

      @include padding-lr();

      background: #FFFFFF;
    }

    .unbind-mobile-tips {
      height: 0;
      padding-bottom: 0;
      overflow: hidden;

      &.transition {
        height: 44px;
        padding-bottom: 8px;
      }

      .unbind-mobile-tips_inner {
        opacity: 0;

        &.animate {
          opacity: 1;
        }

        height: 36px;
        line-height: 36px;
        padding: 0 15px;
        border-radius: 2px;
        background-color: #fff5e3;
        font-size: 12px;
        font-weight: 600;
        color: #3a3a3a;

        @include font-semibold();

        .bind-mobile {
          color: #ea5a49;
          text-decoration: none;

          .arrow_red_r {
            width: 4px;
            height: 6px;
            vertical-align: middle;
            margin-bottom: 3px;
          }
        }
      }
    }

    .logo {
      width: 135px;
      height: 22px;
    }

    section {
      @include padding-lr();

      background: #FFFFFF;
    }

    section.entrance-middle {
      padding-bottom: 15px;
    }

    section.entrance-bottom {
      margin-top: 10px;
      padding-top: 15px;
      padding-bottom: 15px;
    }

    .balance {
      padding: 16px;
      width: 335px;
      height: 124px;
      background: url("//assets.qkcdn.com/images/97e3b01f9f32bb4883df6071de579209.png") no-repeat;
      background-size: 100% 100%;

      .ls09 {
        letter-spacing: 0.9px;
      }

      .fs12-fw600 {
        font-size: 12px;
        font-weight: 600;

        @include font-semibold();
      }

      .cf7 {
        color: #ff9177;
      }

      .cff {
        color: #FFFFFF;
      }

      .mrg-l4 {
        margin-left: 4px;
      }

      .mrg-r16 {
        margin-right: 16px;
      }

      .lh17 {
        line-height: 17px;
      }

      .layout-relative {
        position: relative;
        margin-top: 10px;

        .money {
          display: inline-block;
          height: 48px;
          line-height: 48px;
          font-size: 48px;
          color: #ffffff;
          font-family: DINMittelschriftStd;
        }

        .withdraw {
          position: absolute;
          right: 0;
          display: inline-block;
          width: 74px;
          height: 32px;
          line-height: 32px;
          border-radius: 16px;
          background-color: #ffe371;
          box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
          font-size: 13px;
          font-weight: 600;
          color: #3a3a3a;

          @include font-semibold();

          img.photo {
            width: 28px;
            height: 28px;
            border-radius: 50%;
            vertical-align: top;
            margin-top: 2px;
            margin-left: 2px;
            margin-right: 2px;
          }
        }
      }
    }

    footer {
      img.reminder {
        display: block;
        width: 174px;
        height: 100px;
        margin: 29px auto 34px;
      }
    }
  }
</style>
<script>
  import Vue from 'vue'
  import ColumnItems from '@/dashboard/components/ColumnItems.vue'
  import RowItems from '@/dashboard/components/RowItems.vue'
  import AnimateNum from '@/widget/AnimateNum.vue'
  import channel from '@/dashboard/channel'

  import {mapState, mapActions} from 'vuex'

  let entrances_middle = [
    {color: '#3a3a3a',
      bg: '//assets.qkcdn.com/images/7ddef4981fcf542e9f212bc3e0359eb8.png',
      title: '试玩赚钱',
      subTitle: '海量应用 下载奖现金',
      path: '/tasks'
    },
    {color: '#ffffff',
      bg: '//assets.qkcdn.com/images/d2b472f2ae4351f8227bea3f7b99c611.png',
      title: '收徒赚钱',
      subTitle: '上不封顶 躺着赚现金',
      path: '/apprentice'
    }
  ]
  let entrances_bottom = [
    {bg: '//assets.qkcdn.com/images/0717aa26844e8e4635c0597da39cde35.png',
      title: '办卡赚钱',
      subTitle: '无需开卡 高额现金奖励',
      path: '/credit/list'
    },
    {bg: '//assets.qkcdn.com/images/ee091e446ee9956ae31a3529a3bb4f5f.png',
      title: '注册赚钱',
      subTitle: '步骤简单 高额现金奖励',
      path: '/gaoe'
    },
    {bg: '//assets.qkcdn.com/images/0b346ba449d484d54537ae39aa51bb48.png',
      title: '每日开奖',
      subTitle: '天天福利 千元大奖一起分',
      path: '/lottery'
    }
  ]
  export default {
    components: {
      ColumnItems,
      RowItems,
      AnimateNum
    },
    data () {
      return {
        entrancesMiddle: entrances_middle,
        entrancesBottom: entrances_bottom,
        bottomEntranceLocked: true,
        showUnbindMobileTips: false,
        showUnbindMobileTips_animate: false
      }
    },
    computed: {
      ...mapState('dashboard', [
        'uid',
        'nickname',
        'avatar',
        'balance',
        'today_reward',
        'total_reward',
        'lottery',
        'create_day',
        'bind_mobile',
        'gift',
        'show_customer',
        'webclip',
        'has_finished_task_once'
      ])
    },
    mounted () {
      channel.$on('updateIncome', (data) => {
        this.updateIncome(data)
      })
      this.prepareOpenHongbao().then((popup) => {
        popup.show()
      })
    },
    destroyed () {
      $bus.$emit('hideWebclip')
      $bus.$emit('hideCustomerService')
      if (this.hongbao) {
        this.hongbao.$destroy()
      }

      if (this.showAnimateNumDelay) {
        clearTimeout(this.showAnimateNumDelay)
      }
    },
    methods: {
      ...mapActions('dashboard', [
        'dashboard',
        'fetchGift',
        'lotteryGetReward'
      ]),
      updateIncome (data) {
        this.balance = data.balance
        this.total_reward = data.total_income
        this.today_reward = data.today_income
        this.startAnimateNumber(data.balance, data.delay)
      },
      startAnimateNumber (amount, delay = 0) {
        if ((parseInt(amount) !== 0) && !amount) return
        this.animate_num_end = parseFloat(amount) || 0

        this.showAnimateNumDelay = setTimeout(() => {
          this.$refs.animatenum.run(this.animate_num_end)
        }, delay)
      },
      prepareOpenHongbao () {
        let prepareHongbao = this.prepareHongbao
        let fetchGift = this.fetchGift
        return new Promise((resolve) => {
          require.ensure([], () => {
            let el = document.createElement('div')
            el.id = 'open-hongbao'
            document.body.appendChild(el)
            const OpenHongbao = require('./OpenHongbao.vue')

            let popup = new Vue({
              components: {
                OpenHongbao
              },
              el: '#open-hongbao',
              template: `
                <open-hongbao ref="openHongbao" :onOpenHongbao="onOpenHongbao"></open-hongbao>
              `,
              methods: {
                show () {
                  this.$refs.openHongbao.show()
                },
                hide () {
                  this.$refs.openHongbao.hide()
                },
                onOpenHongbao () {
                  let openHongbao = () => {
                    const coin_animate_duration = 750
                    const popup_leave_duration = 300

                    const timeout = (duration, result) => {
                      return new Promise(resolve => setTimeout(() => resolve(result), duration))
                    }

                    timeout(coin_animate_duration - 250)
                      .then(() => {
                        this.hide()
                        return timeout(popup_leave_duration)
                      })
                      .then(() => {
                        prepareHongbao().then(popup => popup.show())
                      })
                  }
                  fetchGift().then(() => {
                    openHongbao()
                  })
                }
              }
            })

            resolve(popup)
          })
        })
      },

      prepareHongbao () {
        return new Promise((resolve) => {
          require.ensure([], () => {
            let el = document.createElement('div')
            el.id = 'hongbao'
            document.body.appendChild(el)
            const Hongbao = require('@/dashboard/HongbaoNew.vue')

            let gift = this.gift
            let hongbao = new Vue({
              components: {
                Hongbao
              },
              el: '#hongbao',
              template: `
                <hongbao :gift="gift" ref="hongbao" :make-money-callback="onClick"></hongbao>
              `,
              data: {
                gift: gift
              },
              methods: {
                show () {
                  this.$refs.hongbao.show()
                },
                hide () {
                  this.$refs.hongbao.hide()
                },
                onClick: () => {
                  $router.forward('/tasks')
                }
              }
            })

            resolve(hongbao)
          })
        })
      },
      jumpTo (routeName) {
        switch (routeName) {
          case 'bindMobile':
            $router.forward({
              path: '/user/bindMobile/0',
              query: { backroute: '/dashboard' }
            })
            break
          case 'user':
            $router.forward({
              path: '/user/'
            })
            break
        }
      }
    }
  }
</script>



