import _ from 'lodash'
import moment from 'moment'
import cookie from './cookie'
import bus from './bus'
import assistant from './assistant'
import api from './api'
import QkPopup from './popup'
import Upload from './upload'
import StorageFactory from './storage'

import Vue from 'vue'
import VueAnimateNumber from 'vue-animate-number'
Vue.use(VueAnimateNumber)

window._ = _
window.moment = Vue.prototype.$moment = moment
window.$cookie = Vue.prototype.$cookie = cookie
window.$bus = Vue.prototype.$bus = bus
window.$assistant = Vue.prototype.$assistant = assistant
window.$api = Vue.prototype.$api = api
window.$popup = QkPopup
window.$upload = Upload
// window.$storage = StorageFactory(localStorage)
window.$sess = StorageFactory(sessionStorage)

// $modal.destroy() bug
let destroyModal = $modal.destroy
$modal.destroy = (modal) => {
  destroyModal(modal)
  if (document.querySelectorAll('[von-modal]').length === 0) {
    document.body.classList.remove('modal-open')
  }
}



