const LoanList = r => require.ensure([], () => r(require('./views/LoanList')), 'loan-list')
const LoanDetail = r => require.ensure([], () => r(require('./views/LoanDetail')), 'loan-detail')

export default [
  {
    path: '/loan',
    name: 'loan',
    component: LoanList
  },
  {
    path: '/loan/detail/:id',
    name: 'loan_detail',
    component: LoanDetail
  }
]



