<template>
  <div class="page page-zstask-list" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#FFDD55"
      menu-bg-color="#FFD155"
      color="#3A3A3A"
      title="专属任务"
      :on-back-button-click="back"
      :expanded="false"
    >
    </AnimateHeader>

    <div class="page-content zstask-list">
      <ActionList v-for="(zstasks, index) in tasks" :key="index" :title="zstasks.date">
        <Action v-for="task in zstasks.zs_tasks" :key="task.task_id"  @click.native="start(task, zstasks)">
          <div slot="icon" class="icon">
            <img :src="task.icon">
          </div>

          <span slot="app-name" v-text="task.app_name"></span>
          <span slot="desc" class="count-down-box">
            <span v-if="task.status === 2" class="ongoing">进行中…</span>
            <span v-if="task.status === 0" v-text="task.desc"></span>
          </span>
          <div slot="reward-wrap" class="reward-content" :class="{disabled: zstasks.is_enabled === 0}">
            <span class="plus">+</span>
            <span class="reward" v-text="parseFloat(task.money).toFixed(1)"></span>
            <span class="yuan">元</span>
          </div>
        </Action>
      </ActionList>
    </div>
  </div>
</template>
<style lang='scss'>
  .page-zstask-list {
    .zstask-list {
      padding-top: 50px;
      padding-bottom: 20px;
      background: #FFFFFF;

      .disabled {
        color: #B5B5B5;
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {Action, ActionList} from '../components/actions'
  import {mapState, mapActions} from 'vuex'

  import {
    ZSTASK_STATE_ONGOING
  } from 'constants'

  import {
    popupHasOngoing
  } from '../popups/popups.js'

  export default {
    components: {
      Action,
      ActionList,
      AnimateHeader
    },

    computed: {
      ...mapState('zstask', [
        'tasks'
      ])
    },

    mounted () {
      this.$store.dispatch('zstask/fetch')
    },

    methods: {
      back () {
        $router.back('/tasks')
      },

      start (task, zstasks) {
        if (!zstasks.is_enabled && task.status !== ZSTASK_STATE_ONGOING) {
          let hasOngoing = _.some(zstasks.zs_tasks, (task) => task.status === ZSTASK_STATE_ONGOING)
          if (hasOngoing) {
            this.showPopup('has_ongoing')
          } else {
            this.showPopup('muted_task', {
              date: zstasks.date
            })
          }
          return
        }

        let route = {
          name: 'zstask_detail',
          params: {
            id: task.zs_task_id
          }
        }

        $router.forward(route)
      },

      showPopup (popupType, args) {
        let popup

        if (popupType === 'muted_task') {
          $toast.show('任务还没开始哦~')
        }

        if (popupType === 'has_ongoing') {
          let p = popupHasOngoing
          popup = $popup.fromTemplate(p.template, p.options)
        }

        if (popup) popup.show()
      },

      ...mapActions('zstask', [
        'fetch'
      ])
    }
  }
</script>



