// 彩蛋任务
const EggsTaskDetail = r => require.ensure([], () => r(require('./Detail')), 'egg-detail')

// 抽奖大转盘
const LuckyDraw = r => require.ensure([], () => r(require('./LuckyDraw')), 'lucky-draw')

export default [
  { path: '/eggs/detail', name: 'eggs_detail', component: EggsTaskDetail },
  { path: '/eggs/luckyDraw', name: 'eggs_luckyDraw', component: LuckyDraw }
]



