import _ from 'lodash'

import {
  KEY_NICKNAME,
  KEY_UID
} from 'constants'

export const timeout = (duration) => {
  return new Promise((resolve) => {
    setTimeout(resolve, duration || 0)
  })
}

export const getCustomerServiceLink = () => {
  let link = 'https://static.meiqia.com/dist/standalone.html?eid=27582&metadata={"ID":' + ($storage.get(KEY_UID) || '游客') + ',"昵称":' + ($storage.get(KEY_NICKNAME) || '无') + '}'
  return encodeURI(link)
}

// Crius 统计
const _assembleUrl = (msg) => {
  let url = 'https://stats.youlianyc.com/stats/website/report'
  let query = ''

  _.each(msg, function (v, k) {
    if (query === '') {
      query = '?' + k + '=' + v
    } else {
      query += '&' + k + '=' + v
    }
  })
  return url + query + '&rnd=' + Date.now()
}

const statSend = (msg) => {
  var xhr = new XMLHttpRequest()
  xhr.open('GET', _assembleUrl(msg), true)
  xhr.send()
}

const statAction = (actionType, actionCode, payload) => {
  if (__ENV__ && __ENV__.stat_switch === 1) {
    let msg = {
      url: encodeURIComponent(location.href),
      u: $storage.get('qk:biz:uid'),
      uu: $storage.get('qk:biz:uuid'),
      a: 'qianka',
      t: actionType,
      c: actionCode
    }

    if (payload) msg.ap = JSON.stringify(payload)

    let currentRoute = $router.currentRoute.name

    msg.ref = currentRoute
      ? 'page_' + currentRoute
      : encodeURIComponent(document.referrer)

    statSend(msg)
  }
}

export const crius = {
  send: statSend,
  sendAction: statAction
}

export const getUrlParams = (url) => {
  var params = {}
  var query = url.split('?')
  if (query.length > 1) {
    var buf = query[1].split('&')
    for (var i = 0; i < buf.length; i++) {
      var tmp = buf[i].split('=')
      params[tmp[0]] = tmp[1]
    }
  }
  return params
}

import v4 from 'uuid/v4'
export const uuid = () => {
  return v4()
}

export const get_protocol = () => {
  const re = /^https?/
  let result = re.exec(location.href)
  return result && result[0]
}

export const appendUrlParams = (url, params) => {
  let append = []
  let seperator = url.split('?').length > 1 ? '&' : '?'
  _.each(params, (v, k) => {
    append.push(`${k}=${encodeURIComponent(v)}`)
  })
  return [url, append.join('&')].join(seperator)
}

// DNT flag
export const is_dnt = () => {
  const root = document.documentElement
  return parseInt(root.getAttribute('dnt'))
}

export const is_ios_11 = () => {
  let re = /OS ((\d{1,2})_\d{1,2}||(\d{1,2})_\d{1,2}_\d{1,2}) like Mac OS X/i

  let result = re.exec(window.navigator.userAgent)
  let ios_ver = result && result[1] && result[1].substr(0, 2)
  return parseInt(ios_ver) === 11
}



