<template>
  <transition name="fade-customer-service">
    <div :style="{ 'bottom': pos_bottom + 'px' }" class="customer-service" v-if="show" @click="talkToCustomerService()" :class="{'move': kameiMove()}">
      <img src="//qianka.b0.upaiyun.com/images/34ce008031c02405a50b4850c3718d8b.png" alt="咖妹">
      <!-- <img src="//qianka.b0.upaiyun.com/images/34ce008031c02405a50b4850c3718d8b.png" alt="咖妹-2x">
      <img src="//qianka.b0.upaiyun.com/images/b583900780ba1d777ebedde4893091d1.png" alt="咖妹-3x"> -->
    </div>
  </transition>
</template>
<style lang="scss" scoped>
  .fade-customer-service-enter-active, .fade-customer-service-leave-active {
    transition: opacity 0.5s;
    -webkit-transition: opacity 0.5s;
  }

  .fade-customer-service-enter, .fade-customer-service-leave-to {
    opacity: 0;
  }

  .customer-service {
    height: 45px;
    position: absolute;
    bottom: 27px;
    z-index: 11;
    right: 16px;

    img {
      width: 90px;
      height: 45px;
    }

    &.move {
      bottom: 50px;
    }
  }
</style>
<script>
  import { getCustomerServiceLink } from 'utils'
  import {mapState} from 'vuex'

  export default {
    created () {
      $bus.$on('showCustomerService', (data = {pos_bottom: 26}) => {
        this.pos_bottom = data.pos_bottom
        this.show = true
      })

      $bus.$on('hideCustomerService', () => {
        this.show = false
      })
    },

    data () {
      return {
        show: false,
        pos_bottom: 0
      }
    },

    computed: {
      ...mapState('taskDetail', [
        'show_coupon'
      ])
    },

    methods: {
      // 试玩详情客服
      talkToCustomerService () {
        window.location.href = getCustomerServiceLink()
      },

      kameiMove () {
        return this.show_coupon && $router && $router.currentRoute.name === 'task_detail'
      }
    }
  }
</script>



