// 收徒任务首页
const Index = r => require.ensure([], () => r(require('./home/Index.vue')), 'apprentice')

// 我的收徒：
const Achievements = r => require.ensure([], () => r(require('./achievements/Index.vue')), 'achievements')

// 我的收徒：奖励
const Income = r => require.ensure([], () => r(require('./achievements/Income.vue')), 'achievements-income')

// 我的收徒: 徒弟/徒孙列表
const List = r => require.ensure([], () => r(require('./achievements/List.vue')), 'achievements-list')

// 弹层测试页
// const TestPopups = r => require(['./test/TestPopups.vue'], r)

export default [
  { path: '/apprentice', name: 'apprentice', component: Index },
  { path: '/apprentice/achievements',
    name: 'apprentice_achievements',
    component: Achievements,
    children: [
      {
        path: 'income',
        name: 'apprentice_achievements_income',
        component: Income
      },
      {
        path: 'list/:type',
        name: 'apprentice_achievements_list',
        component: List,
        props: true
      }
    ]
  }
  // ,
  // { path: '/apprentice/test/popups', name: 'apprentice_test_popups', component: TestPopups }
]



