<template>
  <div class="page">
    <AnimateHeader
    bg-color="#f5f5f5"
    menu-bg-color="#f0f0f0"
    color="#3A3A3A"
    title="个人中心"
    :on-back-button-click="back"
    :expanded="false"
    :animate="false"
    >
    </AnimateHeader>
    <div class="page-content user-info">
      <div class="user-account">
        <div class="my-account">我的资产</div>
        <div class="avatar" @click="$router.forward('/user/profile')"><img :src="user_info.avatar_url" alt=""></div>
        <div class="income"><span>{{user_info.my_total_income}}</span><img src="//qianka.b0.upaiyun.com/images/a91173004e3f0282514595e85b66bb04.png" alt=""></div>
        <div class="widthdraw-progress" @click="$router.forward('/withdraw/process/kabi')"><span>有 </span><span :class="{'assertive': user_info.withdraw_count > 0}">{{user_info.withdraw_count}}</span><span> 笔提现</span><img src="//qianka.b0.upaiyun.com/images/f314ecace76ee8968a807ebe5abc2b6e.png" alt=""></div>
        <div class="division"></div>
      </div>
      <div class="withdraw">
        <div class="withdraw-item">
          <div class="label">咖币 <span class="assertive account-income">{{user_info.balance}}</span></div>
          <div class="rate">1咖币=1元</div>
          <div class="withdraw-button button-assertive" @click="withdraw()">立即提现</div>
        </div>
        <div class="withdraw-item jfb-item">
          <div class="label">集分宝 <span class="assertive account-income">{{user_info.jfb_account.surplus}}</span></div>
          <div class="rate">100集分宝=1元</div>
          <img v-if="user_info.jfb_account.surplus > 0" class="spot" src="//assets.qkcdn.com/images/1ac8c4bd8250c4c9c36bc8330b951d0c.png" alt="">
          <div class="jfb-button" @click="withdraw('jifen')">立即兑换</div>
        </div>
      </div>
      <div class="account-related">
        <div class="related">账户相关</div>
        <div class="action-type">
          <div class="action-button text-center" @click="$router.forward('/user/accountDetail')">
            <img src="//qianka.b0.upaiyun.com/images/953d0a3a163c5a49296e773a27ef4db3.png" alt="">
            <div>账户明细</div>
          </div>

          <div class="action-button text-center" @click="$router.forward('/user/profile')">
            <img src="//qianka.b0.upaiyun.com/images/1a455c8e5c7f6938902d2a46ff0b50c8.png" alt="">
            <div>账户设置</div>
          </div>

          <div class="action-button text-center" @click="$router.forward('/help')">
            <img src="//qianka.b0.upaiyun.com/images/d75552af1f193e0c5bf4d5215e8861c3.png" alt="">
            <div>帮助中心</div>
          </div>

          <div class="action-button text-center" @click="$router.forward('/about')">
            <img src="//qianka.b0.upaiyun.com/images/70cd54efbca52eb00d8ce0af758bb855.png" alt="">
            <div>关于钱咖</div>
          </div>
        </div>

        <div class="action-type">
          <!--<div class="action-button text-center" v-if="this.user_info.show_duobao" @click="duobao()">
            <img src="//qianka.b0.upaiyun.com/images/dd74b45dee0f84f8ba20e2d36addc33e.png" alt="">
            <div>一元夺宝</div>
          </div>-->
          <div class="action-button text-center"  @click="$router.forward('/entrance')">
            <img src="//assets.qkcdn.com/images/b199218cadc5a35fe9b0649897cc2b83.png" alt="">
            <div>钱咖入口</div>
          </div>
          <div class="action-button text-center" @click="logout()">
            <img src="//qianka.b0.upaiyun.com/images/318612375ecfa16000ef6e525fccb69f.png" alt="">
            <div>切换帐号</div>
          </div>
          <div class="action-button text-center" @click="$router.forward('/rebate/orderlist/daifan')">
            <img src="//assets.qkcdn.com/images/b71907e28862ad98da3984aa40a3d233.png" alt="">
            <div>返利订单</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .user-info {
    margin-top: 50px;
    background: #FFF;

    .user-account {
      position: relative;
      height: 130px;
      margin-bottom: 20px;

      .my-account {
        position: absolute;
        top: 24px;
        left: 20px;
        font-size: 20px;
        color: #DDDDDD;
        height: 28px;
        line-height: 28px;

        @include font-regular();
      }

      .avatar {
        position: absolute;
        right: 24px;
        top: 24px;

        img {
          width: 44px;
          height: 44px;
          border-radius: 50%;
        }
      }

      .income {
        position: absolute;
        top: 56px;
        left: 20px;

        span {
          font-family: giorgio;
          font-size: 64px;
          color: #3A3A3A;
          line-height: 64px;
          height: 64px;
          margin-right: 5px;
        }

        img {
          width: 19px;
          height: 21px;
        }
      }

      .widthdraw-progress {
        position: absolute;
        height: 18px;
        line-height: 18px;
        font-size: 13px;
        top: 98px;
        right: 20px;

        @include font-regular();

        span {
          vertical-align: middle;
        }

        img {
          width: 4px;
          height: 6px;
          margin-left: 5px;
          vertical-align: middle;
        }
      }

      .division {
        top: 129px;
        left: 20px;
        border-top: 1px solid #3A3A3A;
        position: relative;
        width: 335px;

        &::before, &::after {
          width: 0px;
          height: 0px;
          content: '';
          position: absolute;
          border: transparent solid;
          top: -10px;
          left: 16px;
        }

        &::before {
          border-width: 5px;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-bottom: 4px solid #000;
        }

        &::after {
          top: -9px;
          border-width: 5px;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-bottom: 4px solid #fff;
        }
      }
    }

    .withdraw {
      margin-bottom: 44px;

      .withdraw-item {
        position: relative;
        height: 40px;
        padding-left: 24px;
        margin-bottom: 20px;

        &.account-income {
          @include font-semibold();
        }

        .label {
          font-size: 16px;
          color: #3A3A3A;
          margin-bottom: 3px;

          @include font-regular();
        }

        .rate {
          font-size: 12px;
          height: 17px;
          line-height: 17px;
          color: #B5B5B5;

          @include font-regular();
        }

        .withdraw-button {
          position: absolute;
          text-align: center;
          top: 5px;
          right: 24px;
          width: 80px;
          height: 32px;
          border-radius: 100px;
          background: #EF4F2A;
          line-height: 33px;
          font-size: 13px;
          color: #FFFFFF;

          @include font-semibold();
        }

        .spot {
          position: absolute;
          top: 1px;
          right: 26px;
          width: 13px;
          height: 13px;
          z-index: 10;
        }

        .jfb-button {
          position: absolute;
          text-align: center;
          top: 5px;
          right: 24px;
          width: 80px;
          height: 32px;
          border-radius: 100px;
          border: solid 1px #3a3a3a;
          background-color: #ffffff;
          line-height: 31px;
          font-size: 13px;
          color: #3a3a3a;
          border-color: #3a3a3a;

          @include font-semibold();

          &:active {
            color: #FFFFFF;
            background-color: #3a3a3a;
          }
        }

        .withdraw-jfb-button {
          position: absolute;
          top: 3px;
          right: 24px;
          min-height: 32px;
          height: 32px;
          line-height: 32px;
          font-size: 13px;
          color: #3A3A3A;
          background: #FFFFFF;
          border: 1px solid #3A3A3A;
          border-radius: 100px;
        }
      }
    }

    .account-related {
      .related {
        font-size: 20px;
        height: 28px;
        line-height: 28px;
        color: #DDDDDD;
        margin-left: 20px;
        margin-bottom: 24px;

        @include font-regular();
      }

      .action-type {
        padding: 0 10px 0 10px;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 30px;

        .action-button {
          flex-grow: 1;
          max-width: 25%;
          font-size: 12px;

          @include font-regular();

          img {
            width: 32px;
            height: 30px;
            margin-bottom: -4px;
          }

          div {
            height: 17px;
            line-height: 17px;
          }
        }
      }
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import {mapState, mapActions} from 'vuex'
  import {popupLogoutUnbind} from '../popup.js'

  export default {
    components: {
      AnimateHeader
    },

    computed: {
      ...mapState('user', [
        'user_info'
      ])
    },

    mounted () {
      this.fetch()
    },

    methods: {
      // duobao () {
      //   location.href = this.user_info.show_duobao
      // },

      withdraw (type) {
        if (type === 'jifen') {
          $router.forward({
            path: '/withdraw/rebatescore'
          })
          return
        }
        $router.forward({
          path: '/withdraw/'
        })
      },

      logout () {
        if (this.user_info.mobile_bind) { // 已绑定手机
          $router.forward({
            path: '/login',
            query: { backroute: '/user' }
          })
        } else { // 未绑定手机
          let p = popupLogoutUnbind
          let popup = $popup.fromTemplate(p.template, p.options)
          popup.show().then((btn) => {
            if (btn === 0) { // 绑定手机
              $router.forward({
                path: '/user/bindMobile/0',
                query: {backroute: '/user'}
              })
            } else if (btn === 1) { // 切换账号
              $router.forward({
                path: '/login',
                query: { backroute: '/user' }
              })
            }
          })
        }
      },

      back () {
        $router.back('/dashboard')
      },

      ...mapActions('user', [
        'fetch'
      ])
    }
  }
</script>



