import {
  ASSISTANT_STATUS_ON,
  ASSISTANT_STATUS_OFF,
  KEY_LAST_USER_CONNECT_KEY,
  COOKIE_KEY_LAST_UID
} from 'constants'

import {channel, events} from './channel'
import {appendUrlParams} from 'utils'

const types = {
  UPDATE_INFO: 'update_info',
  UPDATE_STATUS: 'update_status'
}

const state = {
  status: 1, // 助手状态 0: 助手未开启

  info: {
    app_type: 1, // 助手类型 1: AppStore, 2: AppStore VIP, 3: 企业版
    app_name: '',
    app_icon: '',
    app_developer: '',
    app_scheme: '',
    app_status: 1, // 助手在应用商店的状态 1: 在线, 2: 已失效
    download_url: '' // 助手下载地址
  }
}

const mutations = {
  [types.UPDATE_INFO] (state, payload) {
    _.extend(state.info, _.pick(payload, _.keys(state.info)))
  },

  [types.UPDATE_STATUS] (state, status) {
    // 每次与钥匙连通时, 记录用户id 到 localStorage
    // 老用户登录时也要做这件事情
    if (status === ASSISTANT_STATUS_ON) {
      $storage.set(KEY_LAST_USER_CONNECT_KEY, $cookie.get(COOKIE_KEY_LAST_UID))
    }
    state.status = status
  }
}

// 在助手未开启的情况下，判断当前用户是否为新用户
const is_newbie = () => {
  // 如果之前和钥匙联通后保存在 localStorage 中的 uid 和 cookie 中的 last_uid 一样，则为老用户
  let last_uid = $storage.get(KEY_LAST_USER_CONNECT_KEY)
  if (last_uid && last_uid === $cookie.get(COOKIE_KEY_LAST_UID)) {
    return false
  }

  // 或者当 cookie 里存在 uid 时, 则为老用户
  let cookie_uid = $cookie.get('uid')
  if (cookie_uid) {
    $storage.set(KEY_LAST_USER_CONNECT_KEY, $cookie.get(COOKIE_KEY_LAST_UID))
    return false
  }

  return true
}

const getters = {
  // 是否显示助手未开启弹层
  showAssistantUnopenPopup (state) {
    return state.status === ASSISTANT_STATUS_OFF && !is_newbie() &&
      state.info.app_status === 1
  },

  // 是否显示助手已失效弹层
  showAssistantInvalidPopup (state) {
    return state.status === ASSISTANT_STATUS_OFF && !is_newbie() &&
      state.info.app_status === 2
  },

  isNewbie () {
    return is_newbie()
  }
}

const actions = {
  alive ({ state, commit }) { // 检查助手是否开启，如没有开启，则开启轮询
    return $assistant.alive().then(() => {
      commit(types.UPDATE_STATUS, ASSISTANT_STATUS_ON)
      return Promise.resolve()
    }, err => { // eslint-disable-line
      // 助手未开，开始轮询助手
      $assistant.poll()

      return $api.assistantInfo()
        .then(res => res.data && res.data.payload)
        .then(payload => {
          // 此时为了一次决定展现的弹层类型 assistant.status 和 assistant.info 需要同时更新
          commit(types.UPDATE_INFO, payload)
          commit(types.UPDATE_STATUS, ASSISTANT_STATUS_OFF)
          return Promise.reject()
        })
    })
  },

  open ({ state }) { // 开启助手
    let scheme = state.info.app_scheme
    if (scheme) location.href = scheme + 'openURL?type=outside&url=' + encodeURIComponent(location.href)
  },

  download ({ state }) { // 下载助手
    let download_url = state.info.download_url
    if (download_url) {
      location.href = appendUrlParams(download_url, {
        'return_url': location.href
      })
    }
  },

  getInfo ({ commit }) {
    return $api.assistantInfo().then(payload => {
      commit(types.UPDATE_INFO, payload)
    })
  },

  syncAssistantUnopen ({ state }) {
    channel.$emit(events.POPUP_ASSISTANT_UNOPEN, {
      showAssistantUnopenPopup: state.status === ASSISTANT_STATUS_OFF &&
        !is_newbie() && state.info.app_status === 1
    })
  },

  syncAssistantInvalid ({ state }) {
    channel.$emit(events.POPUP_ASSISTANT_INVALID, {
      showAssistantInvalidPopup: state.status === ASSISTANT_STATUS_OFF &&
        !is_newbie() && state.info.app_status === 2
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



