const types = {
  SYNC: 'sync',
  SYNC_APPLY_LISTS: 'sync_apply_Lists',
  SYNC_JFB: 'sync_jfb'
}

const getters = {
  alipayInfo ({info}) {
    let userInfo = info.alipay.user_info || {}
    let alipayMapping = []
    let realPricesHash = []
    let amountHash = []
    let mapping = info.alipay.mapping
    console.log('info.alipay.mapping:getters', mapping)
    _.each(mapping, (v, k) => {
      realPricesHash.push(v)
      amountHash.push(k)
      let tips = ''
      alipayMapping.push({
        title: `提现${k}元`,
        desc: tips
      })
    })
    let alipayInfo = {
      enabled: info.alipay.enabled,
      account: userInfo.object_number,
      realName: userInfo.real_name,
      withdrawLists: alipayMapping,
      realPricesHash,
      amountHash
    }
    return alipayInfo
  },

  wechatInfo ({info}) {
    let wechat = info.wechat
    let mapping = wechat.mapping
    // 自动填充 姓名
    let real_name = wechat.user_info && wechat.user_info.real_name
    let wechatOptions = []
    let realprices = []
    let amount = []
    let cnt = 0
    let counter = [0, 1, 2, 5]
    _.each(mapping, (v, k) => {
      realprices.push(k)
      let tips = counter[cnt] === 0 ? '' : `送筹码${counter[cnt]}枚`
      wechatOptions.push({
        title: `提现${k}元`,
        desc: tips
      })
      amount.push(k)
      cnt++
    })

    // 大额
    let mappingLarge = info.wechat_large.mapping
    let wechatBigOptions = []
    let realpricesLarge = []
    let amountLarge = []
    let cntLarge = 0
    let counterLarge = [25, 50, 100]
    _.each(mappingLarge, (v, k) => {
      realpricesLarge.push(v)
      let tips = counterLarge[cntLarge] === 0 ? '' : `优惠${counterLarge[cntLarge]}咖币`
      wechatBigOptions.push({
        title: `提现${k}元`,
        desc: tips
      })
      amountLarge.push(k)
      cntLarge++
    })

    return {
      real_name,
      wechatOptions,
      realprices,
      amount,
      wechatBigOptions,
      realpricesLarge,
      amountLarge
    }
  },

  mobileInfo ({info}) {
    let mobile = info.mobile
    let mapping = mobile.mapping
    let phone = mobile.user_info && mobile.user_info.object_number
    let phoneOptions = []
    let realprices = []
    let amount = []
    let cnt = 0
    let counter = [0, 1, 2, 5]
    _.each(mapping, (v, k) => {
      realprices.push(k)
      let tips = counter[cnt] === 0 ? '' : `送筹码${counter[cnt]}枚`
      phoneOptions.push({
        title: '充' + k + '元',
        desc: tips
      })
      amount.push(k)
      cnt++
    })
    return {
      mobile: phone,
      phoneOptions,
      realprices,
      amount
    }
  },

  jfbInfo ({jfbInfo}) {
    let mapping = jfbInfo.mapping
    let balance = jfbInfo.total_jfb
    let account = jfbInfo.user_info && jfbInfo.user_info.object_number
    let real_name = jfbInfo.user_info && jfbInfo.user_info.real_name
    let realprices = []
    let amount = []
    let rebateScoreOptions = []
    _.each(mapping, (v, k) => {
      realprices.push(mapping[k])
      rebateScoreOptions.push({
        title: `${k}集分宝`,
        desc: `等值${v}元`
      })
      amount.push(k)
    })

    return {
      balance,
      account,
      real_name,
      rebateScoreOptions,
      realprices,
      amount,
      enabled: jfbInfo.enabled,
      mobile_bind: jfbInfo.mobile_bind
    }
  }
}

const initState = () => {
  return {
    info: {
      balance: '',
      mobile_bind: 0,
      alipay: {},
      wechat: {},
      mobile: {},
      wechat_large: {}
    },
    jfbInfo: {},
    applyLists: []
  }
}

const state = initState()

const mutations = {
  [types.SYNC] ({info}, payload) {
    console.log('mutations:' + types.SYNC, payload)
    info.alipay = payload.alipay
    console.log('info.alipay:', info.alipay)
    let mapping = info.alipay.mapping
    for (let key in mapping) {
      console.log(key)
      let val = mapping[key]
      if (isNaN(val)) {
        delete mapping[key]
      }
    }
    info.wechat = payload.wechat
    info.wechat_large = payload.wechat_large
    info.mobile = payload.mobile
    info.balance = payload.balance
    info.mobile_bind = payload.mobile_bind
  },

  [types.SYNC_APPLY_LISTS] (state, payload) {
    state.applyLists = payload
    console.log('mutations:' + types.SYNC_APPLY_LISTS, payload)
  },

  [types.SYNC_JFB] (state, payload) {
    state.jfbInfo = payload
  }
}

const actions = {
  getWithdrawList ({commit}, payload) {
    return $api.withdrawList()
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC, payload)
      })
  },

  submitMobile ({commit}, payload) {
    return $api
      .withdrawToMobile(payload)
  },

  submitWechat ({commit}, payload) {
    return $api.withdrawToWechat(payload)
  },

  submitAlipay ({commit}, payload) {
    return $api.withdrawToAlipay(payload)
  },

  getJfbWithdrawList ({commit}, payload) {
    return $api.jifenbaoWithdraw()
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC_JFB, payload)
      })
  },

  submitJfb ({commit}, payload) {
    return $api.withdrawJfb(payload)
  },

  WithdrawApplyListAll ({commit}) {
    return $api.withdrawApplyListAll()
  },

  withdrawApplyList ({commit}, params) {
    return $api.withdrawApplyList(params.type, params.offset, params.limit)
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



