<!-- 进行中的任务列表 -->
<template>
  <div>
    <ActionList v-if="show_push || ongoing.length > 0">
      <!-- 开启推送提醒 -->
      <Action v-if="show_push" @click.native="showPushPopup">
        <div slot="icon" class="icon">
          <img src="//qianka.b0.upaiyun.com/images/56cdf6513974f01557184e2d5a15cdce.png">
        </div>

        <span slot="desc">
          <span class="push-tips">增加任务抢夺成功率</span>
        </span>
        <div slot="reward-wrap" class="reward-content">
          <span class="plus">+</span>
          <span class="reward">40</span>
          <span class="yuan">%</span>
        </div>
      </Action>

      <Action v-if="ongoing.length > 0" v-for="task in ongoing" :key="task.task_id" @click.native="start(task)">
        <div slot="icon" class="icon">
          <img :src="task.icon">
        </div>

        <span slot="app-name" v-text="task.title"></span>
        <span slot="desc" class="count-down-box">
          <span class="ongoing">进行中...</span>
        </span>
        <div slot="reward-wrap" class="reward-content">
          <span class="plus">+</span>
          <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
          <span class="yuan">元</span>
        </div>
      </Action>
    </ActionList>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  import {popup_enable_push} from '../popups/popups'

  export default {
    components: {
      Action,
      ActionList
    },

    computed: {
      ...mapState('task', [
        'ongoing',
        'show_push'
      ])
    },

    mounted () {
      console.log(this.ongoing)
    },

    methods: {
      start (task) {
        switch (task.type) {
          case 1:
            $router.forward(`/task/detail/${task.id}`)
            break
          case 2:
            $router.forward(`/easy/detail/${task.id}`)
            break
        }
      },

      showPushPopup () {
        $assistant.alive().then((res) => {
          let popup = $popup.fromTemplate(
            popup_enable_push.template,
            popup_enable_push.options
          )

          popup.show().then(buttonIndex => {
            if (buttonIndex === 0) {
              let pl = res.data && res.data.payload
              console.log(pl.scheme + '://notification.appsetting')
              location.href = pl.scheme + '://notification.appsetting'
            }
          })
        })
      }
    }
  }
</script>



