<!-- 列表页头部 - 带 subheader -->
<template>
  <div class="animate-header-wrapper">
    <div class="animate-header"
      :style="{ height: state.headerHeight + 'px' }"
    >
      <div v-if="showBackButton"
        class="menu-item menu-item-left"
        :style="{
          backgroundColor: menuBgColor
        }"
        @click="onBack()"
      >
        <BackArrow :color="color" />
      </div>

      <div class="header-content" :style="{
        backgroundColor: bgColor
      }">
        <slot name="content">
          <h4 class="light"
            :style="{
              marginTop: state.titleMarginTop + 'px',
              fontSize: state.titleFontSize + 'px',
              lineHeight: state.titleLineHeight + 'px',
              color: color
            }" v-text="title"></h4>
          <p class="light"
            :style="{
              opacity: state.subTitleOpacity,
              color: color
            }" v-html="subTitle"
          ></p>
        </slot>
      </div>

      <div v-if="showMenuButton"
        class="menu-item menu-item-right"
        :style="{
          backgroundColor: menuBgColor
        }"
        @click="onMenu()"
      >
        <slot name="menu">
          <Close :color="color"/>
        </slot>
      </div>
    </div>

    <div v-if="hasSubHeader" :style="{marginTop: (state.headerHeight + 60) + 'px' }">
      <slot name="subheader"></slot>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  @import "~scss/mixins";

  .animate-header-wrapper {
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 8;

    .animate-header {
      position: fixed;
      top: 0px;
      width: 100%;
      display: flex;

      .menu-item {
        width: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
      }

      .menu-item-left {
        order: 1;
      }

      .menu-item-right {
        order: 3;
      }

      .header-content {
        order: 2;
        flex: 1;
        justify-content: flex-start;

        h4, p {
          padding: 0 20px;
        }

        h4 {
          font-size: 20px;
          line-height: 28px;
          margin: 21px 0 9px 0;

          @include font-medium();
        }

        p {
          font-size: 13px;
          line-height: 18px;
          margin: 0;

          @include font-regular();
        }
      }
    }

    .sub-header {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 60px;
    }
  }
</style>

<script>
  import BackArrow from './BackArrow.vue'
  import Close from './Close.vue'
  import {tween} from 'shifty'

  const HEADER_HEIGHT_MAX = 100
  const HEADER_HEIGHT_MIN = 50
  const TITLE_MARGIN_TOP_MAX = 21
  const TITLE_MARGIN_TOP_MIN = 14
  const TITLE_FONT_SIZE_MAX = 20
  const TITLE_FONT_SIZE_MIN = 16
  const TITLE_LINE_HEIGHT_MAX = 28
  const TITLE_LINE_HEIGHT_MIN = 22
  const DURATION = 150
  const EFFECT = 'easeOutQuad'

  const EXPANDED = {
    headerHeight: HEADER_HEIGHT_MAX,
    titleMarginTop: TITLE_MARGIN_TOP_MAX,
    titleFontSize: TITLE_FONT_SIZE_MAX,
    titleLineHeight: TITLE_LINE_HEIGHT_MAX,
    subTitleOpacity: 1,
    fixed: false
  }

  const SHRINKED = {
    headerHeight: HEADER_HEIGHT_MIN,
    titleMarginTop: TITLE_MARGIN_TOP_MIN,
    titleFontSize: TITLE_FONT_SIZE_MIN,
    titleLineHeight: TITLE_LINE_HEIGHT_MIN,
    subTitleOpacity: 0,
    fixed: true
  }

  const initState = (expanded) => {
    return expanded ? EXPANDED : SHRINKED
  }

  export default {
    components: {
      BackArrow,
      Close
    },

    props: {
      title: String,
      subTitle: String,
      // 背景色
      bgColor: String,
      // 两端按钮背景色
      menuBgColor: String,
      // 文字颜色
      color: {
        type: String,
        default: '#FFF'
      },
      showBackButton: {
        type: Boolean,
        default: true
      },
      onBackButtonClick: Function,
      showMenuButton: {
        type: Boolean,
        default: false
      },
      onMenuButtonClick: Function,
      animateDuration: Number,
      animate: {
        type: Boolean,
        default: true
      },
      expanded: {
        type: Boolean,
        default: false
      },
      getScrollTop: Function,
      hasSubHeader: Boolean
    },

    mounted () {
      if (!this.animate) return
      this.timer = setInterval(() => {
        let scrollTop = this.getScrollTop
          ? this.getScrollTop()
          : Vonic.app.pageContentScrollTop()

        if (scrollTop > HEADER_HEIGHT_MAX && !this.shrinked) {
          this.shrink()
        }

        if (scrollTop < HEADER_HEIGHT_MAX && this.shrinked) {
          this.expand()
        }
      }, 50)
    },

    beforeDestroy () {
      if (this.timer) clearInterval(this.timer)
    },

    data () {
      return {
        state: initState(this.expanded),
        shrinked: !this.animate,
        timer: undefined
      }
    },

    methods: {
      onBack () {
        if (this.onBackButtonClick) this.onBackButtonClick()
      },

      onMenu () {
        if (this.onMenuButtonClick) this.onMenuButtonClick()
      },

      _shift (reverse) {
        let options = {
          from: EXPANDED,
          to: SHRINKED,
          duration: this.animateDuration || DURATION,
          easing: EFFECT,
          step: state => {
            this.state = state
          }
        }

        reverse
          ? _.extend(options, { from: SHRINKED, to: EXPANDED })
          : void 666

        tween(options).then(state => {
          this.state = state
        })
      },

      shrink () {
        this._shift()
        this.shrinked = true
      },

      expand () {
        this._shift(true)
        this.shrinked = false
      },

      isShrinked () {
        return this.shrinked
      }
    }
  }
</script>



