/* eslint no-unused-vars: "off" */
import Vuex from 'vuex'
// dashboard页
import dashboard from './dashboard'
// 助手
import assistant from './assistant'
// 试玩任务列表
import task from './task'
// 试玩任务详情
import taskDetail from './taskDetail'
// 专属任务
import zstask from './zstask'
// 简易任务
import easy from './easy'
// 用户中心
import user from './user'

import help from './help'

import withdraw from './withdraw'
// 闪电借钱
import loan from './loan'

import {KEY_UID} from 'constants'

// channel
import {channel} from './channel'

// global store
const types = {
  SHOW_INSTALL_CERTIFICATION_POPUP: 'show_install_certification_popup',
  HIDE_INSTALL_CERTIFICATION_POPUP: 'hide_install_certification_popup'
}

const state = {
  showInstallCertificationPopup: false
}

const getters = {
  uid: state => {
    return $storage.get(KEY_UID)
  }
}

const mutations = {
  [types.SHOW_INSTALL_CERTIFICATION_POPUP] (state) {
    state.showInstallCertificationPopup = true
  },

  [types.HIDE_INSTALL_CERTIFICATION_POPUP] (state) {
    state.showInstallCertificationPopup = false
  }
}

const actions = {
  showInstallCertificationPopup ({commit}) {
    commit(types.SHOW_INSTALL_CERTIFICATION_POPUP)
  },

  hideInstallCertificationPopup ({commit}) {
    commit(types.HIDE_INSTALL_CERTIFICATION_POPUP)
  }
}

const store = new Vuex.Store({
  modules: {
    dashboard,
    assistant,
    task,
    taskDetail,
    zstask,
    easy,
    user,
    help,
    withdraw,
    loan
  },
  state,
  getters,
  mutations,
  actions
})

store.on = store.$on = channel.$on
store.emit = store.$emit = channel.$emit

export default store



