// 信用卡任务列表页
const CreditList = r => require.ensure([], () => r(require('./list/Index')), 'credit-list')

// 信用卡详情页
const CreditDetail = r => require.ensure([], () => r(require('./CreditDetail')), 'credit-detail')

export default [
  { path: '/credit/:type', name: 'credit', component: CreditList },
  {
    path: '/credit/detail/:id',
    name: 'credit_detail',
    props: true,
    component: CreditDetail
  }
]



