import {channel, events} from './channel'
import {
  ERR_CERTIFICATION,
  ERR_JAILBREAKER,
  ERR_UNBINDPHONE,
  ERR_ACCOUNTFROZEN
} from 'constants'

// 简易任务详情
const initState = () => ({
  detail: {
    app_name: '',
    icon: '',
    reward: '',
    bundle_id: '',
    deadline: 0,
    download_url: '',
    task_id: 0,
    callback_url: ''
  }
})

const state = initState()

const types = {
  INIT: 'init',
  SYNC: 'sync'
}

const mutations = {
  [types.INIT] (state) {
    _.extend(state.detail, initState())
  },

  [types.SYNC] (state, payload) {
    _.extend(state.detail, _.pick(payload, _.keys(state.detail)))
  }
}

const getters = {

}

const actions = {
  fetch ({ commit }, task_id) {
    commit(types.INIT)

    return $api.simpletaskDetail(task_id)
      .then(res => res.data && res.data.payload)
      .then(payload => {
        if (payload.status !== 2) { // 任务未完成或不在进行中
          return $router.back('/tasks')
        }

        commit(types.SYNC, payload)
        return Promise.resolve(payload)
      })
      .catch(err => {
        return Promise.reject(err)
      })
  },

  start ({commit}, task_id) {
    return $api
      .simpletaskStart(task_id)
      .then(res => res.data)
      .then(data => {
        if (data.err_code === 0) {
          return Promise.resolve(data.payload)
        } else {
          switch (data.err_code) {
            case ERR_CERTIFICATION:
              commit('show_install_certification_popup', null, {root: true})
              break
            case ERR_JAILBREAKER:
              $loading.hide()
              channel.$emit(events.POPUP_JAIL_BREAKER)
              break
            case ERR_UNBINDPHONE:
              $loading.hide()
              channel.$emit(events.POPUP_BIND_PHONE)
              break
            case ERR_ACCOUNTFROZEN:
              $loading.hide()
              channel.$emit(events.POPUP_USER_FREEZE)
          }
          return Promise.reject()
        }
      })
  },

  check (ctx, task_id) {
    return $api.simpletaskState(task_id)
      .then(res => res.data && res.data.payload)
  },

  giveup (ctx, task_id) {
    return $api.simpletaskGiveup(task_id)
      .then(res => res.data && res.data.payload)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



