const Rebate = r => require.ensure([], () => r(require('./Rebate')), 'rebate')

const OrderList = r => require.ensure([], () => r(require('./orderlist/Index')), 'rebate-orderlist')

const AskForHelp = r => require.ensure([], () => r(require('./AskForHelp/Index.vue')), 'rebate-askforhelp')

const RebateGuide = r => require.ensure([], () => r(require('./RebateGuide')), 'rebate-guide')

const Appeal = r => require.ensure([], () => r(require('./appeal/Index.vue')), 'rebate-appeal')

const JumpLoading = r => require.ensure([], () => r(require('./JumpLoading')), 'rebate-jumploading')

export default [
  { path: '/rebate', name: 'rebate', component: Rebate },
  { path: '/rebate/orderlist/:type', name: 'rebate_orderlist', component: OrderList },
  { path: '/rebate/help', name: 'rebate_help', component: AskForHelp },
  { path: '/rebate/guide', name: 'rebate_guide', component: RebateGuide },
  { path: '/rebate/appeal', name: 'rebate_appeal', component: Appeal },

  // { path: '/rebate/test/popup', name: 'rebate_test_popup', component: RebatePopup },

  // 用于过审的页面
  { path: '/fanli', name: 'fanli', component: Rebate },
  { path: '/fanli/orderlist/:type', name: 'fanli_orderlist', component: OrderList },
  { path: '/fanli/help', name: 'fanli_help', component: AskForHelp },
  { path: '/fanli/guide', name: 'fanli_guide', component: RebateGuide },

  // 跳转淘宝loading页面，分两种路由
  { path: '/rebate/loading/:goodsName/:keyId', name: 'loading', component: JumpLoading },
  { path: '/rebate/loading/:goodsName', name: 'loading', component: JumpLoading },
  { path: '/fanli/loading/:goodsName', name: 'loading', component: JumpLoading }
]



