<!-- 新人专享 & 标准任务 -->
<template>
  <div>
    <!-- 新人专享 -->
    <ActionList v-if="newbie.length > 0" title="新人专享" :subtitle="newbieTips">
      <Action v-for="task in newbie" :key="task.task_id" @click.native="start(task)">
        <!-- 任务图标 -->
        <div class="icon" slot="icon">
          <img :src="task.icon">
        </div>

        <!-- 任务标题 -->
        <span v-inview:enter="taskEnterView" v-bind:data-id="task.id" slot="app-name" v-text="task.title"></span>
        <!-- 任务标签 -->
        <Tags slot="desc" :tags="task.tags"></Tags>
        <!-- 任务奖励金额 -->
        <div slot="reward-wrap" class="reward-content">
          <span class="plus">+</span>
          <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
          <span class="yuan">元</span>
        </div>
      </Action>
    </ActionList>

    <ActionList title="标准任务" :subtitle="tasksTips" v-if="tasks.length>0">
      <Action v-for="task in tasks" :key="task.task_id" @click.native="start(task)">
        <!-- 任务图标 -->
        <div class="icon" slot="icon">
          <img :src="task.icon">
        </div>

        <!-- 任务标题 -->
        <span v-inview:enter="taskEnterView" v-bind:data-id="task.id" slot="app-name" v-text="task.title"></span>
        <!-- 任务标签 -->
        <Tags slot="desc" :tags="task.tags"></Tags>
        <!-- 任务奖励金额 -->
        <div slot="reward-wrap" class="reward-content" v-if="!task.portalName">
          <span class="plus">+</span>
          <span class="reward" v-text="parseFloat(task.reward).toFixed(1)"></span>
          <span class="yuan">元</span>
        </div>
        <div slot="reward-wrap" class="reward-content" v-if="task.portalName">
          <span class="plus">+</span>
          <span class="reward" v-text="task.portalName === 'shoutu' ? '10' : '2000'"></span>
          <span class="yuan" v-text="task.portalName === 'shoutu' ? '元' : '元/月'"></span>
        </div>
      </Action>
    </ActionList>
  </div>
</template>
<script>
  import Vue from 'vue'
  import {mapState, mapGetters, mapActions} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  import Tags from '../components/actions/Tags.vue'
  import downloadAssistantPopupMixin from './downloadAssistantPopupMixin'
  import { popup_has_task_ongoing } from '../popups/popups.js'
  import { crius } from 'utils'
  import Inview from 'vueinview'
  import { KEY_CRIUS_ALREADY_REPORT_SUBTASK_VIEW } from '../../../constants'

  Vue.use(Inview)

  // 需要上报的任务id
  let WILL_REPORT_TASK_LIST = []

  export default {
    components: {
      Action,
      ActionList,
      Tags
    },

    data () {
      return {

      }
    },
    updated () {
      // 因为in-view在update之后, 所以延时1秒执行第一次上报
      setTimeout(() => {
        // 第一次页面渲染完成上报
        this.reportSubtaskToCrius()
      }, 1000)
    },

    computed: {
      ...mapState('task', [
        'newbie',
        'tasks',
        'ongoing',
        'has_tasks'
      ]),

      ...mapGetters('task', [
        'newbieTips',
        'tasksTips'
      ]),

      ...mapGetters('assistant', [
        'isNewbie'
      ]),
      ...mapState('assistant', {
        'assistant_alive': 'status'
      })
    },

    mixins: [downloadAssistantPopupMixin],

    beforeMount () {
      console.log('beforeMount')
      // 注册touchend事件
      document.body.addEventListener('touchend', this.reportSubtaskToCrius)
    },

    beforeDestroy () {
      console.log('beforeDestroy')

      // Destroy前做一次上报
      this.reportSubtaskToCrius()
    },

    destroyed () {
      console.log('destroyed')
      // 注销touchend事件
      document.body.removeEventListener('touchend', this.reportSubtaskToCrius)
    },

    methods: {
      // 今天日期格式
      today () {
        let d = new Date()
        let tday = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate()
        return tday
      },
      // 从storage中获取已经上报的任务id列表
      getAlreadyReportTasks () {
        let data = $storage.get(KEY_CRIUS_ALREADY_REPORT_SUBTASK_VIEW)
        let payload = {}
        let viewList = []
        if (data && data.trim() !== '') {
          payload = JSON.parse(data)
        }
        console.log(data, payload)

        let day = this.today()
        // 检查cache中数据有效性
        if (!payload || !payload.data || !Array.isArray(payload.data)) {
          console.log('cache中数据有效性')
          return viewList
        }
        // 如果日期不是当天，则要清除缓存重新记录
        if (!payload.day || payload.day !== day) {
          this.clearAlreadyReportTasks()
          console.log('cache不是当天')
          return viewList
        }
        // 返回缓存中的任务id列表
        viewList = payload.data

        return viewList
      },
      // 把任务id列表保存到缓存中， 并标记为当天
      setAlreadyReportTasks (tasks) {
        let day = this.today()
        let payload = {}
        payload.data = tasks
        payload.day = day
        $storage.set(KEY_CRIUS_ALREADY_REPORT_SUBTASK_VIEW, JSON.stringify(payload))
      },
      // 清空缓存数据
      clearAlreadyReportTasks () {
        $storage.set(KEY_CRIUS_ALREADY_REPORT_SUBTASK_VIEW, '')
      },
      // 上报统计任务曝光
      reportSubtaskToCrius () {
        if (WILL_REPORT_TASK_LIST.length === 0) {
          console.log('无数据上报...')
          return
        }
        try {
          console.log('WILL_REPORT_TASK_LIST', WILL_REPORT_TASK_LIST.length)
          // 已上报的任务列表
          let alreadyList = this.getAlreadyReportTasks()
          // 可上报的任务列表
          let canList = []
          // 检查当前要上报的任务是不是已经上报过了
          WILL_REPORT_TASK_LIST.forEach(function (v, i) {
            if (alreadyList.indexOf(v) === -1) {
              canList.push(v)
            }
          })

          // 重置待上报数据
          WILL_REPORT_TASK_LIST = []

          if (canList.length === 0) {
            // 没有可上报的数据
            console.log('没有可上报的数据')
            return
          }

          /* 统计任务曝光 */
          crius.sendAction('PAGE', 'subtask_view', canList)
          /* 统计任务曝光 - END */

          console.log('统计任务曝光', canList)

          // 把上任务上报放到已上报的storage中
          canList.forEach(function (v, i) {
            alreadyList.push(v)
          })
          this.setAlreadyReportTasks(alreadyList)

          console.log('上报完成...')
        } catch (err) {
          console.log(err)
        }
      },
      // 任务在可见视图时触发
      taskEnterView ($v) {
        let taskId = $v.getAttribute('data-id')
        taskId = parseInt(taskId)

        if (WILL_REPORT_TASK_LIST.indexOf(taskId) !== -1) {
          console.log('had', taskId)
          return
        }
        WILL_REPORT_TASK_LIST.push(taskId)
        console.log(taskId)
      },

      shoutu () {
        $router.forward({
          path: '/apprentice',
          query: {
            from_shoutu_portal: 1
          }
        })
      },

      start (t) {
        this.isAssistantAlive().then(() => {
          // 优先处理放置于投放中板块内的portal
          if (t.isPortal) {
            t.onPortalClick()
            return
          }

          if (t.status === 2) { // 点击了进行中的任务，尝试直接进入任务详情页面
            $router.forward({
              path: `/task/detail/${t.task_id}`
            })

            return
          }

          let ongoingTask = _.find(this.ongoing, t => {
            return t.type === 1
          })

          if (ongoingTask && ongoingTask.id !== t.id) {
            $popup.fromTemplate(
              popup_has_task_ongoing.template('标准'),
              popup_has_task_ongoing.options
            )
              .show()
              .then((buttonIndex) => {
                if (buttonIndex === 1) {
                  this.$store.dispatch('task/start', t)
                }
              })
          } else {
            /* 统计抢任务点击 */
            crius.sendAction('EVENT', 'click_subtask_start', {
              task_id: t.id
            })
            /* 统计抢任务点击 - END */

            this.$store.dispatch('task/start', t)
          }
        }, () => {
          if (this.isNewbie) {
            this.showDownloadAssistantPopup()
          }
        })
      },

      ...mapActions('assistant', {
        'isAssistantAlive': 'alive'
      })
    }
  }
</script>



