<!-- 推荐模块 -->
<template>
  <div>
    <!-- 推荐任务 -->
    <ActionList v-if="recommends.length > 0" title="推荐">
      <Action v-for="(item, index) in recommends" :key="index" @click.native="jump(item)">
        <!-- 任务图标 -->
        <div class="icon" slot="icon">
          <img :src="item.icon">
        </div>

        <!-- 任务标题 -->
        <span slot="app-name" v-text="item.title"></span>
        <!-- 任务标签 -->
        <Tags slot="desc" :tags="item.tags"></Tags>
        <!-- 任务奖励金额 -->
        <div slot="reward-wrap" class="reward-content">
          <span class="plus"
            v-text="item.portalName === 'fanli' ? '返' 
            : (item.portalName === 'loan' ? '借' : '+')"></span>
          <span class="reward" v-text="item.reward"></span>

          <span 
            class="yuan" 
            v-text="item.portalName === 'fanli' ? '%' : (item.portalName === 'loan' ? '元' : (item.portalName === 'youlian' ? '元／月' : '元／日'))"></span>
        </div>
      </Action>
    </ActionList>
  </div>
</template>
<script>
  import {mapState} from 'vuex'
  import {Action, ActionList} from '../components/actions'
  import Tags from '../components/actions/Tags.vue'

  export default {
    components: {
      Action,
      ActionList,
      Tags
    },

    computed: {
      ...mapState('task', [
        'recommends'
      ])
    },

    methods: {
      jump (item) {
        item.onPortalClick()
      }
    }
  }
</script>



