import {popup_has_bind_mobile} from '../components/user_center/popup.js'

const initState = () => ({
  user_info: {
    avatar_url: '//assets.qkcdn.com/images/66e1d570722a0f0679a591d565a5077b.png',
    nickname: '',
    id: 0,
    balance: 0.00,
    jfb_account: {},
    // show_duobao: 0,
    my_total_income: 0,
    webclip: '',
    wx_bind: 0,
    mobile_bind: 0,
    withdraw_count: 0
  },

  user_profiles: {
    avatar_url: '//qianka.b0.upaiyun.com/images/531d323ca307aaa72f174921c195d6ff.png',
    id: 0,
    nickname: '',
    mobile_bind: 0,
    mobile_number: '',
    weixin_bind: 0,
    wx_nickname: '',
    wx_bind: 0,
    qrcode_url: ''
  }
})

const state = initState()

const getters = {
  mobileNumber (state) {
    let mobile = state.user_profiles.mobile_number.split('')
    mobile.splice(3, 4, ' **** ')
    mobile = mobile.join('')
    return mobile
  }
}

const types = {
  INIT: 'init',
  SYNC_USER: 'sync_user',
  SYNC_PROFILES: 'sync_profiles',
  CHANGE_AVATAR: 'change_avatar'
}

const mutations = {
  [types.INIT] (state, payload) {
    _.extend(state, initState())
  },

  [types.SYNC_USER] (state, payload) {
    payload.balance = Number(payload.balance).toFixed(2)
    _.extend(state.user_info, _.pick(payload, _.keys(state.user_info)))
    if (payload.jfb_account.is_open) {
      state.user_info.my_total_income = (Number(state.user_info.balance) + Number(state.user_info.jfb_account.surplus_to_price)).toFixed(2)
    } else {
      state.user_info.my_total_income = Number(state.user_info.balance).toFixed(2)
    }
  },

  [types.SYNC_PROFILES] (state, payload) {
    _.extend(state.user_profiles, _.pick(payload, _.keys(state.user_profiles)))
  },

  [types.CHANGE_AVATAR] (state, payload) {
    state.user_profiles.avatar_url = payload
  }
}

const actions = {
  fetch ({commit}) {
    commit(types.INIT)
    $api.user()
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC_USER, payload)
      })
  },

  getProfile ({commit}) {
    $api.userProfile()
      .then(res => res.data && res.data.payload)
      .then((payload) => {
        commit(types.SYNC_PROFILES, payload)
      })
  },

  updateAvatar ({commit}, fileInput) {
    $upload(fileInput).then((data, status) => {
      let avatarData = data.payload.files['0']
      let avatar = avatarData.hash + '.' + avatarData.ext
      let avatarUrl = data.payload.uri_prefix + '/' + avatar

      $api.setUserImage({ avatar }).then((response) => {
        commit(types.CHANGE_AVATAR, avatarUrl)
        $toast.show(response.data.payload.message)
      })
    }, (error, status) => {
      $toast.show(error)
    })
  },

  updateNickname ({commit}, nickname) {
    $api.setUserNickname({
      nickname: nickname
    }).then(response => {
      $toast.show(response.data.payload.message)
      $router.back('/user/profile')
    })
  },

  bindMobileSendCode ({commit}, form) {
    return $api.bindMobileSendCode(form)
  },

  bindMobile ({commit}, form) {
    return $api.bindMobile(form)
      .then(res => res.data)
      .then((data) => {
        if (data.err_code === 40003) {
          let popup = $popup.fromTemplate(
            popup_has_bind_mobile.template,
            popup_has_bind_mobile.options
          )
          popup.show().then((buttonIndex) => {
            if (buttonIndex === 1) {
              let path = {
                path: '/login',
                query: {backroute: '/user/bindMobile/0'}
              }
              $router.forward(path)
            }
          })
        } else {
          $toast.show(data.payload.message)
          let path = _.get($router.currentRoute.query, 'backroute', '/user/profile')
          $router.back({ path })
        }
      })
  },

  bindWechat ({commit}, verify) {
    $api.bindWechat(verify)
      .then((response) => {
        $toast.show(response.data.payload.message, 1000)
        setTimeout(() => {
          let path = _.get($router.currentRoute.query, 'backroute', '/user/profile')
          $router.back({ path })
          // $router.forward('/user/profile')
        }, 1000)
      })
  },

  unbindWechat () {
    let backToProfile = () => {
      setTimeout(() => {
        $router.back({
          path: '/user/profile'
        })
      }, 1000)
    }
    $api.unbindWechat().then((response) => {
      $toast.show(response.data.payload.message, 1000)
      backToProfile()
    }).catch(() => {
      backToProfile()
    })
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}



