// 装机必备首页
const Index = r => require.ensure([], () => r(require('./home/Index.vue')), 'hotapps')

// 分类细化：
const TypeDetails = r => require.ensure([], () => r(require('./typeDetails/Index.vue')), 'hotapps-typedetails')

// app详情：
const AppDetail = r => require.ensure([], () => r(require('./appDetail/Index.vue')), 'hotapps-appdetail')

export default [
  { path: '/hotapps/:type', name: 'hotapps', component: Index },
  { path: '/hotapps/typeDetails/:parent_id', name: 'type_details', component: TypeDetails },
  { path: '/hotapps/appDetail/:app_id', name: 'app_detail', component: AppDetail }
]



