<template>
  <div class="page has-navbar" v-nav="{title: '芝麻信用认证', showBackButton: true, onBackButtonClick: back }">
    <div class="page-content">
      <div class="remind-icon text-center"><img src="//qianka.b0.upaiyun.com/images/2fec065173b77b7a57b35da2fcf0efc4.png" alt=""><span>认证成功</span></div>
      <div class="zhima-button text-center"><span class="text-center">返回试玩任务（{{second}}s）</span></div>
    </div>
  </div>
</template>

<style lang='scss' scoped>
@import "../../scss/mixins";

  .page-content {
    .remind-icon {
      padding-top: 72px;

      img {
        width: 32px;
        height: 36px;
      }

      span {
        margin-top: 15px;
        display: block;
        @include font-regular;
        font-size: 16px;
        color: #484746;
      }
    }
    .zhima-button {
      margin-top: 40px;
      margin-left: 16px;
      width: 343px;
      height: 44px;
      background: #04C9B3;
      border-radius: 2px;

      span {
        @include font-regular;
        height: 44px;
        line-height: 44px;
        font-size: 16px;
        color: #FFFFFF;
      }
    }
  }
</style>

<script>
  export default {
    data () {
      return {
        second: 3
      }
    },

    mounted () {
      let t = setInterval(() => {
        if (this.second > 0) {
          this.second--
        } else {
          clearInterval(t)
          $router.back({ path: '/tasks' })
        }
      }, 1000)
    },

    methods: {
      back () {
        $router.back({ path: '/tasks' })
      }
    }
  }
</script>



