<style lang="scss">
  @import "./popup";
  @import "../../scss/variables";
  @import "../../scss/mixins";

  .popup-container .von-popup {
    background-color: #fff;

    .popup-head {
      border-bottom: none;
    }

    .popup-body {
      padding: 26px 18px 22px;
      position: relative;

      p {
        font-weight: 100 !important;
        -webkit-font-smoothing: subpixel-antialiased;
      }

      &.no-content * {
        opacity: 0;
      }

      .button-close {
        position: absolute;
        top: 5px;
        right: 5px;
        font-size: 30px;
      }
    }

    .popup-buttons {
      padding: 0;
      min-height: 45px;

      .button {
        min-height: 45px;
        font-size: 14px;
        line-height: 20px;
        border-top: none;
        border-right: none;
        border-radius: 0;
        padding-top: 13px;
        padding-bottom: 12px;

        &:first-of-type {
          border-bottom-left-radius: 4px;
        }

        &:last-of-type {
          border-right: none;
          border-bottom-right-radius: 4px;
        }

        margin-right: 0;
        background-color: transparent;

        &.button-light {
          background-color: #fff;

          &:active {
            background-color: #fbfbfb;
          }
        }

        &.button-assertive {
          border-top: none;
          background-color: $assertive;

          &:active {
            background-color: #D25041;
          }
        }
      }
    }
  }
</style>
<template>
  <div class="popup-container" :effect="effect"
       :class="{'popup-showing active': state == 1, 'popup-showing popup-hidden': state == 2}">
    <div class="popup von-popup">
      <div v-if="title" class="popup-head">
        <div class="popup-title" v-html="title"></div>
      </div>

      <div class="popup-body" :class="{'no-content': state == 0}">
        <slot></slot>
        <button v-if="showClose == 'true'" class="button button-royal button-small button-clear button-close" @click="hide(-1)">
          <i class="ion-ios-close-empty"></i>
        </button>
        <div v-if="buttons.length" class="hairline-bottom"></div>
      </div>

      <div v-if="buttons.length > 0" class="popup-buttons">
        <button v-for="(b, index) in buttons"
          class="button button-block"
          :class="{
            'button-assertive': b.theme == 'assertive',
            'button-positive': b.theme == 'positive',
            'button-blanced': b.theme == 'blanced',
            'button-energized': b.theme == 'energized',
            'button-calm': b.theme == 'calm',
            'button-royal': b.theme == 'royal',
            'button-dark': b.theme == 'dark',
            'button-light': b.theme == 'light',
            'button-light': !b.theme,
          }"
          @click="hide(index)"
        >
        <span v-text="b.text"></span>
        <div class="hairline-right" v-if="buttons.length>1"></div>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
  /* eslint eqeqeq: "off", camelcase: "off" */

  const popup_enter_duration = 200 // eslint-disable-line no-unused-vars
  const popup_leave_duration = 300

  export default {
    props: {
      effect: {
        type: String,
        default: 'default'
      },
      title: {
        type: String,
        default: ''
      },
      cssClass: {
        type: String,
        default: ''
      },
      showClose: {
        type: String,
        default: 'false'
      },
      closeCallback: String,
      keep: String
    },

    data () {
      return {
        state: 0, // 0: hidden, 1: showing, 2: active
        buttons: []
      }
    },

    mounted () {
      if (this.cssClass) {
        let classes = this.cssClass.split(' ')
        for (var i = 0; i < classes.length; i++) {
          this.$el.querySelector('[von-popups] .von-popup').classList.add(classes[i])
        }
      }
    },

    methods: {
      show () {
        this.state = 1
        document.body.classList.add('popup-open')
        $backdrop.show()

        return new Promise((resolve, reject) => {
          this.$on('PopupButtonClickEvent', (data) => {
            resolve(data.buttonIndex)
          })
        })
      },

      hide (buttonIndex) {
        // 处理 keep = 'true' 的情况
        if (this.keep == 'true' && buttonIndex > -1) {
          let buttonClickCallback = this.buttons[buttonIndex] && this.buttons[buttonIndex].onClick
          if (buttonClickCallback) {
            buttonClickCallback(buttonIndex)
          }
          return
        }

        // 处理 keep = 'true' 且 showClose = 'true' 下的 onClose 事件
        if (this.keep === 'true' && this.showClose && buttonIndex == -1) {
          if (this.closeCallback) setTimeout(window[this.closeCallback] && window[this.closeCallback](), 300)
        }

        this.state = 2
        $backdrop.hide()
        setTimeout(() => {
          document.body.classList.remove('popup-open')
        }, 300)

        setTimeout(() => {
          this.state = 0
          this.$emit('PopupButtonClickEvent', {buttonIndex: buttonIndex})
          // fix bug
          $popup.destroy()
        }, popup_leave_duration)
      },

      setButtons (buttons) {
        this.buttons = buttons
      },

      updateButtonTextByIndex (index, text) {
        this.buttons[index].text = text
      },

      updateButtonStatusByIndex (index, enabled) {
        let b = this.$el.querySelectorAll('.popup-buttons button')
        if (b && b[index]) {
          if (enabled) {
            b[index].removeAttribute('disabled')
          } else {
            b[index].setAttribute('disabled', '')
          }
        }
      }
    }
  }
</script>



